import duplicateOrdersServices from '../services/DuplicateOrdersServices';
import ParentPaginateStore from '../../../../../core/stores/ParentPaginateStore';
import { action, observable } from 'mobx';

export class DuplicateOrdersStore extends ParentPaginateStore {
  @observable public openDetails: boolean;
  @observable public filterBySummary: boolean;
  @observable public urgencyType: string;
  constructor() {
    super(duplicateOrdersServices);
    this.openDetails = false;
    this.filterBySummary = true;
    this.urgencyType = '';
  }
  public setPagingOption(data: any) {
    this.setElements(data.content);
    this.setCount(data.page.totalElements);
  }
  @action
  public setOpenDetails(value: boolean) {
    this.openDetails = value;
  }
  @action
  public setFilterBySummary(value: boolean) {
    this.filterBySummary = value;
    if (!value) {
      this.removeFilter({ operator: 'EQUALITY', field: 'orderType', value: '' });
    }
    this.getElements();
  }
  @action
  public setFilterType(ordertype: string) {
    if (!!ordertype) {
      ordertype === 'TOTAL'
        ? this.removeFilter({ operator: 'EQUALITY', field: 'orderType', value: ordertype })
        : this.addFilter({ operator: 'EQUALITY', field: 'orderType', value: ordertype });
      this.setFilterTypeValues(ordertype);
      if (this.filterBySummary && !this.isLoading) {
        this.getElements();
      }
    }
  }
  @action
  public setFilterTypeValues(ordertype: string) {
    this.urgencyType = ordertype;
  }
  @action
  public updateFilterType() {
    this.addFilter({ operator: 'EQUALITY', field: 'orderType', value: this.urgencyType });
  }
}

const duplicateOrdersStore = new DuplicateOrdersStore();
export default duplicateOrdersStore;
