import React, { Component } from 'react';
import { VCardPanel } from 'gsi-vx-ui';
import { observer } from 'mobx-react';
import DeliveryStatusGraph from './DeliveryStatusGraph/DeliveryStatusGraph';
import { GraphContainer } from './styles';
import { InjectedIntlProps, injectIntl } from 'react-intl';

interface IRecentTasksProps extends InjectedIntlProps {
  load: boolean;
}

@observer
class DeliveryStatusPanel extends Component<IRecentTasksProps> {
  constructor(props: IRecentTasksProps) {
    super(props);
  }

  render() {
    const { intl } = this.props;
    return (
      <VCardPanel
        headerText={`${intl.formatMessage({ id: 'home.dashboard.delivery_status.header_panel' })}`}
        headerIcon="drive-time"
      >
        <GraphContainer>
          <DeliveryStatusGraph />
        </GraphContainer>
      </VCardPanel>
    );
  }
}

export default injectIntl(DeliveryStatusPanel);
