import React, { Component, ReactNode } from 'react';
import { VCardPanel, VTable } from 'gsi-vx-ui';
import { inject, observer } from 'mobx-react';
import { DispatchStore } from '../../stores/dispatch/DispatchStore';
import { IVTableProps } from 'gsi-vx-ui/lib/types/Table/Table';
import { IVWidgetTableProps } from 'gsi-vx-ui/lib/types/Table/Widget/Widget';
import { Icon } from '@blueprintjs/core';
import { AppThemeContext, ITheme } from '../../../../../../App';

const ThemeConsumer = AppThemeContext.Consumer;
interface IRoutedOrdersProps {
  dispatch_store?: DispatchStore;
}

@inject('dispatch_store')
@observer
export class RoutedOrders extends Component<IRoutedOrdersProps> {
  constructor(props: IRoutedOrdersProps) {
    super(props);
  }

  onSelect = (data: any) => {
    console.log(data);
    // const activeDriver = { id: 1, fullName: '', truck: '' };
    // data.items.forEach((item: any) => {
    //   if (item.key === 'fullName') {
    //     activeDriver.fullName = item.label;
    //   }
    //   if (item.key === 'truck') {
    //     activeDriver.truck = item.label;
    //   }
    // });
    // if (activeDriver.fullName !== '') {
    // this.props.dispatch_store!.setActiveDriver(activeDriver);
    // }
  };

  onSort(data: any) {
    console.log(data);
  }

  render() {
    const amount = 25;
    const data: IVTableProps = this.transformRoutesList();
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <VCardPanel
              collapse
              defaultActive
              headerTextJustify={'center'}
              headerOrientation={'row-reverse'}
              bodyPadding={'0'}
              openIcon={'plus'}
              closeIcon={'minus'}
              headerText={`Routed Orders (${amount})`}
              headerBackgroundColor={themeContext.headers}
              headerColor={'#fff'}
            >
              <VTable {...data} />
            </VCardPanel>
          )
        }
      </ThemeConsumer>
    );
  }

  transformRoutesList(): IVTableProps {
    // todo do some tranform to get de list.
    /*
     * Get the result of the store and then here transform the data to the list
     */

    const checkboxWidget: IVWidgetTableProps = {
      column: 'type',
      widget: {
        type: 'CHECKBOX'
      }
    };
    const customWidget: IVWidgetTableProps = {
      column: 'geo',
      widget: {
        type: 'CUSTOMERCOMPONENT',
        cusmtomerCell: {
          renderCustomer: (value: string): ReactNode => {
            return (
              <div>
                <Icon icon={'phone'} iconSize={15} intent={'success'} /> {value}
              </div>
            );
          }
        }
      }
    };

    const widgetsCell: IVWidgetTableProps[] = [checkboxWidget, customWidget];

    return {
      widgetsCell,
      cellSelectionType: 'ENTIRE_ROW',
      onSelectionChange: this.onSelect,
      columns: ['s', 'type', 'geo', 'ticket', 'account', 'name', 'lastdate', 'orderdate', 'qty'],
      columns_name: {
        s: 'Rt-Sq',
        type: 'Type',
        geo: 'Geo',
        ticket: 'Ticket',
        account: 'Account',
        name: 'Name',
        lastdate: 'Last Dal Date',
        orderdate: 'Order Date',
        qty: 'QTY'
      },
      data: [
        {
          s: '1-1',
          id: '1',
          type: true,
          geo: '',
          ticket: '202',
          account: '1002',
          name: 'Custom Name',
          lastdate: '12/25/2018',
          orderdate: '12/25/2018',
          qty: '70'
        },
        {
          s: '1-2',
          id: '1',
          type: false,
          geo: '',
          ticket: '201',
          account: '1001',
          name: 'Custom Name',
          lastdate: '12/25/2018',
          orderdate: '12/25/2018',
          qty: '75'
        },
        {
          s: '1-3',
          id: '1',
          type: false,
          geo: '',
          ticket: '203',
          account: '1003',
          name: 'Custom Name',
          lastdate: '12/25/2018',
          orderdate: '12/25/2018',
          qty: '150'
        }
      ],
      reordering: true
    };
  }
}
