import React, { Component } from 'react';
import { Grid } from 'semantic-ui-react';

import TicketsContainer from './TicketsContainer';
import stationLogsStore, { StationLogsStore } from '../stores/StationLogsStore';
import StationLogsTickets from './StationLogsTickets';

class StationLogContainer extends Component {
  stationStore: StationLogsStore;

  constructor(props: any) {
    super(props);
    this.stationStore = stationLogsStore;
  }

  render() {
    return (
      <Grid style={{ padding: 5 }}>
        <Grid.Column width={9}>
          <TicketsContainer />
        </Grid.Column>
        <Grid.Column width={7}>
          <StationLogsTickets />
        </Grid.Column>
      </Grid>
    );
  }
}

export default StationLogContainer;
