import React, { Component } from 'react';
import { ButtonStyle } from './styles';
import { VCardPanel, VCheckboxField, VSelectField } from 'gsi-vx-ui';
import { Col, Grid, Row } from 'react-flexbox-grid';
import { FieldState, FormState } from 'formstate';
import { required } from '../../../../../commons/form/Validators';

export class DispatchProfileAction extends Component {
  form: FormState<any>;

  constructor(props: any) {
    super(props);
    this.form = new FormState<any>({
      profile: new FieldState('').validators(required),
      orders: new FieldState('').validators(required)
    });
  }

  render() {
    const optionsSelect = [{ label: 'Day', value: 1 }, { label: 'Night', value: 2 }, { label: 'All', value: 3 }];
    return (
      <VCardPanel noHeader bodyPadding={'5px 0'} width={'100%'}>
        <Grid fluid>
          <Row>
            <VSelectField
              options={optionsSelect}
              filterable={false}
              inline
              label={'Select Profile'}
              fieldState={this.form.$.profile}
              id="profile"
            />
          </Row>
          <Row>
            <VCheckboxField
              fieldState={this.form.$.orders}
              id="orders"
              label="Show All Orders"
              inline
              alignIndicator="left"
            />
          </Row>
          <Row>
            <Col xs={12} md={9}>
              <Row>
                <ButtonStyle
                  alignText={'left'}
                  fill
                  icon="floppy-disk"
                  text={'Save Profile'}
                  onClick={this.handleSaveProfile}
                />
              </Row>
            </Col>
          </Row>
          <Row>
            <Col xs={12} md={9}>
              <Row>
                <ButtonStyle
                  alignText={'left'}
                  fill
                  icon="print"
                  text={'Print Manifest'}
                  onClick={this.handlePrintManifest}
                />
              </Row>
            </Col>
          </Row>
        </Grid>
      </VCardPanel>
    );
  }

  private handleSaveProfile = () => {};

  private handlePrintManifest = () => {};
}
