import { ParentProfileStore } from '../../../../../core/stores/ParentProfileStore';
import profileService from '../../../../../core/services/ProfileService';
import { action, observable } from 'mobx';

export class OrderProfileStore extends ParentProfileStore {
  @observable public listProfiles: any[];
  constructor() {
    super(profileService);
    this.listProfiles = [];
  }
  getProfiles() {
    this.setLoading(true);
    this.serviceContainer
      .getAll()
      .then((response: any) => {
        this.setProfiles(response.data);
        this.getDefaultProfile();
        this.loadListProfiles();
        this.setLoading(false);
      })
      .catch((err: any) => {
        this.setLoading(false);
        throw err.response;
      });
  }

  loadListProfiles() {}

  @action
  setListProfiles(elements: any[]) {
    this.listProfiles = elements;
  }
}

const orderProfileStore = new OrderProfileStore();
export default orderProfileStore;
