import React, { Component } from 'react';
import { observer } from 'mobx-react';
import IGenericTable, { ISortBy } from '../../../../commons/components/table/IGenericTable';
import GenericTable from '../../../../commons/components/table/table';
import { action, observable } from 'mobx';

interface IListReviewStationLogsProps {
  tickets: any[];
}

@observer
class ListReviewStationLogs extends Component<IListReviewStationLogsProps> {
  @observable dataTableData?: IGenericTable;
  sortTable: ISortBy[];

  constructor(props: IListReviewStationLogsProps) {
    super(props);
    this.sortTable = [];
    this.createDataTable();
  }

  handelSelectItem = (data: any) => {};

  @action handelSort = (data: ISortBy[]) => {
    this.dataTableData!.sortBy = data;
    // todo apply logic to sort by the array passed.
  };

  createDataTable() {
    const { tickets } = this.props;
    let headers;
    let data;
    data = tickets.map(ticket => {
      return {
        key: ticket.id,

        items: [
          {
            key: ticket.stationDatetime.date,
            label: ticket.stationDatetime.date || '',
            icon: { icon: 'clock' as 'clock', size: 'large' as 'large', type: 'canceled' }
          },
          {
            key: ticket.ticketNumber,
            label: ticket.ticketNumber || 0
          },
          {
            key: ticket.stationName,
            label: ticket.stationName || ''
          },
          {
            key: 'assssss',
            label: 'To ASK' || ''
          },
          {
            key: ticket.driver.lastName,
            label: `${ticket.driver.lastName || ''}, ${ticket.driver.driverFullName || ''}`
          }
        ]
      };
    });
    headers = [
      {
        key: 'stationLogDate',
        label: 'STATION LOG DATE'
      },
      {
        key: 'ticket',
        label: 'TICKET #'
      },
      {
        key: 'pickUpDropOff',
        label: 'PICKUP / DROP OFF'
      },
      {
        key: 'pickUpDropOffNumber',
        label: 'PICKUP / DROP OFF #'
      },
      {
        key: 'driverName',
        label: 'DRIVER NAME'
      }
    ];

    this.dataTableData = {
      headers,
      data,
      sortBy: this.sortTable,
      onSort: this.handelSort,
      onSelect: this.handelSelectItem
    };
  }

  render() {
    return (
      <React.Fragment>
        <GenericTable genericTableData={this.dataTableData!} />
      </React.Fragment>
    );
  }
}

export default ListReviewStationLogs;
