import { action, observable } from 'mobx';

export class MessagingStore {
  @observable loading: boolean;
  @observable driverSelected: string;
  @observable drivers: any[];
  @observable messages: any[];

  constructor() {
    this.loading = false;
    this.driverSelected = 'Driver';
    this.drivers = [];
    this.messages = [];
    this.loadDriver();
    this.loadMessages();
  }

  @action
  setMessages(messages: any[]) {
    this.messages = messages;
  }

  @action
  setLoading(value: boolean) {
    this.loading = value;
  }

  @action
  getMessages() {
    return this.messages;
  }

  @action
  setDrivers(drivers: any[]) {
    this.drivers = drivers;
  }

  @action
  getDrivers() {
    return this.drivers;
  }

  @action
  loadDriver() {
    const mockDrivers = [
      {
        driver: 'BOB A',
        id: 1,
        truck: 'TRUCK 1'
      },
      {
        driver: 'JEFF B',
        id: 2,
        truck: 'TRUCK 2'
      },
      {
        driver: 'JOE C',
        id: 3,
        truck: 'TRUCK 2'
      },
      {
        driver: 'RICK D',
        id: 4,
        truck: 'TRUCK 4'
      },
      {
        driver: 'TOM E',
        id: 5,
        truck: 'TRUCK 5'
      }
    ];

    this.setDrivers(mockDrivers);
  }

  @action
  loadMessages() {
    const mockMessages = [
      {
        time: '7:05 AM',
        ack: false,
        id: 1,
        toname: 'Dispatch',
        from: 'theom',
        msg: 'Driver THEO M received 15 new  ticket(s).'
      },
      {
        time: '7:6 AM',
        ack: false,
        id: 2,
        toname: 'Dispatch',
        from: 'steveb',
        msg: 'Driver Steve B received 9 new  ticket(s).'
      },
      {
        time: '7:13 AM',
        ack: false,
        id: 3,
        toname: 'Dispatch',
        from: 'austin',
        msg: 'Driver Austin S received 21 new  ticket(s).'
      },
      {
        time: '7:16 AM',
        ack: false,
        id: 4,
        toname: 'Dispatch',
        from: 'davew',
        msg: 'Driver Davew A received 10 new  ticket(s).'
      },
      {
        time: '7:56 AM',
        ack: false,
        id: 5,
        toname: 'Dispatch',
        from: 'mikes',
        msg: 'Driver Davew A received 10 new  ticket(s).'
      },
      {
        time: '7:57 AM',
        ack: false,
        id: 6,
        toname: 'Dispatch',
        from: 'THEO M',
        msg: 'Mobile Pricing download initiated: 6196508 bytes, driver THEO M, shift 4824.'
      }
    ];

    this.setMessages(mockMessages);
  }
  @action
  selectDriver(driver: string) {
    this.driverSelected = driver;
  }
}

const messagingStore = new MessagingStore();
export default messagingStore;
