import * as React from 'react';
import { IRiftRoute, RiftRouter } from 'rift-router';

import authService from '../services/AuthServices';
import { LoginContainer } from '../../modules/security/user/containers/login/login';
import Main from '../components/templates/Main';
import { ReviewComponent } from '../../modules/review';
import { HomeContainer } from '../../modules/home';
import { DeliveryContainer } from '../../modules/delivery';
import { SetupContainer } from '../../modules/setup';

function requireAuth(): any {
  if (!authService.isLoggedIn()) {
    return '/login';
  }
}

export const routes: IRiftRoute[] = [
  {
    component: () => <LoginContainer />,
    path: '/login'
  },
  {
    component: () => '',
    onEnter: () => '/home',
    path: '*'
  },
  {
    component: () => <Main />,
    onEnter: () => requireAuth(),
    path: '',
    children: [
      {
        component: () => <ReviewComponent />,
        path: '/review'
      },
      {
        component: () => <HomeContainer />,
        path: '/home'
      },
      {
        component: () => <DeliveryContainer />,
        path: '/delivery'
      },
      {
        component: () => <SetupContainer />,
        path: '/setup'
      },
      {
        component: () => <SetupContainer />,
        path: '/account'
      },
      {
        component: () => <SetupContainer />,
        path: '/dashboard'
      }
    ]
  },
  {
    component: () => <HomeContainer />,
    path: '/dashboard'
  }
];

const routeStore = new RiftRouter(routes);

export default routeStore;
