import React, { Component } from 'react';
import HomeLayout from '../components/HomeLayout';
import { inject, observer, Provider } from 'mobx-react';
import homeStore, { HomeStore } from '../HomeStore';
import { AppStore } from '../../../core/stores/AppStore';

interface IProp {
  app?: AppStore;
}

@inject('app')
@observer
class HomeContainer extends Component<IProp> {
  home_store: HomeStore;
  constructor(props: any) {
    super(props);
    this.home_store = homeStore;
    this.props.app!.getOperations();
    this.props.app!.getAlerts();
  }
  render() {
    return (
      <Provider {...{ home_store: this.home_store }}>
        <HomeLayout />
      </Provider>
    );
  }
}

export default HomeContainer;
