import { action, computed, observable } from 'mobx';
import { find, findIndex, remove } from 'lodash';
import { IColumnsRep, IConditionalsType, IFilterType, IProfileType } from '../types/ProfileTypes';

export class ParentProfileStore {
  @observable profile: IProfileType | undefined;
  @observable profiles: IProfileType[];
  @observable isLoading: boolean;

  serviceContainer: any;
  constructor(profileService: any) {
    this.serviceContainer = profileService;
    this.isLoading = false;
    this.profiles = [];
  }

  @action
  setLoading(value: boolean) {
    this.isLoading = value;
  }

  @action
  setProfile(element: any) {
    this.profile = element;
  }

  @action
  resetProfile() {
    this.profile = undefined;
  }

  @action
  setProfiles(elements: any[]) {
    this.profiles = elements;
  }

  @action updateColumns(name: string, value: boolean) {
    if (this.profile && this.profile.columns) {
      const colIndex = findIndex(this.profile.columns, { name });
      if (colIndex >= 0) {
        this.profile.columns[colIndex].visible = value;
      }
    }
  }

  getProfiles() {
    this.setLoading(true);
    this.serviceContainer
      .getAll()
      .then((response: any) => {
        this.setProfiles(response.data);
        this.getDefaultProfile();
        this.setLoading(false);
      })
      .catch((err: any) => {
        this.setLoading(false);
        throw err.response;
      });
  }
  getActiveProfile(id: number | string) {
    this.setLoading(true);
    this.serviceContainer
      .getById(id)
      .then((response: any) => {
        this.setProfile(response.data);
        this.setLoading(false);
      })
      .catch((err: any) => {
        this.setLoading(false);
        throw err.response;
      });
  }

  getDefaultProfile() {
    const item = find(this.profiles, { is_default: true });
    if (item) {
      this.getActiveProfile(item.id);
    }
  }

  @action
  addFilter(value: IFilterType) {
    if (this.profile && this.profile.filters) {
      const index = findIndex(this.profile.filters, (filter: IFilterType) => {
        return filter.field === value.field;
      });
      if (index !== -1) {
        this.profile.filters[index] = value;
      } else {
        this.profile.filters.push(value);
      }
    }
  }

  @action
  removeFilter(value: IFilterType) {
    if (this.profile && this.profile.filters) {
      remove(this.profile.filters, (filter: IFilterType) => {
        return filter.field === value.field;
      });
    }
  }

  @action
  addConditional(value: IConditionalsType) {
    if (this.profile && this.profile.conditionals) {
      const index = findIndex(this.profile.conditionals, (filter: IFilterType) => {
        return filter.field === value.field;
      });
      if (index !== -1) {
        this.profile.conditionals[index] = value;
      } else {
        this.profile.conditionals.push(value);
      }
    }
  }

  @action
  removeConditional(value: IConditionalsType) {
    if (this.profile && this.profile.conditionals) {
      remove(this.profile.conditionals, (filter: IFilterType) => {
        return filter.field === value.field;
      });
    }
  }

  @computed
  get mapped_columns() {
    if (this.profile && this.profile.columns) {
      return this.profile.columns
        .filter((v: IColumnsRep) => {
          return v.visible;
        })
        .map((value: IColumnsRep) => {
          return value.name;
        });
    }
    return [];
  }

  @computed
  get columns_name() {
    if (this.profile && this.profile.columns) {
      const columns_name: any = {};
      this.profile.columns
        .filter((v: IColumnsRep) => {
          return v.visible;
        })
        .forEach((value: IColumnsRep) => {
          columns_name[value.name] = value.label;
        });
      return columns_name;
    }
    return null;
  }
}
