import { observer } from 'mobx-react';
import * as React from 'react';
/** Semantic */
import { Label, SemanticSIZES } from 'semantic-ui-react';
import { FromGroup, InputFieldFormField, InputLabel, InputMultiFieldForm, LabelError } from './styles';
import { FieldState } from 'formstate';

/** FieldState */

/**
 * Field Props
 */
export interface IFieldProps {
  /** Any UI stuff you need */
  label: string;
  labelPosition: any;
  size?: SemanticSIZES;
  icon?: any;
  iconPosition?: any;
  required?: boolean;
  disabled?: boolean;
  inline?: boolean;
  type?: any;
  loading?: boolean;
  placeholder?: string;
  fluid?: boolean;
  labels: string[];
  labelStyle?: { [key: string]: any };

  /** The fieldState */
  fieldState: FieldState<any>;
}

/**
 * Field component. Must be an observer.
 */

@observer
class MultiInputField extends React.Component<IFieldProps> {
  constructor(props: IFieldProps) {
    super(props);
  }

  public render() {
    const {
      label,
      size,
      fieldState,
      icon,
      required,
      disabled,
      inline,
      type,
      iconPosition,
      loading,
      placeholder,
      fluid,
      labels,
      labelStyle
    } = this.props;
    const inputList = Object.keys(fieldState.$).map((item: any, key: any) => {
      return (
        <React.Fragment key={item}>
          <InputMultiFieldForm
            {...{ size, type, icon, iconPosition, loading, placeholder, fluid }}
            value={fieldState.$[item].value || ''}
            onChange={(e: any) => fieldState.$[item].onChange(e.target.value)}
          />
          <Label>{labels[key] || ''}</Label>
        </React.Fragment>
      );
    });
    return (
      <InputFieldFormField {...{ required, disabled, inline }} error={fieldState.hasError}>
        <InputLabel style={labelStyle}>{label}</InputLabel>
        <FromGroup>{inputList}</FromGroup>

        {this.props.fieldState.hasError && <LabelError>{this.props.fieldState.error}</LabelError>}
      </InputFieldFormField>
    );
  }
}

export default MultiInputField;
