import React, { Component } from 'react';
import { observer } from 'mobx-react';

import { Button } from '@blueprintjs/core';
import { VCardPanel, VTable } from 'gsi-vx-ui';
import { Col, Row } from 'react-flexbox-grid';
import styled from 'styled-components';
import { ISortResult } from 'gsi-vx-ui/lib/types/Table/Table';

const FooterCol = styled(Col)`
  display: flex;
  justify-content: flex-end;
`;
interface ITrackingComponentProps {
  trackings: any[];
  drivers: any[];
  setDriver(driver: string): void;
  driver: string;
}

@observer
class TrackingComponent extends Component<ITrackingComponentProps> {
  constructor(props: ITrackingComponentProps) {
    super(props);
  }
  handelSelectItemDriver = (data: any) => {
    this.props.setDriver(data.items[0].label);
  };
  render() {
    const { drivers, trackings, driver } = this.props;
    return (
      <div>
        <Row>
          <Col xs={12} lg={3}>
            <VTable
              typeHeightRow={'HALF'}
              columns={['driver', 'truck']}
              data={drivers}
              sortable={{ columns: ['driver', 'truck'], onSort: (sort: ISortResult) => {} }}
            />
          </Col>
          <Col xs={12} lg={9}>
            <VCardPanel headerText={`${driver}`} bodyPadding={'0px'} headerHorizontalAlign={'center'}>
              <VTable
                typeHeightRow={'HALF'}
                columns={['time', 'status', 'duration', 'speed', 'location']}
                data={trackings}
              />
            </VCardPanel>
          </Col>
        </Row>
        <Row>
          <FooterCol xs={12}>
            <Button icon={'export'} style={{ marginTop: 5, marginBottom: 5, width: 200 }}>
              Export to Excel
            </Button>
          </FooterCol>
        </Row>
      </div>
    );
  }
}

export default TrackingComponent;
