import React, { Component } from 'react';
import { Col, Grid, Row } from 'react-flexbox-grid';
import ExportDeliveriesExport from './ExportDeliveriesExport/ExportDeliveriesExport';
import NotExportedDrivers from './NotExportedDrivers/NotExportedDrivers';

class SummaryExport extends Component {
  render() {
    return (
      <Grid fluid>
        <Row start={'xs'} between={'xs'}>
          <Col xs={6}>
            <ExportDeliveriesExport />
          </Col>
          <Col xs={6}>
            <NotExportedDrivers />
          </Col>
        </Row>
      </Grid>
    );
  }
}

export default SummaryExport;
