import React, { Component } from 'react';
import { Col, Grid, Row } from 'react-flexbox-grid';

import DeliveryStatusPanel from './DeliveryStatusPanel';
import { HomeStore } from '../HomeStore';
import RecentAlert from './RecentAlert';
import { inject, observer } from 'mobx-react';
import HomeTaskCard from './task_card/HomeTaskCard';
import { HomeLayoutContainer } from './styles';
import { RiftRouter } from 'rift-router';
import GallonsPanel from './GallonsPanel';
import { AppStore } from '../../../core/stores/AppStore';

const ColumnStyle = {
  justifyContent: 'flex-start',
  alignItems: 'start',
  display: 'flex'
};

interface IProp {
  app?: AppStore;
  home_store?: HomeStore;
  router?: RiftRouter;
}

@inject('app', 'home_store', 'router')
@observer
class HomeLayout extends Component<IProp> {
  constructor(props: IProp) {
    super(props);
  }

  render() {
    const { home_store, router, app } = this.props;
    return (
      <HomeLayoutContainer>
        <Grid fluid>
          <Row>
            <Col xs={12} sm={12} md={6}>
              <DeliveryStatusPanel load={app!.isLoading} />
            </Col>
            <Col xs={12} sm={12} md={6}>
              <GallonsPanel load={app!.isLoading} />
            </Col>
          </Row>
          <Row>
            {app!.operations.length > 0 && (
              <Col xs={12}>
                <HomeTaskCard operations={app!.operations} load={app!.isLoading} />
              </Col>
            )}
          </Row>
          <Row>
            {app!.recentAlerts.length > 0 && (
              <Col xs={4}>
                <br />
                <RecentAlert router={router} alerts={app!.recentAlerts} load={app!.isLoading} />
              </Col>
            )}
          </Row>
        </Grid>
      </HomeLayoutContainer>
    );
  }
}

export default HomeLayout;
