import { observer } from 'mobx-react';
import * as React from 'react';
/** Semantic */
import { SemanticSIZES } from 'semantic-ui-react';
import { InputFieldForm, InputFieldFormField, InputLabel, LabelError } from './styles';
import { FieldState } from 'formstate';

/** FieldState */

/**
 * Field Props
 */
export interface IFieldProps {
  /** Any UI stuff you need */
  label?: string;
  labelPosition?: any;
  size?: SemanticSIZES;
  icon?: any;
  iconPosition?: any;
  required?: boolean;
  disabled?: boolean;
  inline?: boolean;
  type?: any;
  loading?: boolean;
  placeholder?: string;
  fluid?: boolean;
  labelStyle?: { [key: string]: any };

  /** The fieldState */
  fieldState: FieldState<any>;
}

/**
 * Field component. Must be an observer.
 */

@observer
class InputField extends React.Component<IFieldProps> {
  constructor(props: IFieldProps) {
    super(props);
  }

  public render() {
    const {
      label,
      size,
      fieldState,
      icon,
      required,
      disabled,
      inline,
      type,
      iconPosition,
      loading,
      placeholder,
      fluid,
      labelStyle
    } = this.props;
    return (
      <InputFieldFormField {...{ required, disabled, inline }} error={fieldState.hasError}>
        <InputLabel style={labelStyle}>{label}</InputLabel>
        <InputFieldForm
          {...{ size, type, icon, iconPosition, loading, placeholder, fluid }}
          value={fieldState.value || ''}
          onChange={(e: any) => fieldState.onChange(e.target.value)}
        />
        {this.props.fieldState.hasError && <LabelError>{this.props.fieldState.error}</LabelError>}
      </InputFieldFormField>
    );
  }
}

export default InputField;
