import { action, observable } from 'mobx';
import { IconName } from '@blueprintjs/core';
import { ITabsTypes } from 'gsi-vx-ui/lib/types/TabsMenu/types';

export class DeliveryStore {
  @observable
  activeModule: string;

  @observable
  modules: ITabsTypes[];

  @observable
  isLoading: boolean;

  constructor() {
    this.isLoading = false;
    this.modules = [
      { navbarTabId: 'deliverySummary', icon: { icon: 'folder-close' }, label: 'Delivery Sumary', key: 'module_1' },
      { navbarTabId: 'ordenManagement', icon: { icon: 'folder-close' }, label: 'Order Management', key: 'module_2' },
      { navbarTabId: 'dispatch', icon: { icon: 'folder-close' }, label: 'Dispatch', key: 'module_3' },
      { navbarTabId: 'tracking', icon: { icon: 'folder-close' }, label: 'Tracking', key: 'module_4' },
      { navbarTabId: 'messagin', icon: { icon: 'folder-close' }, label: 'Messaging', key: 'module_5' }
    ];
    this.activeModule = this.modules[3].navbarTabId.toString();
  }

  @action
  setActiveModule(module: string) {
    this.activeModule = module;
  }

  @action
  setLoading(value: boolean) {
    this.isLoading = value;
  }
  @action
  setColor(module: number, color: IconName) {
    this.modules[module]['color'] = color;
  }
}
const deliveryStore = new DeliveryStore();
export default deliveryStore;
