import deliveryExceptionServices from '../services/DeliveryExceptionServices';
import ParentPaginateStore from '../../../../../core/stores/ParentPaginateStore';
import { action, observable } from 'mobx';
import { debounce, forEach, includes } from 'lodash';

export class DeliveryExceptionStore extends ParentPaginateStore {
  @observable openDetails: boolean;
  @observable moqSearch: string;
  @observable total: number;
  @observable moqStartDate: string;
  @observable moqEndDate: string;
  @observable listStatus: any[];
  constructor() {
    super(deliveryExceptionServices);
    this.openDetails = false;
    this.total = 0;
    this.listStatus = [];
    this.moqStartDate = '';
    this.moqSearch = '';
    this.moqEndDate = '';
  }
  public setPagingOption(data: any) {
    this.setElements(data.content);
    this.setCount(data.page.totalElements);
  }
  @action
  public setOpenDetails(value: boolean) {
    this.openDetails = value;
  }
  @action
  public setListStatus(value: any[]) {
    this.listStatus = value;
  }
  @action
  public statusChange(value: string[]) {
    debounce(() => {
      const status: any[] = [];
      forEach(this.listStatus, (s: any) => {
        status.push({ ...s, active: includes(value, s.value) });
      });
      this.setListStatus(status);
      this.addFilter({ field: 'status', value: value.join(','), operator: 'IN' });
      this.getElements();
    }, 1200)();
  }
  @action
  public getStatus() {
    this.serviceContainer.getStatus().then((obj: any) => {
      const status: any[] = [];
      const statusId: any[] = [];
      for (const props in obj.data) {
        statusId.push(props);
        status.push({ value: props, label: obj.data[props], active: true });
      }
      this.setListStatus(status);
      this.addFilter({ field: 'status', value: statusId.join(','), operator: 'IN' });
      this.getElements();
    });
  }
  @action
  public setMoqSearch(value: string) {
    this.moqSearch = value;
  }
  public getElements(url: string = '', others?: any) {
    super.getElements('listDeliveryExceptions', others);
  }
  @action
  public searchChange(data: any) {
    debounce(() => {
      console.log('search');
      this.setSearch(data);
    }, 1600)();
  }
  @action
  public setDates(start: any, end: any) {
    debounce(() => {
    !!start?this.addFilter({field:'startDate', operator:'COMPARABLE_GREATER_EQUAL_THAN', value:start.format('YYYY-MM-DDTHH:mm:ss')}):this.removeFilter({field:'startDate', operator:'IN', value:''});
    !!end?this.addFilter({field:'endDate', operator:'COMPARABLE_GREATER_EQUAL_THAN', value:end.format('YYYY-MM-DDTHH:mm:ss')}):this.removeFilter({field:'endDate', operator:'IN', value:''});
    this.moqEndDate = end;
    this.moqStartDate = start;
    this.getElements();
    }, 1800)();
  }
}

const deliveryExceptionStore = new DeliveryExceptionStore();
export default deliveryExceptionStore;
