import React, { Component } from 'react';
import { RiftRouter } from 'rift-router';
import { VCardPanel } from 'gsi-vx-ui';

import IRecentAlert from '../../../core/types/RecentAlert';
import { ListAlert, ListAlertItem } from './styles';
import { AnchorButton, Button, Classes, Dialog, Intent, Tag, Tooltip } from '@blueprintjs/core';
import { InjectedIntlProps, injectIntl } from 'react-intl';

interface IRecentAlertProps extends InjectedIntlProps {
  alerts: IRecentAlert[];
  router?: RiftRouter;
  load: boolean;
}

interface IState {
  modalOpen: boolean;
}

class RecentAlert extends Component<IRecentAlertProps, IState> {
  constructor(props: IRecentAlertProps) {
    super(props);
    this.state = {
      modalOpen: false
    };
  }

  render() {
    const { alerts, load, intl } = this.props;
    return (
      <React.Fragment>
        <VCardPanel
          headerText={`${intl.formatMessage({ id: 'home.dashboard.alerts' })}`}
          headerIcon="warning-sign"
          bodyPadding="0px"
        >
          <ListAlert>
            {alerts.map((alert: IRecentAlert, key: number) => (
              <ListAlertItem
                key={key}
                onClick={() => {
                  this.NavigateAlert(alert);
                }}
                icon={
                  <Tag round title={`${intl.formatMessage({ id: alert.label })}`} intent="danger">
                    {alert.total}
                  </Tag>
                }
                text={`${intl.formatMessage({ id: alert.label })}`}
              />
            ))}
          </ListAlert>
        </VCardPanel>
        <Dialog
          icon="info-sign"
          onClose={this.handleClose}
          title={`${intl.formatMessage({ id: 'home.dashboard.alerts' })}`}
          isOpen={this.state.modalOpen}
        >
          <div className={Classes.DIALOG_BODY}>
            <p>Start the revolution. Unleash the power of data integration with Palantir Foundry.</p>
          </div>
          <div className={Classes.DIALOG_FOOTER}>
            <div className={Classes.DIALOG_FOOTER_ACTIONS}>
              <Tooltip content="This button is hooked up to close the dialog.">
                <Button onClick={this.handleClose}>Close</Button>
              </Tooltip>
              <AnchorButton intent={Intent.PRIMARY} href="https://www.palantir.com/palantir-foundry/" target="_blank">
                Visit the Foundry website
              </AnchorButton>
            </div>
          </div>
        </Dialog>
      </React.Fragment>
    );
  }

  private NavigateAlert(alert: IRecentAlert) {
    console.log(alert);

    this.props.router!.riftTo(`/delivery${this.getRouteParams(alert.route)}`);

    // this.props.router!.search = alert.route;
    // this.props.router!.riftTo('/delivery');
  }

  private getRouteParams = (object: any) => {
    let params = '';
    console.log(object);
    const keys = Object.keys(object);
    if (keys.length > 0) {
      keys.map((key, index) => {
        const modifier = index === 0 ? '?' : '&&';
        params += `${modifier}${key}=${object[key]}`;
      });
    }
    return params;
  };

  handleClose = () => {
    this.setState({ modalOpen: false });
  };

  goTo = (e: any, url: {}) => {
    // this.props.router!.search = {submodule:'module_3', child:'2', drop: 'pending_order'};
    this.props.router!.riftTo('/delivery?submodule=module_3');
    // this.setState({ modalOpen: true });
    // this.props.router ? this.props.router.riftTo(url) : (window.location.href = url);
  };
}

export default injectIntl(RecentAlert);
