import React, { Component } from 'react';
import { Icon, Label, Menu } from 'semantic-ui-react';
import IModulesTypes from '../../types/modules';
import { observer } from 'mobx-react';

interface IMenuProps {
  secondary?: boolean;
  pointing?: boolean;
  tabular?: boolean;
  compact?: boolean;
  vertical?: boolean;
}
interface ITabsMenuProps {
  activeTab: IModulesTypes;
  tabList: IModulesTypes[];
  menuProps?: IMenuProps;
  handleChange(tab: IModulesTypes): void;
}

@observer
class TabsMenu extends Component<ITabsMenuProps> {
  constructor(props: ITabsMenuProps) {
    super(props);
  }

  render() {
    const { activeTab, menuProps } = this.props;
    return (
      <Menu {...menuProps}>
        {this.props.tabList.map(tab => (
          <Menu.Item
            content={tab.content!}
            key={tab.name}
            name={tab.label}
            color={tab.color}
            active={activeTab.name === tab.name}
            onClick={e => this.props.handleChange(tab)}
          >
            {!tab.content && tab.icon && (
              <div style={{ position: 'relative' }}>
                <Icon name={tab.icon} />
                {!!tab.label_floting && (
                  <Label style={{ position: 'relative', left: '94%' }} circular size="tiny" color={tab.color} floating>
                    {tab.label_floting}
                  </Label>
                )}
                {tab.label}
              </div>
            )}
          </Menu.Item>
        ))}
      </Menu>
    );
  }
}

export default TabsMenu;
