import BasePaginateService from '../../../../../../core/services/BasePaginateService';
import { IFilterType } from '../../../../../../core/types/FilterType';
import { AxiosPromise } from 'axios';
import qs from 'qs';

export class DetailsService extends BasePaginateService {
  constructor() {
    super('listShiftDetails');
  }

  public getByIdDriverCheckList(url: string, itemId: string | number, filters: IFilterType): AxiosPromise<any> {
    const params = {
      filters
    };
    return this.httpAxios
      .get(`${url}/${itemId}/`, {
        params: { data: JSON.stringify(params) },
        paramsSerializer: params => {
          return qs.stringify(params, { arrayFormat: 'brackets' });
        }
      })
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }
}

const detailsService = new DetailsService();
export default detailsService;
