import React, { Component, SyntheticEvent } from 'react';
import { Drawer, H3, H5, Intent, MenuDivider, ProgressBar, Tag } from '@blueprintjs/core';
import { AlertContainer, ListAlert, ListAlertItem, ProcessItem } from './styles';
import IRecentAlert from '../../../types/RecentAlert';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { IOperationType } from '../../../types/OperationType';

interface IProps extends InjectedIntlProps {
  isOpen: boolean;
  onClose: (event?: SyntheticEvent<HTMLElement>) => void;
  process?: IOperationType[];
  alerts?: IRecentAlert[];
  onNavigate: (data: IRecentAlert) => void;
}

class DetailsDrawer extends Component<IProps> {
  render() {
    const { isOpen, onClose, process, alerts, intl, onNavigate } = this.props;
    return (
      <Drawer
        icon="import"
        title={`${intl.formatMessage({ id: 'home.dashboard.import_process' })}`}
        size="300px"
        isOpen={isOpen}
        canEscapeKeyClose
        canOutsideClickClose
        onClose={onClose}
      >
        <AlertContainer>
          <MenuDivider title={`${intl.formatMessage({ id: 'home.dashboard.alerts' })}`} />
          <ListAlert>
            {alerts!.map((value: IRecentAlert, key: number) => {
              return (
                <ListAlertItem
                  key={key}
                  onClick={() => {
                    onNavigate(value);
                  }}
                  icon={
                    <Tag round title={`${intl.formatMessage({ id: value.label })}`} intent="danger">
                      {value.total}
                    </Tag>
                  }
                  text={`${intl.formatMessage({ id: value.label })}`}
                />
              );
            })}
          </ListAlert>

          <MenuDivider title={`${intl.formatMessage({ id: 'home.dashboard.import_process' })}`} />
          <ListAlert>
            {process!.map((value: IOperationType, key: number) => {
              const percent = (value.updates * 100) / value.total;
              const intent = percent === 100 ? Intent.SUCCESS : Intent.WARNING;
              let type = '';
              switch (value.dashboardOperation) {
                case 'ACCOUNTS':
                  type = `${intl.formatMessage({ id: 'home.dashboard.accounts' })}`;
                  break;
                case 'ORDERS':
                  type = `${intl.formatMessage({ id: 'home.dashboard.orders' })}`;
                  break;
                case 'PACKAGE_PRICING':
                  type = `${intl.formatMessage({ id: 'home.dashboard.package_pricing' })}`;
                  break;
              }
              return (
                <ProcessItem key={key}>
                  <H3>{value.updates}</H3>
                  <ProgressBar intent={intent} value={percent} />
                  <H5>{type}</H5>
                </ProcessItem>
              );
            })}
          </ListAlert>
        </AlertContainer>
      </Drawer>
    );
  }
}

export default injectIntl(DetailsDrawer);
