import { action, computed, observable } from 'mobx';

import authService from '../services/AuthServices';
import dashboardServices from '../services/DashboarServices';
import IRecentAlert from '../types/RecentAlert';
import { IOperationType } from '../types/OperationType';
import { AxiosResponse } from 'axios';
import IModulesNavigateType from '../types/ModulesNavigateType';
import { ITheme, themes } from '../../App';

export class AppStore {
  @observable
  isLoading: boolean;

  @observable
  public isOpenProcess: boolean;

  @observable
  public isCollapseMenu: boolean;

  @observable
  recentAlerts: IRecentAlert[];

  @observable
  operations: IOperationType[];

  @observable
  commodityType: string;

  @observable
  commodityTypeOptions: any[];

  @observable
  modulesNavigate: IModulesNavigateType;

  @observable
  lang: string;

  @observable
  theme: ITheme;

  @observable
  brand: string;

  constructor() {
    this.isLoading = false;
    this.commodityType = 'propane';
    this.commodityTypeOptions = [
      {
        key: 'propane',
        value: 'propane',
        text: 'Propane',
        selected: true,
        image: { avatar: true, src: '/images/avatar/small/jenny.jpg' }
      },
      {
        key: 'crude',
        value: 'crude',
        text: 'Crude',
        selected: false,
        image: { avatar: true, src: '/images/avatar/small/jenny.jpg' }
      },
      {
        key: 'chemical',
        value: 'chemical',
        text: 'Chemical',
        selected: false,
        image: { avatar: true, src: '/images/avatar/small/jenny.jpg' }
      }
    ];
    this.isCollapseMenu = false;
    this.isOpenProcess = false;
    this.recentAlerts = [];
    this.operations = [];
    this.modulesNavigate = {};
    this.lang = 'en';
    this.theme = themes['light'];
    this.brand = '';
  }

  getOperations() {
    this.setLoading(true);
    dashboardServices
      .getAll('operation', {})
      .then((response: AxiosResponse<IOperationType[]>) => {
        this.setOperations(response.data);
      })
      .catch((error: any) => {
        console.log(error);
      })
      .finally(() => {
        this.setLoading(false);
      });
  }

  getAlerts() {
    this.setLoading(true);
    dashboardServices
      .getAlerts({})
      .then((response: AxiosResponse<IRecentAlert[]>) => {
        this.getAlertsMessages(response.data);
      })
      .catch((error: any) => {
        console.log(error);
      })
      .finally(() => {
        this.setLoading(false);
      });
  }

  @action
  getAlertsMessages(alerts: IRecentAlert[]) {
    this.setRecentAlerts(
      alerts.map((value: IRecentAlert) => {
        switch (value.alertType) {
          case 'INCOMPLETE_ORDERS':
            value.label = 'home.alerts.incomplete_orders';
            value.route = { submodule: 'module_2', child: '3' };
            break;
          case 'UNGEOCODE_ACCOUNTS':
            value.label = 'home.alerts.ungeocode_accounts';
            value.route = {};
            break;
          case 'DUPLICATE_ORDERS':
            value.label = 'home.alerts.duplicate_orders';
            value.route = { submodule: 'module_2', child: '2', drop: 'duplicate' };
            break;
          case 'NO_DELIVERY_FOR_REVIEW':
            value.label = 'home.alerts.no_delivery_for_review';
            value.route = { submodule: 'module_2', child: '2', drop: 'no_deliveries' };
            break;
          case 'UNDELIVERY_STOP_FOR_REVIEW':
            value.label = 'home.alerts.undelivery_stop_for_review';
            value.route = { submodule: 'module_2', child: '2', drop: 'pending_order' };
            break;
          default:
            value.label = 'home.alerts.incomplete_orders';
            value.route = { submodule: 'module_2', child: '3' };
        }
        return value;
      })
    );
  }

  @action
  setOperations(operations: IOperationType[]) {
    this.operations = operations;
  }

  @action
  setLoading(value: boolean) {
    this.isLoading = value;
  }

  @action
  public setIsOpenProcess(value: boolean) {
    this.isOpenProcess = value;
  }

  @action
  public setIsCollapseMenu(value: boolean) {
    this.isCollapseMenu = value;
  }

  @action
  public setStatusCollapseMenu() {
    this.isCollapseMenu = !this.isCollapseMenu;
  }

  @action
  public setModulesNavigate(moduleNavigate: IModulesNavigateType) {
    this.modulesNavigate = moduleNavigate;
  }

  @computed
  public get isLogged() {
    return !!authService.isLoggedIn();
  }

  @action
  public resetUser() {
    authService.clearToken();
  }
  @action
  setLang(value: string) {
    this.lang = value;
  }

  @action
  setRecentAlerts(alert: IRecentAlert[]) {
    this.recentAlerts = alert;
  }

  @action
  setTheme(value: ITheme) {
    this.theme = value;
  }

  @action
  setBrand(value: string) {
    this.brand = value;
  }

  public storeUserCredentials(credentials: any) {
    authService.storeUserCredentials(credentials);
  }

  public logout() {
    authService.clearToken();
  }
}

const appStore = new AppStore();

export default appStore;
