import { BaseService } from '../../../../../core/services/BaseServices';
import { AxiosPromise } from 'axios';
import qs from 'qs';

export class OrderManagementServices extends BaseService {
  constructor() {
    super('/');
  }

  public getZones(params: any): AxiosPromise<any[]> {
    return this.httpAxios
      .get('listZones', { params })
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }

  public getProducts(params: any): AxiosPromise<any[]> {
    return this.httpAxios
      .get('listAllProducts', { params })
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }
  public getStates(params: any): AxiosPromise<any[]> {
    return this.httpAxios
      .get('listStateCodes', { params })
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }

  public getTypes(params: any): AxiosPromise<any[]> {
    return this.httpAxios
      .get('listOrderTypes', { params })
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }
  public getFillTypes(params: any): AxiosPromise<any[]> {
    return this.httpAxios
      .get('listAllFillType', { params })
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }

  public getUrgency(params: any): AxiosPromise<any[]> {
    return this.httpAxios
      .get('getGroupedUrgencyList', { params })
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }

  public cancelOrder(id: number | string): AxiosPromise<any> {
    return this.httpAxios
      .post('cancelOrderById', id)
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }
  public saveProfile(data: any): AxiosPromise<any> {
    return this.httpAxios
      .post('saveOrderAdvancedFilter', data)
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }
  public updateProfile(item: any): AxiosPromise<any> {
    return this.httpAxios
      .put('updateOrderAdvancedFilter', item)
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }

  public exportExcel(
    url: string,
    params: any,
    profile: number | string,
    feature: string,
    size: number
  ): AxiosPromise<any> {
    return this.httpAxios
      .get(url, {
        params: { profile, size, feature, data: JSON.stringify(params) },
        paramsSerializer: params => {
          return qs.stringify(params, { arrayFormat: 'brackets' });
        },
        headers: {
          'Content-Type': 'application/vnd.ms-excel;charset=utf-8'
        },
        maxContentLength: 10000,
        responseType: 'arraybuffer',
        timeout: 10000000
      })
      .then(response => response)
      .catch(err => {
        throw err;
      }) as AxiosPromise<any>;
  }

  public getAdvanceProfile(): AxiosPromise<any[]> {
    return this.httpAxios
      .get('listAdvancedFilters')
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }

  public getAccounts(params: any): AxiosPromise<any[]> {
    return this.httpAxios
      .get('getAccountList', { params })
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }
  public saveOrder(data: any): AxiosPromise<any> {
    return this.httpAxios
      .post('saveOrder', data)
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }
  public updateOrder(item: any): AxiosPromise<any> {
    const url = item.slipOrder ? 'updateSlipOrder' : 'updateOrder';
    return this.httpAxios
      .put(url, item)
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }
  public getFees(params: any): AxiosPromise<any[]> {
    return this.httpAxios
      .get('listAllAdditionalFeeByProductId', {
        params
      })
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }
  public getFeesByOrder(params: any): AxiosPromise<any[]> {
    return this.httpAxios
      .get('listOrderAdditionalFeeByOrderId', {
        params
      })
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }
}
const orderManagementServices = new OrderManagementServices();
export default orderManagementServices;
