import React from 'react';
import ParentPaginateStore from '../../../../../../../core/stores/ParentPaginateStore';
import { action, computed, observable } from 'mobx';
import { ShiftModel } from '../../../models/shiftModel';
import { ShiftTruckInventoryModel } from '../../../models/shiftTruckInventory';
import shiftTruckInventoryService from '../../../services/ShiftTruckInventoryService/ShiftTruckInventoryService';

export class ShiftTruckInventoryStore extends ParentPaginateStore {
  @observable
  element: ShiftModel;
  @observable
  elements: ShiftTruckInventoryModel[];
  @observable
  public columns: string[];

  constructor() {
    super(shiftTruckInventoryService);
    this.element = new ShiftModel();
    this.elements = [];
    this.columns = ['truckName', 'tankName', 'productName', 'start', 'loaded', 'end', 'totalDelQty'];
  }

  @action
  setElement(element: ShiftModel) {
    this.element = element;
  }

  @action
  setElements(elements: ShiftTruckInventoryModel[]) {
    this.elements = elements;
  }

  @action
  setColumns(columns: string[]) {
    this.columns = columns;
  }

  @computed
  get sortOrderForTable(): { order: 'ASC' | 'DESC' | 'NONE'; columnName: string; columnIndex: number }[] {
    return this.orders.map(item => {
      return { order: item.direction, columnName: item.property, columnIndex: this.columns.indexOf(item.property) };
    });
  }

  public setPagingOption(data: any) {
    const elements: ShiftTruckInventoryModel[] = data.content;
    if (data.content.length) {
      this.setElements(elements);
      this.setCount(data.totalElements);
    } else {
      this.setElements([]);
      this.setCount(0);
    }
  }
}

const shiftTruckInventoryStore = new ShiftTruckInventoryStore();
export default shiftTruckInventoryStore;
