import styled from 'styled-components';
import { Form, Input } from 'semantic-ui-react';

export const InputFieldFormField = styled(Form.Field)`
  display: ${props => (props.inline ? 'flex' : 'block')};
  align-items: center;
  justify-content: flex-start;
  margin: ${props => props.error && '0 0 2em !important'};
  position: relative;
`;

export const FromGroup = styled(Form.Group)`
  display: ${props => (props.inline ? 'flex' : 'block')};
  align-items: center;
  justify-content: flex-start;
  margin: ${props => props.error && '0 0 2em !important'};
  position: relative;
  width: 100%;
  margin: inherit !important;
`;

export const InputLabel = styled.label`
  text-align: right;
  min-width: 150px;
`;

export const InputFieldForm = styled(Input)`
  width: 100% !important;
`;

export const InputMultiFieldForm = styled(Input)`
  flex: 1;
  input {
    max-width: 48px !important;
  }
`;

export const LabelError = styled.span`
  width: 100%;
  color: #db2828;
  position: absolute;
  bottom: -20px;
  right: 0;
  text-align: right;
`;
