import React, { Component } from 'react';
import stationLogsStore, { StationLogsStore } from '../stores/StationLogsStore';
import ListReviewStationLogs from '../components/ListReviewStationLogs';
import SearchReviewStationLogsContainer from './search/SearchReviewStationLogsContainer';

class TicketsContainer extends Component {
  stationStore: StationLogsStore;

  constructor(props: any) {
    super(props);
    this.stationStore = stationLogsStore;
  }

  render() {
    const { tickets, handlerOnSerarch } = this.stationStore;
    return (
      <React.Fragment>
        <SearchReviewStationLogsContainer />
        <ListReviewStationLogs tickets={tickets} />
      </React.Fragment>
    );
  }
} // <SearchReviewStationLogs handleOnSubmit={handlerOnSerarch} />

export default TicketsContainer;
