import axios, { AxiosInstance, AxiosRequestConfig } from 'axios';
import { showToastNotification } from 'gsi-vx-ui';

const config: AxiosRequestConfig = {
  baseURL: process.env.REACT_APP_API_URL,
  headers: {
    'Content-Type': 'application/json'
  },
  maxContentLength: 2000,
  maxRedirects: 5,
  responseType: 'json',
  timeout: 100000,
  validateStatus: (status: number) => status >= 200 && status < 300
};

function getToken() {
  const userdata = localStorage.getItem('userdata');
  if (userdata) {
    return JSON.parse(userdata).access_token;
  }
  return '';
}

function clearToken(): void {
  localStorage.removeItem('userdata');
}

export function createHttpAxios(url: string, cnf?: AxiosRequestConfig): AxiosInstance {
  const resultConfig = { ...config, ...cnf };
  resultConfig.baseURL += url;
  const axiosInstance = axios.create(resultConfig);
  axiosInstance.interceptors.request.use((conf: any) => {
    if (!conf.headers.Authorization) {
      conf.headers.Authorization = `Bearer ${getToken()}`;
    }

    return conf;
  });
  axiosInstance.interceptors.response.use(
    (response: any) => {
      return response;
    },
    (error: any) => {
      let err = '';
      if (error.response && error.response.status) {
        const status = error.response.status!;
        if (400 === status) {
          err = error.response.data.message;
        } else if (status > 405 && 500 <= status) {
          err = error.response.data.message;
        } else if (401 === status || 403 === status) {
          err = 'Session Expired';
          clearToken();
          if (window.location.pathname !== '/login') {
            window.location.href = '/login';
          }
        }
      } else {
        err = 'Network Error';
      }
      showToastNotification({
        message: err,
        type: 'danger',
        icon: 'error'
      });
      return Promise.reject(err);
    }
  );

  return axiosInstance;
}
