import React, { Component } from 'react';
import { DeliveryDispatchLayout } from '../../components/dispatch_layout/DeliveryDispatchLayout';
import { Provider } from 'mobx-react';
import dispatchStore, { DispatchStore } from '../../stores/dispatch/DispatchStore';

export class DeliveryDispatch extends Component<any> {
  dispatchStore: DispatchStore;

  constructor(props: any) {
    super(props);
    this.dispatchStore = dispatchStore;
  }

  render() {
    return (
      <Provider {...{ dispatch_store: this.dispatchStore }}>
        <DeliveryDispatchLayout />
      </Provider>
    );
  }
}
