import React from 'react';
import { action, observable } from 'mobx';
import openOrdersServices from '../services/OpenOrdersServices';
import ParentPaginateStore from '../../../../../core/stores/ParentPaginateStore';

export class OpenOrdersStore extends ParentPaginateStore {
  @observable public totalOrders: number;
  @observable public filterByUrgency: boolean;
  @observable public urgencyNumber: number;
  @observable public urgencyType: string;
  @observable public isDetailsOpen: boolean;
  @observable public isModalOpen: boolean;
  @observable public url: string;
  constructor() {
    super(openOrdersServices);
    this.totalOrders = 0;
    this.urgencyNumber = 0;
    this.filterByUrgency = true;
    this.isDetailsOpen = false;
    this.isModalOpen = false;
    this.urgencyType = '';
    this.url = 'getOrderList';
    this.addFilter({ field: 'orderStatus', value: 'Open', operator: 'EQUALITY' });
  }
  public setPagingOption(data: any) {
    this.setElements(data.content);
    this.setCount(data.page.totalElements);
    this.setTotalOrders(data.count);
  }
  @action
  public setTotalOrders(value: number) {
    this.totalOrders = value;
  }
  @action
  public setUrl(value: string) {
    this.url = value;
  }

  @action
  public setOpenModal(value: boolean) {
    this.isModalOpen = value;
  }
  @action
  public setOpenDetails(value: boolean) {
    this.isDetailsOpen = value;
  }
  @action
  public setFilterByUrgency(value: boolean) {
    this.filterByUrgency = value;
    if (!value) {
      this.removeFilter({ operator: 'EQUALITY', field: 'orderType', value: '' });
      this.removeFilter({ operator: 'EQUALITY', field: 'urgencyType', value: '' });
      this.getElements();
    } else {
      this.setFilterType();
    }
  }
  @action
  public setFilterType(ordertype: string = this.urgencyType, urgency: number = this.urgencyNumber) {
    if (!!urgency) {
      this.setFilterTypeValues(ordertype, urgency);
      ordertype === 'TOTAL' && this.removeFilter({ operator: 'EQUALITY', field: 'orderType', value: ordertype });
      if (this.filterByUrgency) {
        ordertype !== 'TOTAL' && this.addFilter({ operator: 'EQUALITY', field: 'orderType', value: ordertype });
        this.addFilter({ operator: 'EQUALITY', field: 'urgencyType', value: urgency });
      } else {
        this.removeFilter({ operator: 'EQUALITY', field: 'orderType', value: ordertype });
        this.removeFilter({ operator: 'EQUALITY', field: 'urgencyType', value: urgency });
      }
      if (this.filterByUrgency && !this.isLoading) {
        this.getElements();
      }
    }
  }
  @action
  public setFilterTypeValues(ordertype: string, urgency: number) {
    this.urgencyNumber = urgency;
    this.urgencyType = ordertype;
  }
  public getElements(url: string = this.url, others?: any) {
    super.getElements(url);
  }
  @action
  public updateFilterType() {
    this.addFilter({ operator: 'EQUALITY', field: 'orderType', value: this.urgencyType });
  }
}

const openOrdersStore = new OpenOrdersStore();
export default openOrdersStore;
