import React, { Component } from 'react';
import { Icon, IconName } from '@blueprintjs/core';

import {
  CardAction,
  CardAmountInfo,
  CardContainer,
  CardContent,
  CardHeaderText,
  CardIconHeader,
  CardMeta,
  TaskUpdate
} from './styles';

import { TaskPercentBar } from './TaskPercentBar';
import { InjectedIntlProps, injectIntl } from 'react-intl';

interface IProp extends InjectedIntlProps {
  background?: string;
  headerText: string;
  updateText: string;
  icon: IconName;
  pendingUpdateAmount: number;
  totalAccount: number;
  load: boolean;
  link?: string;
}

class TaskCard extends Component<IProp> {
  constructor(props: IProp) {
    super(props);
  }

  render() {
    const { background, headerText, updateText, icon, pendingUpdateAmount, totalAccount, load, intl } = this.props;
    return (
      <CardContainer background={background}>
        <React.Fragment>
          <TaskUpdate>
            <div style={{ margin: 0, color: '#fff' }}>
              <Icon icon="refresh" iconSize={30} />
              <h4 style={{ color: '#fff' }}>{`${intl.formatMessage({ id: 'app.update' })}`}</h4>
            </div>
          </TaskUpdate>
          <CardContent>
            <CardHeaderText>{headerText}</CardHeaderText>
            <CardMeta>
              <span className="date" style={{ color: '#fff' }}>
                {updateText}
              </span>
              <CardAmountInfo>{totalAccount - pendingUpdateAmount}</CardAmountInfo>
            </CardMeta>
            <CardIconHeader>
              <Icon icon={icon} iconSize={45} color="#fff" />
            </CardIconHeader>
          </CardContent>
          <CardAction>
            <TaskPercentBar amount={totalAccount - pendingUpdateAmount} total={totalAccount} />
          </CardAction>
        </React.Fragment>
      </CardContainer>
    );
  }
}

export default injectIntl(TaskCard);
