import React from 'react';
import MessagingComponent from '../../components/messaging/MessagingComponent';
import messagingStore, { MessagingStore } from '../../stores/messaging/MessagingStore';
import { observer } from 'mobx-react';

@observer
class MessagingContainer extends React.Component {
  messaging_store: MessagingStore;
  constructor(props: any) {
    super(props);
    this.messaging_store = messagingStore;
  }
  handleOnDriver = (driver: string) => {
    this.messaging_store.selectDriver(driver);
  };

  render() {
    return (
      <React.Fragment>
        <MessagingComponent
          messages={this.messaging_store.messages}
          drivers={this.messaging_store.drivers}
          setDriver={this.handleOnDriver}
        />
      </React.Fragment>
    );
  }
}

export default MessagingContainer;
