import * as React from 'react';
import Login from '../../components/login/login';
import { inject, observer } from 'mobx-react';
import { AppStore } from '../../../../../core/stores/AppStore';
import { required } from '../../../../commons/form/Validators';
import { FieldState, FormState } from 'formstate';
import { action, observable } from 'mobx';
import { RiftRouter } from 'rift-router';
import { User } from '../../../../../core/models/User';
import authService from '../../../../../core/services/AuthServices';

interface IProp {
  app?: AppStore;
  router?: RiftRouter;
}

@inject('app', 'router')
@observer
export class LoginContainer extends React.Component<IProp> {
  form: FormState<any>;
  @observable error: boolean;
  @observable isLoading: boolean;

  constructor(props: IProp) {
    super(props);
    this.form = new FormState<any>({
      username: new FieldState('').validators(required),
      password: new FieldState('').validators(required)
    });
    this.error = false;
    this.isLoading = false;
  }

  @action
  setError(value: boolean) {
    this.error = value;
  }

  @action
  setIsLoading(value: boolean) {
    this.isLoading = value;
  }

  render() {
    return <Login onLogin={this.login} form={this.form} error={this.error} isLoading={this.isLoading} />;
  }

  login = () => {
    this.form.validate().then(res => {
      this.setError(res.hasError);
      if (!res.hasError) {
        const user = new User(this.form.$.username.$, authService.encriptPassword(this.form.$.password.$));

        const userForm = new FormData();
        userForm.append('username', user.username);
        userForm.append('password', user.password);
        userForm.append('grant_type', user.grant_type);
        this.setIsLoading(true);
        authService
          .login(userForm)
          .then(response => {
            this.props.app!.storeUserCredentials(response.data);
            this.props.router!.riftTo('/');
            this.props.app!.setModulesNavigate({ activeModule: 'home' });
            this.setIsLoading(false);
          })
          .catch((error: any) => {
            console.log(error);
            this.setIsLoading(false);
          });
      }
    });
  };
}
