import React, { Component } from 'react';
import { observer } from 'mobx-react';
import GallonsGraph from './GallonsGraph/GallonsGraph';
import { VCardPanel, VSelectField } from 'gsi-vx-ui';
import { FieldState, FormState } from 'formstate';
import { observable } from 'mobx';
import { GraphContainer, SelectContainer } from './styles';
import { InjectedIntlProps, injectIntl } from 'react-intl';

interface IRecentTasksProps extends InjectedIntlProps {
  load: boolean;
}

@observer
class GallonsPanel extends Component<IRecentTasksProps> {
  @observable
  form!: FormState<any>;

  constructor(props: IRecentTasksProps) {
    super(props);
    this.form = new FormState({
      type: new FieldState('DRIVERS')
    });
  }

  render() {
    const { intl } = this.props;
    const options = [
      {
        value: 'DRIVERS',
        label: `${intl.formatMessage({ id: 'home.dashboard.gallon_status.drivers_label' })}`
      },
      {
        value: 'PRODUCTS',
        label: `${intl.formatMessage({ id: 'home.dashboard.gallon_status.products_label' })}`
      }
    ];

    return (
      <VCardPanel
        headerText={`${intl.formatMessage({
          id: 'home.dashboard.gallon_status.header_panel'
        })} ${this.getTranslateGallonsTitle(this.form.$.type.$)}`}
        headerIcon="database"
      >
        <SelectContainer>
          <VSelectField
            options={options}
            id={'type'}
            label={intl.formatMessage({ id: 'home.dashboard.gallon_status.select_filter' })}
            inline
            fieldState={this.form.$.type}
          />
        </SelectContainer>
        <GraphContainer>
          <GallonsGraph type={this.form.$.type.$} />
        </GraphContainer>
      </VCardPanel>
    );
  }
  getTranslateGallonsTitle(label: 'DRIVERS' | 'PRODUCTS') {
    const { intl } = this.props;
    const translate = {
      DRIVERS: `${intl.formatMessage({ id: 'home.dashboard.gallon_status.drivers_label' })}`,
      PRODUCTS: `${intl.formatMessage({ id: 'home.dashboard.gallon_status.products_label' })}`
    };
    return translate[label];
  }
}

export default injectIntl(GallonsPanel);
