import orderAccountServices from '../services/OrderAccountServices';
import ParentPaginateStore from '../../../../../core/stores/ParentPaginateStore';
import { action, observable } from 'mobx';

export class OrderAccountStore extends ParentPaginateStore {
  @observable openDetails: boolean;
  @observable columnsAcc: string[];
  constructor() {
    super(orderAccountServices);
    this.openDetails = false;
    this.columnsAcc = ['accountId', 'name', 'fullAddress', 'city', 'state', 'productName', 'tanksize'];
  }
  public setPagingOption(data: any) {
    this.setElements(data.content);
    this.setCount(data.page.totalElements);
  }
  @action
  public setOpenDetails(value: boolean) {
    this.openDetails = value;
  }
  @action
  public setColumnsAcc(value: string[]) {
    this.columnsAcc = value;
  }
}

const orderAccountStore = new OrderAccountStore();
export default orderAccountStore;
