import React, { Component } from 'react';
import { Col, Grid, Row } from 'react-flexbox-grid';
import { DispatchDrivers } from '../dispatch_drivers/DispatchDrivers';
import { RoutedOrders } from '../routed_orders/RoutedOrders';
import { DispatchProfileAction } from '../profile_actions/DispatchProfileAction';
import { mapSegment, RowMargin } from './styles';
import { VCardPanel } from 'gsi-vx-ui';
import { Button, ButtonGroup } from '@blueprintjs/core';

export class DeliveryDispatchLayout extends Component {
  handleCreateEditRoute = () => {};

  render() {
    return (
      <Grid fluid>
        <Row>
          <Col xs={3}>
            <RowMargin>
              <ButtonGroup minimal={false} fill>
                <Button icon="new-link">Create Routes</Button>
                <Button icon="edit">Edit Routes</Button>
              </ButtonGroup>
            </RowMargin>
            <Row>
              <DispatchDrivers />
            </Row>
          </Col>
          <Col xs={9}>
            <div style={mapSegment}>
              <VCardPanel height={'95%'} noHeader />
            </div>
          </Col>
        </Row>
        <Row>
          <Col xs={3}>
            <Row>
              <DispatchProfileAction />
            </Row>
          </Col>
          <Col xs={12} md={9}>
            <RoutedOrders />
          </Col>
        </Row>
      </Grid>
    );
  }
}
