import React, { Suspense } from 'react';
import stationLogsStore, { StationLogsStore } from '../../stores/StationLogsStore';

const SearchReviewStationLogs = React.lazy(() => import('../../components/search/SearchReviewStationLogs'));

class SearchReviewStationLogsContainer extends React.PureComponent {
  stationStore: StationLogsStore;
  constructor(props: any) {
    super(props);
    this.stationStore = stationLogsStore;
  }
  render() {
    const { handlerOnSerarch } = this.stationStore;
    return (
      <React.Fragment>
        <Suspense fallback={<React.Fragment>Loading...</React.Fragment>}>
          <SearchReviewStationLogs handleOnSubmit={handlerOnSerarch} />
        </Suspense>
      </React.Fragment>
    );
  }
}

export default SearchReviewStationLogsContainer;
