import React, { Component } from 'react';
import { VCardPanel, VSpinner, VTable } from 'gsi-vx-ui';
import { IVTableProps } from 'gsi-vx-ui/lib/types/Table/Table';
import { inject, observer } from 'mobx-react';
import { ShiftTruckInventoryStore } from '../../../stores/summary/ShiftTruckInventoryStore/ShiftTruckInventoryStore';
import { ShiftTruckInventoryModel } from '../../../models/shiftTruckInventory';
import { AppThemeContext, ITheme } from '../../../../../../../App';

const ThemeConsumer = AppThemeContext.Consumer;

interface ShiftTruckInventoryProps {
  truck_inventory_store?: ShiftTruckInventoryStore;
}

@inject('truck_inventory_store')
@observer
class ShiftTruckInventory extends Component<ShiftTruckInventoryProps> {
  constructor(props: ShiftTruckInventoryProps) {
    super(props);
  }

  render() {
    const data = this.transformRoutesList();
    const loading = this.props.truck_inventory_store!.isLoading;
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <VCardPanel
              headerTextJustify={'center'}
              bodyPadding={'0'}
              headerText={'Truck Inventory'}
              headerBackgroundColor={themeContext.headers}
              headerColor={'#FFFFFF'}
              height={'135px'}
            >
              {loading ? <VSpinner /> : <VTable {...data} />}
            </VCardPanel>
          )
        }
      </ThemeConsumer>
    );
  }

  transformRoutesList(): IVTableProps {
    const data = this.props.truck_inventory_store!.elements.map((item: ShiftTruckInventoryModel) => {
      return {
        truckName: item.truckName,
        tankName: item.tankName,
        productName: item.productName,
        start: item.start,
        loaded: item.loaded,
        end: item.end,
        totalDelQty: item.totalDelQty
      };
    });
    return {
      data,
      reordering: true,
      settingEmptyData: {
        height: '100px'
      },
      onOrderColumns: this.onOrdering,
      columns: this.props.truck_inventory_store!.columns,
      tableHeight: '100px',
      striped: true,
      columns_name: {
        truckName: 'Truck',
        tankName: 'Tank',
        productName: 'Product',
        start: 'Start',
        loaded: 'Loaded',
        end: 'End',
        totalDelQty: 'Total Qty Del'
      },
      typeHeightRow: 'HALF'
    };
  }

  onOrdering = (columns: string[]) => {
    this.props.truck_inventory_store!.setColumns(columns);
  };
}

export default ShiftTruckInventory;
