import React, { Component } from 'react';
import './App.scss';
import { AppStore } from './core/stores/AppStore';
import { RiftGate, RiftRouter } from 'rift-router';
import { inject, observer } from 'mobx-react';
import { flattenMessages } from './modules/commons/utils/utils';
import { addLocaleData, IntlProvider } from 'react-intl';
import { computed, reaction } from 'mobx';
import es from 'react-intl/locale-data/es';
import en from 'react-intl/locale-data/en';
/* Internationalization */
import enMessage from './translations/en.json';
import esMessage from './translations/es.json';

interface IAppState {
  logged: boolean;
  app?: AppStore;
  router?: RiftRouter;
}

export interface ITheme {
  primary: string;
  primary_70?: string;
  primary_50?: string;
  primary_20?: string;
  secondary: string;
  secondary_70?: string;
  secondary_50?: string;
  secondary_20?: string;
  accent_blue: string;
  accent_gray: string;
  accent_red: string;
  accent_green: string;
  dark_grey: string;
  sidebar: string;
  sidebar_text: string;
  headers: string;
}
export const themes = {
  light: {
    primary: '#FF6D00',
    primary_70: '#FF8F43',
    primary_50: '#FFC299',
    primary_20: '#FFE1CC',
    secondary: '#205CAA',
    secondary_70: '#396FAA',
    secondary_50: '#A5B5C4',
    secondary_20: '#D9DBDD',
    accent_blue: '#4bacef',
    accent_gray: '#efefef',
    accent_red: '#f24444',
    accent_green: '#20a8a4',
    dark_grey: '#424242',
    sidebar: '#424242',
    sidebar_text: '#FFFFFF',
    headers: '#396FAA'
  },
  alternative: {
    primary: '#205CAA',
    primary_70: '#396FAA',
    primary_50: '#A5B5C4',
    primary_20: '#D9DBDD',
    secondary: '#FF6D00',
    secondary_70: '#FF8F43',
    secondary_50: '#FFC299',
    secondary_20: '#FFE1CC',
    accent_blue: '#4BACEF',
    accent_gray: '#EFEFEF',
    accent_red: '#F24444',
    accent_green: '#20A8A4',
    dark_grey: '#424242',
    sidebar: '#205CAA',
    sidebar_text: '#FFFFFF',
    headers: '#373d44'
  }
};

export const AppThemeContext = React.createContext<ITheme>(themes.light);

interface IProps {
  app?: AppStore;
}

@inject('app')
@observer
class App extends Component<IProps, IAppState> {
  constructor(props: IProps) {
    super(props);

    const language = (navigator.languages && navigator.languages[0]) || navigator.language;

    // Split locales with a region code
    const lanWithoutCode = language.toLowerCase().split(/[_-]+/)[0];

    this.props.app!.setLang(lanWithoutCode);
  }

  @computed
  get messages() {
    const lang = this.props.app!.lang;
    switch (lang) {
      case 'es':
        addLocaleData(es);
        return esMessage;
      case 'en':
        addLocaleData(en);
        return enMessage;

      default:
        addLocaleData(es);
        return esMessage;
    }
  }

  render() {
    const { app } = this.props;
    return (
      <IntlProvider locale={app!.lang} messages={flattenMessages(this.messages)}>
        <AppThemeContext.Provider value={app!.theme}>
          <div id={'App'} className="App">
            <RiftGate />
          </div>
        </AppThemeContext.Provider>
      </IntlProvider>
    );
  }
}

export default App;
