import React from 'react';
import { TaskProgress, TaskProgressPart, TaskProgressPartInfo } from './styles';

interface ITaskPercentBar {
  total: number;
  amount: number;
}

export const TaskPercentBar = (props: ITaskPercentBar) => {
  const percent = ((props.amount * 100) / props.total).toFixed(1);
  return (
    <TaskProgress>
      <TaskProgressPart percent={12} background={'#525353'}>
        <TaskProgressPartInfo>{`${percent} %`}</TaskProgressPartInfo>
      </TaskProgressPart>
    </TaskProgress>
  );
};
