import styled from 'styled-components';
import { Icon } from '@blueprintjs/core';

export const MenuLogo = styled.div`
  display: flex;
  align-content: flex-start;
  justify-content: center;
  cursor: pointer;
`;

interface IIconSidebar {
  textcolor: string;
}

export const IconSidebar = styled(Icon)`
  svg {
    fill: ${(props: IIconSidebar) => props.textcolor && props.textcolor};
  }
  margin: 5px 0;
`;

interface ILabelSidebar {
  textcolor: string;
}

export const LabelSidebar = styled.span`
  font-size: 12px;
  color: ${(props: ILabelSidebar) => props.textcolor && props.textcolor};
`;

interface ICSidebar {
  collapse: boolean;
  background: string;
  textcolor: string;
}

export const CSidebar = styled.div`
  padding-top: 10px;
  background-color: ${(props: ICSidebar) => props.background};
  box-shadow: inset -1px -1px 0px rgba(0, 0, 0, 0.09);
  color: ${(props: ICSidebar) => props.textcolor && props.textcolor};
  min-height: 100%;
  width: ${(props: ICSidebar) => props.collapse && '0px !important'};
  transition: width ease-in 120ms;
  @media (max-width: 992px) {
    width: 75px;
  }
  @media (min-width: 993px) {
    width: 120px;
  }
`;

export const CSidebarPushable = styled.div`
  display: flex;
  align-items: flex-start;
  justify-content: start;
  width: 100%;
  height: 100%;
  overflow: hidden;
`;

export const CSidebarPusher = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  flex: 1;
  @media (max-width: 992px) {
    width: calc(100% - 75px);
  }
  @media (min-width: 993px) {
    width: calc(100% - 120px);
  }
`;

interface IContainer {
  background: string;
}

export const Container = styled.div`
  padding: 10px;
  width: 100%;
  background-color: ${(props: IContainer) => props.background && props.background};
`;

interface IMenuItemActive {
  active: boolean;
}

export const MenuItem = styled.div`
  width: 100%;
  padding: 10px;
  ${(props: IMenuItemActive) => props.active && 'background-color: rgba(167, 182, 194, 0.3);'}
  &:hover {
    background-color: rgba(167, 182, 194, 0.3);
    cursor: pointer;
    text-decoration: none;
  }
`;

export const VisibleLabel = styled.div`
  @media (max-width: 992px) {
    visibility: hidden;
    height: 0;
  }
  @media (min-width: 993px) {
    visibility: visible;
    height: auto;
  }
`;

interface IThemeCircle {
  themeProps: any;
}

export const ThemeCircle = styled.div`
  width: 0;
  height: 0;
  border-bottom: 10px solid ${(props: IThemeCircle) => props.themeProps.accent_red};
  border-top: 10px solid ${(props: IThemeCircle) => props.themeProps.accent_green};
  border-left: 10px solid ${(props: IThemeCircle) => props.themeProps.primary};
  border-right: 10px solid ${(props: IThemeCircle) => props.themeProps.secondary};
  -moz-border-radius: 10px;
  -webkit-border-radius: 10px;
  border-radius: 10px;
`;
