import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { RiftRouter } from 'rift-router';

import { CSidebar, IconSidebar, LabelSidebar, MenuItem, VisibleLabel } from './styles';
import { AppStore } from '../../stores/AppStore';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { ModulesKey } from '../../types/ModulesNavigateType';
import { AppThemeContext, ITheme } from '../../../App';

const ThemeConsumer = AppThemeContext.Consumer;

interface IProps extends InjectedIntlProps {
  app?: AppStore;
  router?: RiftRouter;
}

@inject('app', 'router')
@observer
class MainSidebar extends Component<IProps> {
  constructor(props: IProps) {
    super(props);
    this.props.app!.getAlerts();
  }
  render() {
    const { app, intl } = this.props;
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <CSidebar
              background={themeContext.sidebar}
              textcolor={themeContext.sidebar_text}
              collapse={this.props.app!.isCollapseMenu}
            >
              <MenuItem
                active={app!.modulesNavigate.activeModule === 'home'}
                onClick={(e: any) => this.linkTo('/home', 'home')}
              >
                <IconSidebar textcolor={themeContext.sidebar_text} icon="home" color="white" iconSize={24} />
                <VisibleLabel>
                  <LabelSidebar textcolor={themeContext.sidebar_text}>{`${intl.formatMessage({
                    id: 'layout.sidebar.home'
                  })}`}</LabelSidebar>
                </VisibleLabel>
              </MenuItem>
              <MenuItem
                active={app!.modulesNavigate.activeModule === 'deliveryManagement'}
                onClick={(e: any) => this.linkTo('/delivery', 'deliveryManagement')}
              >
                <IconSidebar textcolor={themeContext.sidebar_text} icon="drive-time" color="white" iconSize={24} />
                <VisibleLabel>
                  <LabelSidebar textcolor={themeContext.sidebar_text}>{`${intl.formatMessage({
                    id: 'layout.sidebar.delivery_manager'
                  })}`}</LabelSidebar>
                </VisibleLabel>
              </MenuItem>
              <MenuItem
                active={app!.modulesNavigate.activeModule === 'config'}
                onClick={(e: any) => this.linkTo('/setup', 'config')}
              >
                <IconSidebar textcolor={themeContext.sidebar_text} icon="cog" color="white" iconSize={24} />
                <VisibleLabel>
                  <LabelSidebar textcolor={themeContext.sidebar_text}>{`${intl.formatMessage({
                    id: 'layout.sidebar.setup'
                  })}`}</LabelSidebar>
                </VisibleLabel>
              </MenuItem>
              <MenuItem
                active={app!.modulesNavigate.activeModule === 'adminAccount'}
                onClick={(e: any) => this.linkTo('/account', 'adminAccount')}
              >
                <IconSidebar textcolor={themeContext.sidebar_text} icon="user" color="white" iconSize={24} />
                <VisibleLabel>
                  <LabelSidebar textcolor={themeContext.sidebar_text}>{`${intl.formatMessage({
                    id: 'layout.sidebar.account_manager'
                  })}`}</LabelSidebar>
                </VisibleLabel>
              </MenuItem>
              <MenuItem
                active={app!.modulesNavigate.activeModule === 'dashboard'}
                onClick={(e: any) => this.linkTo('/dashboard', 'dashboard')}
              >
                <IconSidebar textcolor={themeContext.sidebar_text} icon="dashboard" color="white" iconSize={24} />
                <VisibleLabel>
                  <LabelSidebar textcolor={themeContext.sidebar_text}>{`${intl.formatMessage({
                    id: 'layout.sidebar.performance_dashboard'
                  })}`}</LabelSidebar>
                </VisibleLabel>
              </MenuItem>
              <MenuItem
                active={app!.modulesNavigate.activeModule === 'dashboard'}
                onClick={(e: any) => this.linkTo('/dashboard', 'dashboard')}
              >
                <IconSidebar textcolor={themeContext.sidebar_text} icon="help" color="white" iconSize={24} />
                <VisibleLabel>
                  <LabelSidebar textcolor={themeContext.sidebar_text}>{`${intl.formatMessage({
                    id: 'layout.sidebar.support'
                  })}`}</LabelSidebar>
                </VisibleLabel>
              </MenuItem>
            </CSidebar>
          )
        }
      </ThemeConsumer>
    );
  }

  linkTo = (path: string, activeModule: ModulesKey) => {
    this.props.app!.setModulesNavigate({ activeModule });
    this.props.router!.riftTo(path);
  };
}

export default injectIntl(MainSidebar);
