import { findIndex, pull, uniqBy } from 'lodash';
import { action, observable } from 'mobx';

export class ParentStore {
  @observable
  public element: any;

  @observable
  public elements: any[];

  @observable
  public selectedElements: any[];

  @observable
  public page: number;

  @observable
  public size: number;
  @observable
  public search: string;

  @observable
  public count: number;

  @observable
  public isLoading: boolean;

  public serviceContainer: any;

  constructor(serviceContainer: any) {
    this.serviceContainer = serviceContainer;
    this.element = {};
    this.elements = [];
    this.selectedElements = [];
    this.search = '';
    this.count = 0;
    this.page = 1;
    this.size = 10;
    this.isLoading = false;
  }

  @action
  public setLoading(value: boolean) {
    this.isLoading = value;
  }

  @action
  public setElement(element: any) {
    this.element = element;
  }

  @action
  public resetElement() {
    this.element = {};
  }

  @action
  public setElements(elements: any[]) {
    this.elements = elements;
  }

  @action
  public setSelectedElements(item: any[]) {
    this.selectedElements = uniqBy(item, 'id');
  }

  @action
  public resetSelectedElements() {
    this.selectedElements = [];
  }

  @action
  public pushSelectedElements(item: any) {
    if (findIndex(this.selectedElements, { id: item.id }) === -1) {
      this.selectedElements.push(item);
    }
  }

  @action
  public pullSelectedElements(item: any) {
    this.selectedElements = pull(this.selectedElements, item);
  }

  @action
  public setPage(item: number) {
    this.page = item;
  }

  @action
  public setSize(value: number) {
    this.size = value;
  }

  @action
  public setCount(value: number) {
    this.count = value;
  }
  @action
  public setSearch(value: string) {
    this.search = value;
  }

  @action
  public createStore() {
    this.element = {};
    this.elements = [];
    this.selectedElements = [];
    this.count = 0;
    this.page = 1;
    this.size = 10;
    this.search = '';
    this.isLoading = true;
  }

  public getElements() {
    this.setLoading(true);
    this.serviceContainer
      .getAll()
      .then((response: any) => {
        this.setCount(response.data.length);
        this.setElements(response.data);
        this.resetSelectedElements();
        this.setLoading(false);
      })
      .catch((err: any) => {
        this.setLoading(false);
        throw err.response;
      });
  }

  public getElementById(id: number | string) {
    this.setLoading(true);
    this.serviceContainer
      .getById(id)
      .then((response: any) => {
        this.element = response.data;
        this.setLoading(false);
      })
      .catch((err: any) => {
        this.setLoading(false);
        throw err.response;
      });
  }

  public updateElement() {
    const item = this.element;
    this.setLoading(true);
    this.serviceContainer
      .update(item)
      .then((response: any) => {
        this.getElements();
        this.resetElement();
        this.resetSelectedElements();
        this.setLoading(false);
      })
      .catch((err: any) => {
        this.setLoading(false);
        throw err.response;
      });
  }

  public deleteElement() {
    const itemId = this.element.id;
    this.setLoading(true);
    this.serviceContainer
      .delete(itemId)
      .then((response: any) => {
        this.getElements();
        this.resetElement();
        this.resetSelectedElements();
        this.setLoading(false);
      })
      .catch((err: any) => {
        this.setLoading(false);
        throw err.response;
      });
  }

  public deleteElementById(itemId: number) {
    this.setLoading(true);
    this.serviceContainer
      .delete(itemId)
      .then((response: any) => {
        this.getElements();
        this.resetElement();
        this.resetSelectedElements();
        this.setLoading(false);
      })
      .catch((err: any) => {
        this.setLoading(false);
        throw err.response;
      });
  }

  public deleteElements() {
    this.setLoading(true);
    const promiseArray: any[] = [];
    for (const element of this.selectedElements) {
      promiseArray.push(this.serviceContainer.delete(element.id));
    }
    Promise.all(promiseArray).then((response: any) => {
      this.getElements();
      this.resetElement();
      this.resetSelectedElements();
      this.setLoading(false);
    });
  }

  public createElement() {
    const item = this.element;
    this.setLoading(true);
    this.serviceContainer
      .create(item)
      .then((response: any) => {
        this.getElements();
        this.resetElement();
        this.resetSelectedElements();
        this.setLoading(false);
      })
      .catch((err: any) => {
        this.setLoading(false);
        const resp = err.response;
        throw resp;
      });
  }
}
