import React, { Component } from 'react';
import TabsMenu from '../../commons/components/tabs_menu/TabsMenu';
import StationLogContainer from '../modules/station_logs/containers/StationLogContainer';
import { inject, observer } from 'mobx-react';
import { ReviewStore } from '../ReviewStore';
import IModulesTypes from '../../commons/types/modules';

interface IReviewLayout {
  review_store?: ReviewStore;
}

@inject('review_store')
@observer
class ReviewLayout extends Component<IReviewLayout> {
  constructor(props: IReviewLayout) {
    super(props);
  }

  handleChange = (data: IModulesTypes) => {
    this.props.review_store!.setActiveModule(data);
  };

  render() {
    const { activeModule, modules } = this.props.review_store!;

    let component;
    switch (activeModule.name) {
      case 'stationsLogs':
        component = <StationLogContainer />;
        break;
      default:
        component = <span>Review</span>;
        break;
    }
    return (
      <React.Fragment>
        <TabsMenu activeTab={activeModule} tabList={modules} handleChange={this.handleChange} />
        {component}
      </React.Fragment>
    );
  }
}

export default ReviewLayout;
