import React, { Component } from 'react';
import { StationLogsTicketsFormToolbar } from '../components/form/tickets/form_toolbar';
import { StationLogsTicketsForm } from '../components/form/tickets/form';
import { Provider } from 'mobx-react';
import stationLogsTicketsStore from '../stores/StationLogsTicketsStore';
import Panel from '../../../../commons/components/panel/Panel';

const store = {
  station_logs_tickets: stationLogsTicketsStore
};

export default class StationLogsTickets extends Component {
  render() {
    return (
      <Provider {...store}>
        <React.Fragment>
          <StationLogsTicketsFormToolbar
            onSave={this.onSave}
            onCreate={this.onCreate}
            onEdit={this.onEdit}
            onHTML={this.onHTML}
          />
          <Panel header={'TICKET'}>
            <StationLogsTicketsForm />
          </Panel>
        </React.Fragment>
      </Provider>
    );
  }

  onSave() {
    console.log('on Save');
  }

  onCreate() {
    console.log('on Create');
  }

  onEdit() {
    console.log('on Edit');
  }

  onHTML() {
    console.log('on HTML');
  }

  onValidateForm() {
    console.log('on Validate');
  }
}
