import { AxiosInstance, AxiosPromise } from 'axios';
import { createHttpAxios } from '../../../../../core/services/http';

export class StationLogsServices {
  public httpAxios: AxiosInstance;
  constructor() {
    this.httpAxios = createHttpAxios('/stationLogs/tickets');
  }

  public getTickets(): AxiosPromise<any> {
    return this.httpAxios
      .get('')
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }
}

const stationLogsServices = new StationLogsServices();

export default stationLogsServices;
