import React, { Component } from 'react';
import { Col, Row } from 'react-flexbox-grid';
import { ListStyle } from './styles';

interface IDetailsListProps {
  items: ItypesList[];
  color?: string;
  orientationHeader?: IOrientation;
  orientationDescription?: IOrientation;
}

interface ItypesList {
  key: string;
  header: string;
  description: any;
}

interface IOrientation {
  position: 'start' | 'center' | 'end';
  resolution: 'xs' | 'sm' | 'md' | 'lg';
  size?: number;
}

export class DetailsList extends Component<IDetailsListProps> {
  constructor(props: IDetailsListProps) {
    super(props);
  }

  render() {
    const { items, orientationHeader, orientationDescription, color } = this.props;
    const renderpositionheader = {
      [orientationHeader ? orientationHeader.position : 'start']: orientationHeader
        ? orientationHeader.resolution
        : 'xs'
    };
    const renderpositiondescription = {
      [orientationDescription ? orientationDescription.position : 'end']: orientationDescription
        ? orientationDescription.resolution
        : 'xs'
    };
    const headerSize = orientationHeader ? orientationHeader.size : 6;
    let descriptionSize = 6;
    if (orientationDescription && orientationDescription.size) {
      descriptionSize = orientationDescription.size;
    } else if (orientationHeader && orientationHeader.size) {
      descriptionSize = 12 - orientationHeader.size;
    }
    return (
      <ListStyle color={color}>
        {items.map(item => (
          <Row key={item.key} middle={'xs'}>
            <Col xs={12} sm={headerSize}>
              <Row {...renderpositionheader} middle={'xs'}>
                <span>{item.header} </span>
              </Row>
            </Col>
            <Col xs={12} sm={descriptionSize}>
              <Row {...renderpositiondescription} middle={'xs'}>
                <span className="bp3-text-muted" style={{ width: '100%' }}>
                  {item.description}
                </span>
              </Row>
            </Col>
          </Row>
        ))}
      </ListStyle>
    );
  }
}
