export class ShiftDriverCheckListModel {
  shiftId: number;
  loginStarted: string;
  logoutEnd: string;
  mobileAppVersion: string;
  driverName: string;
  truckName: string;
  preTripVehicleInspection: string;
  postTripVehicleInspection: string;
  lastLogout: string;
  shiftDeliveries: number;
  completeShiftsCount: number;

  constructor(
    shiftId = 0,
    loginStarted = '',
    logoutEnd = '',
    mobileAppVersion = '',
    driverName = '',
    truckName = '',
    preTripVehicleInspection = '',
    postTripVehicleInspection = '',
    lastLogout = '',
    shiftDeliveries = 0,
    completeShiftsCount = 0
  ) {
    this.shiftId = shiftId;
    this.loginStarted = loginStarted;
    this.logoutEnd = logoutEnd;
    this.mobileAppVersion = mobileAppVersion;
    this.driverName = driverName;
    this.truckName = truckName;
    this.preTripVehicleInspection = preTripVehicleInspection;
    this.postTripVehicleInspection = postTripVehicleInspection;
    this.lastLogout = lastLogout;
    this.shiftDeliveries = shiftDeliveries;
    this.completeShiftsCount = completeShiftsCount;
  }
}
