import React, { Component } from 'react';
import { DeliverySummaryRoute } from '../DeliverySummaryRoute/DeliverySummaryRoute';
import { DeliverySummaryDeliveries } from '../DeliverySummaryDeliveries/DeliverySummaryDeliveries';
import { ITabsPanelTypes, VDateTimePicker, VInputField, VTabsPanel } from 'gsi-vx-ui';
import { FieldState, FormState } from 'formstate';
import SummaryShiftDetail from '../SummaryShiftDetails/SummaryShiftDetail';
import SummaryExport from '../SummaryExport/SummaryExport';
import { Row, Col } from 'react-flexbox-grid';
import { Button } from '@blueprintjs/core';
import styled from 'styled-components';
import { inject, observer } from 'mobx-react';
import { RiftRouter } from 'rift-router';
import { action, observable } from 'mobx';
import { SummaryStore } from '../../stores/summary/SummaryStore';
import { SummaryDeliveriesStore } from '../../stores/summary/DeliveriesStore/SummaryDeliveriesStore';
import { SummaryRouteStore } from '../../stores/summary/routeStore/SummaryRouteStore';
import { ShiftModel } from '../../models/shiftModel';
import { FilterOperator } from '../../../../../../core/types/FilterType';
import moment from 'moment';
import { ShiftDetailsStore } from '../../stores/summary/ShiftDetailsStore/ShiftDetailsStore';
import { DetailsShift } from '../../models/detailsShift';
import { ShiftDriverCheckListModel } from '../../models/ShiftDriverCheckListModel';
import { debounce } from 'formstate/lib/internal/utils';

const ButtonStyle = styled(Button)`
  margin-bottom: 15px;
`;

interface IProps {
  router?: RiftRouter;
  summary_store?: SummaryStore;
  deliveries_store?: SummaryDeliveriesStore;
  route_store?: SummaryRouteStore;
  details_store?: ShiftDetailsStore;
}

@inject('summary_store', 'router', 'deliveries_store', 'route_store', 'details_store')
@observer
export class SummaryLayout extends Component<IProps> {
  form: FormState<any>;
  @observable
  activeModule: string;

  constructor(props: IProps) {
    super(props);
    const date = this.props.summary_store!.date || new Date();
    this.form = new FormState<any>({
      dateTimeSummary: new FieldState(date),
      search: new FieldState('')
    });
    if (this.props.router!.search.hasOwnProperty('child')) {
      this.activeModule = this.props.router!.search.child;
    } else {
      this.activeModule = 'summary';
    }
  }

  @action
  setShift(shift: ShiftModel) {
    this.props.summary_store!.setShift(shift);
    if (shift.shiftId !== this.props.deliveries_store!.element.shiftId) {
      this.props.deliveries_store!.getElements(`?shiftId=${shift.shiftId}`);
    }
    this.props.deliveries_store!.setElement(shift);
  }

  onSelectShift = (data: any) => {
    switch (data.tab) {
      case 'summary':
        this.setShift({
          shiftId: data.element.shiftId,
          driverFullName: data.element.driverFullName,
          truckName: data.element.truckName,
          driverId: data.element.driverId
        });
        if (data.element.driverId) {
          const operator: FilterOperator = 'EQUALITY';
          this.props.details_store!.addFilter({
            operator,
            field: 'date',
            value: moment(this.props.summary_store!.date).format('YYYY-MM-DD')
          });
          this.props.details_store!.addFilter({
            operator,
            field: 'driverId',
            value: this.props.summary_store!.shift.driverId
          });
          this.props.details_store!.getElements();
        } else {
          this.props.details_store!.setElement(new DetailsShift());
          this.props.details_store!.setElementDriverCheckList(new ShiftDriverCheckListModel());
          this.props.details_store!.setElements([]);
          this.props.details_store!.setLoading(false);
        }
        break;
      case 'details':
        if (this.activeModule === 'details') {
          this.setShift({
            shiftId: data.element.shiftId,
            driverFullName: data.element.driver,
            truckName: data.element.truck,
            driverId: data.element.driverId
          });
        }
        break;
    }
  };

  render() {
    const tabList: ITabsPanelTypes[] = [
      {
        label: 'Summary',
        content: (
          <React.Fragment>
            <DeliverySummaryRoute onSelectShift={this.onSelectShift} />
            <br />
            <DeliverySummaryDeliveries />
          </React.Fragment>
        ),
        key: 'summary'
      },
      {
        label: 'Shift Details',
        key: 'details',
        content: <SummaryShiftDetail onSelectShift={this.onSelectShift} />
      },
      {
        label: 'Export',
        key: 'export',
        content: <SummaryExport />
      }
    ];
    return (
      <React.Fragment>
        <Row between={'xs'}>
          <Col xs={2}>
            <VDateTimePicker
              dateType={'DATE'}
              id={'summaryDateSelect'}
              onChange={this.getOnChange}
              fieldState={this.form.$.dateTimeSummary}
              icon={{ iconName: 'calendar' }}
            />
          </Col>
          <Col xs={3}>
            <Row end={'xs'}>
              <Col xs={7}>
                {this.activeModule !== 'export' && (
                  <VInputField
                    fill
                    onChange={this.onSearchChange}
                    fieldState={this.form.$.search}
                    id="search"
                    type="search"
                    leftIcon="search"
                    placeholder={'Search'}
                  />
                )}
              </Col>
              <Col xs={5}>
                <ButtonStyle fill alignText={'center'} icon={'refresh'} text={'Refresh'} onClick={this.onRefresh} />
              </Col>
            </Row>
          </Col>
        </Row>

        <Row>
          <Col xs>
            <VTabsPanel
              padding={'10px 0 0 0'}
              tabList={tabList}
              activeColor={'#e8e8e8'}
              active={this.activeModule}
              handleChange={this.handelChangeTab}
            />
          </Col>
        </Row>
      </React.Fragment>
    );
  }

  private getOnChange = (data: any) => {
    this.props.summary_store!.setDate(data);
    const operator: FilterOperator = 'EQUALITY';
    this.props.route_store!.addFilter({
      operator,
      field: 'date',
      value: moment(data).format('YYYY-MM-DD')
    });
    this.props.route_store!.getElements();
    this.props.deliveries_store!.setLoading(true);
    this.props.details_store!.setLoading(true);
  };

  onRefresh = () => {};

  handelChangeTab = (tab: ITabsPanelTypes) => {
    this.activeModule = tab.key;
  };

  onSearchChange = (data: string) => {
    this.form.$.search.onChange(data);
    this.raiseDoSearchWhenUserStoppedTyping();
  };
  raiseDoSearchWhenUserStoppedTyping = debounce(() => {
    this.props.deliveries_store!.setSearch(this.form.$.search.value);
  }, 1200);
}
