import React, { ReactNode } from 'react';
import { action, observable } from 'mobx';
import { Icon, IconName } from '@blueprintjs/core';
import { IExport } from '../enums/enums';

export class OrdersStore {
  @observable public activeList: string;
  @observable public openFilters: boolean;
  @observable public filterByUrgency: boolean;
  @observable public searchAccountModal: boolean;
  @observable public searchDays: number | string;
  constructor() {
    this.activeList = 'open';
    this.openFilters = false;
    this.searchDays = '';
    this.searchAccountModal = false;
    this.filterByUrgency = true;
  }
  @action
  public setSearchAccountModal(value: boolean) {
    this.searchAccountModal = value;
  }
  @action
  public setActiveList(active: number) {
    let value = '';
    switch (active) {
      case 1:
        value = 'incomplete';
        break;
      case 2:
        value = 'duplicate';
        break;
      case 3:
        value = 'ungeo';
        break;
      case 9:
        value = 'total';
        break;
      default:
        value = 'open';
        break;
    }
    this.activeList = value;
  }
  public static renderDeliveryType(value: string = 'default'): ReactNode {
    let icon: IconName;
    switch (value) {
      case 'WillCall':
        icon = 'phone';
        break;
      case 'Automatic':
        icon = 'cog';
        break;
      case 'Calendar':
        icon = 'calendar';
        break;
      case 'JULIAN':
        icon = 'clipboard';
        break;
      default:
        icon = 'circle';
        break;
    }
    return (
      <div style={{ width: 100, justifyContent: 'flex-start', alignItems: 'flex-start', display: 'flex' }}>
        <span>{value !== 'TOTAL' && <Icon icon={icon} iconSize={17} color={'#10161A'} />}</span>
        <p style={{ marginLeft: value !== 'TOTAL' ? 5 : 30 }}>{value}</p>
      </div>
    );
  }
  public static getCustomerType(value: string = 'circle.6', action: any) {
    let icon: IconName;
    let color: string;
    const result = value.split('.');
    const search = result[0].split('/');
    switch (search[0]) {
      case 'WillCall':
        icon = 'phone';
        break;
      case 'Automatic':
        icon = 'cog';
        break;
      case 'Calendar':
        icon = 'calendar';
        break;
      case 'JULIAN':
        icon = 'clipboard';
        break;
      default:
        icon = 'circle';
        break;
    }
    switch (result[1]) {
      case '1':
        color = '#10161A';
        break;
      case '2':
        color = '#DB3737';
        break;
      case '3':
        color = '#FFC940';
        break;
      case '4':
        color = '#43BF4D';
        break;
      case '5':
        color = '#2B95D6';
        break;
      default:
        color = '#FFFFFF';
        break;
    }
    return (
      <div onDoubleClick={action}>
        <Icon icon={icon} iconSize={17} color={color} />
      </div>
    );
  }
  @action
  public getOrderFeature(): IExport {
    let feature = '';
    let name = '';
    let store:
      | 'open_orders_store'
      | 'duplicate_orders_store'
      | 'open_orders_store'
      | 'ungeo_store'
      | 'incomplete_orders_store' = 'open_orders_store';
    switch (this.activeList) {
      case 'open':
        feature = 'OPEN_ORDER';
        store = 'open_orders_store';
        name = 'Open Orders';
        break;
      case 'duplicate':
        feature = 'DUPLICATE_ORDER';
        store = 'duplicate_orders_store';
        name = 'Duplicate Orders';
        break;
      case 'total':
        feature = 'TOTAL_OPEN_ORDER';
        store = 'open_orders_store';
        name = 'Orders';
        break;
      case 'ungeo':
        feature = 'UN_GEOCODE_ORDER';
        store = 'ungeo_store';
        name = 'UnGeoCoded Orders';
        break;
      case 'incomplete':
        feature = 'INCOMPLETE_ORDER';
        store = 'incomplete_orders_store';
        name = 'Incomplete Orders';
        break;
      default:
        feature = 'OPEN_ORDER';
        store = 'open_orders_store';
        name = 'Open Orders';
        break;
    }
    return { feature, store, name };
  }
  @action
  public setOpenFilters(value: boolean) {
    this.openFilters = value;
  }
  @action
  public changeFilterByUrgency(data: boolean) {
    this.filterByUrgency = data;
  }
  @action
  public setSearchByDays(days: number | string) {
    this.searchDays = days;
  }
}

const orderStore = new OrdersStore();
export default orderStore;
