import React from 'react';
import styled from 'styled-components';

export const ListStyle = styled('div')`
  & > div {
    margin: 0;
    padding: 5px 20px;
    ${(props: any) => props.color && `color: ${props.color};`}
  }
  & > div:nth-child(even) {
    background: #efefefa6;
  }
`;
