import { action, computed, observable } from 'mobx';
import { FieldState, FormState } from 'formstate';
import { required } from '../../../../commons/form/Validators';

export class StationLogsTicketsStore {
  @observable
  form!: FormState<any>;

  constructor() {
    this.initForm();
  }

  @action
  setForm(form: FormState<any>) {
    this.form = form;
  }

  @action
  initForm() {
    const form = new FormState({
      stationType: new FieldState('').validators(required),
      stationLogDate: new FieldState('').validators(required),
      stationDatetime: new FieldState('').validators(required),
      pickupName: new FieldState('').validators(required),
      ticket: new FieldState('').validators(required),
      dropOffName: new FieldState('').validators(required),
      dropOff: new FieldState('').validators(required),
      driver: new FieldState('').validators(required),
      truckedBy: new FieldState('').validators(required),
      er: new FieldState(''),
      topGauge: new FormState({
        ft: new FieldState(''),
        in: new FieldState(''),
        quarter: new FieldState('')
      })
    });
    this.form = form;
  }

  @computed
  get validForm() {
    return !this.form.hasError && !this.form.validating;
  }
}

const stationLogsTicketsStore = new StationLogsTicketsStore();

export default stationLogsTicketsStore;
