import React, { Component, ReactNode } from 'react';
import { VCardPanel, VTable } from 'gsi-vx-ui';
import { IVTableProps } from 'gsi-vx-ui/lib/types/Table/Table';
import { IVWidgetTableProps } from 'gsi-vx-ui/lib/types/Table/Widget/Widget';
import { Button, Icon } from '@blueprintjs/core';
import moment from 'moment';
import styled from 'styled-components';
import { AppThemeContext, ITheme } from '../../../../../../../App';

const ThemeConsumer = AppThemeContext.Consumer;

export const ButtonStyle = styled(Button)`
  margin-bottom: 15px;
  margin-top: 15px;
`;

class ExportDeliveriesExport extends Component {
  render() {
    const date = moment().format('dddd, MMMM D');
    const data = this.transformRoutesList();
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <VCardPanel
              headerTextJustify={'center'}
              headerText={`Export Deliveries for ${date}`}
              headerBackgroundColor={themeContext.headers}
              headerColor={'#FFFFFF'}
            >
              <VTable {...data} />
              <ButtonStyle alignText={'center'} icon={'export'} text={'Export to Back Office'} />
            </VCardPanel>
          )
        }
      </ThemeConsumer>
    );
  }

  transformRoutesList(): IVTableProps {
    // todo do some tranform to get de list.
    /*
     * Get the result of the store and then here transform the data to the list
     */
    const date = moment().format('DD/MM/YYYY LT');
    const customWidget: IVWidgetTableProps = {
      column: 'export',
      widget: {
        type: 'CUSTOMERCOMPONENT',
        cusmtomerCell: {
          renderCustomer: (value: string): ReactNode => {
            return (
              <div>
                <Icon icon={'flag'} iconSize={15} color={value} />
              </div>
            );
          }
        }
      }
    };
    const widgetsCell: IVWidgetTableProps[] = [customWidget];
    return {
      widgetsCell,
      columns: ['driver', 'export', 'datep'],
      columns_name: {
        driver: 'Driver',
        export: 'Export Status',
        datep: 'Date Posted'
      },
      data: [
        {
          driver: 'Bob A',
          export: '#92d36e',
          datep: `${date}`
        },
        {
          driver: 'Truck 2',
          export: '#92d36e',
          datep: `${date}`
        },
        {
          driver: 'Truck 6',
          export: '#92d36e',
          datep: ''
        }
      ],
      reordering: true,
      typeHeightRow: 'HALF'
    };
  }

  onExport = () => {};
}

export default ExportDeliveriesExport;
