import styled from 'styled-components';
import { Card } from '@blueprintjs/core';

export const CardAction = styled.div`
  background-color: rgba(82, 83, 83, 0.3) !important;
  transition: background 0.1s linear;
  text-transform: uppercase;
  color: #fff !important;
  cursor: pointer;
  padding: 0 !important;
  bottom: 0;
  left: 0;
  position: absolute;
  width: 100%;
  &:hover {
    background-color: rgba(82, 83, 83, 0.35) !important;
  }
`;

export const CardHeaderText = styled.div`
  color: #fff !important;
  text-align: right;
  font-size: 16px;
`;

export const CardAmountInfo = styled.h1`
  color: #fff !important;
  margin-top: 10px;
`;

export const CardIconHeader = styled.div`
  position: absolute;
  left: 15px;
  top: 15px;
  background-color: rgba(82, 83, 83, 0.7) !important;
  border-radius: 50%;
  padding: 15px;
  i.inverted.circular.icon {
    font-size: 3em !important;
    background-color: rgba(82, 83, 83, 0.7) !important;
  }
`;
export const CardActionButton = styled.span`
  float: right;
  text-transform: uppercase;
  width: 100%;
  height: 100%;
  position: relative;
`;

interface ICard {
  background?: string;
}
export const CardContainer = styled(Card)`
  background-color: ${(props: ICard) => props.background && `${props.background} !important`};
  min-height: 150px !important;
  cursor: pointer;
  flex: 1;
  margin: 0;
  padding: 10px !important;
  position: relative;
`;

export const TaskProgress = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: 30px;
  margin-top: -1px;
`;

interface ITaskProgressPart {
  percent: number | string;
  background: string;
}
export const TaskProgressPart = styled.div`
  flex: 1 1 ${(props: ITaskProgressPart) => props.percent}%;
  max-width: ${(props: ITaskProgressPart) => props.percent}%;
  background-color: ${(props: ITaskProgressPart) => props.background};
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
`;

export const TaskProgressPartInfo = styled.span`
  color: #fff;
`;

export const TaskUpdate = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  height: 100%;
  background-color: rgba(82, 83, 83, 0.85) !important;
  color: #333;
  text-align: center;
  position: absolute;
  top: 0;
  right: 0;
  width: 0%;
  opacity: 0;
  border-radius: 0 !important;
  z-index: 6;
  transition: all ease-in 0.5s;
  ${CardContainer}:hover & {
    width: 40%;
    opacity: 1;
  }
`;

export const CardGroup = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  flex-grow: 5;
`;

export const CardMeta = styled.div`
  text-align: right;
`;

export const CardContent = styled.div`
  padding: 5px;
`;
