import { action, observable } from 'mobx';
import dashboardServices, { DashboardServices } from '../../core/services/DashboarServices';

export class HomeStore {
  @observable
  isLoading: boolean;

  @observable
  deliveryGraph: any;

  public serviceContainer: DashboardServices;

  constructor() {
    this.serviceContainer = dashboardServices;
    this.isLoading = true;
  }
  @action
  setLoading(value: boolean) {
    this.isLoading = value;
  }

  @action
  setDeliveryGraph(value: any) {
    this.deliveryGraph = value;
  }

  getDeliveryGraph() {
    this.setLoading(true);
    this.serviceContainer
      .getDeliveryGraph({})
      .then(response => {
        if (response.data) {
          this.setDeliveryGraph(response.data);
        }
      })
      .catch((error: any) => {
        console.log(error);
      })
      .finally(() => {
        this.setLoading(false);
      });
  }
}

const homeStore = new HomeStore();
export default homeStore;
