import React, { Component, ReactNode } from 'react';
import { Col, Row } from 'react-flexbox-grid';
import { VSpinner, VTable } from 'gsi-vx-ui';
import { FieldState, FormState } from 'formstate';
import { required } from '../../../../../commons/form/Validators';
import { ISortResult, IVTableProps } from 'gsi-vx-ui/lib/types/Table/Table';
import { IVWidgetTableProps } from 'gsi-vx-ui/lib/types/Table/Widget/Widget';
import ShiftDriverCheckList from './DriverCheckList/ShiftDriverCheckList';
import ShiftTruckInventory from './TruckInventory/ShiftTruckInventory';
import { DeliverySummaryDeliveries } from '../DeliverySummaryDeliveries/DeliverySummaryDeliveries';
import { disposeOnUnmount, inject, observer, Provider } from 'mobx-react';
import { ShiftDetailsStore } from '../../stores/summary/ShiftDetailsStore/ShiftDetailsStore';
import { DetailsShift } from '../../models/detailsShift';
import { SummaryStore } from '../../stores/summary/SummaryStore';
import moment from 'moment';
import { reaction } from 'mobx';
import { Icon } from '@blueprintjs/core';
import { FilterOperator } from '../../../../../../core/types/FilterType';
import shiftTruckInventoryStore, {
  ShiftTruckInventoryStore
} from '../../stores/summary/ShiftTruckInventoryStore/ShiftTruckInventoryStore';
import { ShiftDriverCheckListModel } from '../../models/ShiftDriverCheckListModel';

const iconStyle = {
  backgroundColor: '#c23030',
  color: 'white',
  borderRadius: '50px'
};

interface SummaryShiftDetailProps {
  details_store?: ShiftDetailsStore;
  summary_store?: SummaryStore;
  onSelectShift: (element: any) => void;
}

@inject('details_store', 'summary_store')
@observer
class SummaryShiftDetail extends Component<SummaryShiftDetailProps> {
  form: FormState<any>;
  truckInventoryStore: ShiftTruckInventoryStore;

  constructor(props: SummaryShiftDetailProps) {
    super(props);
    this.truckInventoryStore = shiftTruckInventoryStore;
    if (!this.props.details_store!.element.shiftId) {
      this.truckInventoryStore.setElements([]);
    }
    disposeOnUnmount(this, [
      reaction(
        () => this.props.details_store!.element,
        () => {
          const element = this.props.details_store!.element;
          this.props.onSelectShift({ element, tab: 'details' });
          if (this.truckInventoryStore.element.shiftId !== element.shiftId) {
            if (element.shiftId) {
              this.truckInventoryStore.setElement({
                shiftId: element.shiftId,
                driverFullName: element.driver,
                truckName: element.truck,
                driverId: element.driverId
              });
              this.truckInventoryStore.getElements(`${element.shiftId}`);
            } else {
              this.truckInventoryStore.setElements([]);
            }
          }
          this.props.details_store!.getDriverCheckListById(element.shiftId);
        }
      )
    ]);
    this.form = new FormState<any>({
      driver: new FieldState('').validators(required),
      shift: new FieldState('').validators(required),
      truck: new FieldState('').validators(required),
      truckDetails: new FieldState('').validators(required)
    });
    const operator: FilterOperator = 'EQUALITY';
    if (this.props.details_store!.elements.length === 0) {
      if (this.props.summary_store!.shift.driverId) {
        this.props.details_store!.addFilter({
          operator,
          field: 'date',
          value: moment(this.props.summary_store!.date).format('YYYY-MM-DD')
        });
        this.props.details_store!.addFilter({
          operator,
          field: 'driverId',
          value: this.props.summary_store!.shift.driverId
        });
        this.props.details_store!.getElements();
      }
    } else {
      this.props.onSelectShift({ element: this.props.details_store!.elements[0], tab: 'details' });
      this.props.details_store!.setElement(this.props.details_store!.elements[0]);
    }
  }

  onSelect = (data: any) => {
    const { details_store } = this.props;
    const element = details_store!.elements.find(element => element.shiftId === data.shiftId) || data;
    if (details_store!.elementDriverCheckList.shiftId !== element.shiftId) {
      details_store!.setElementDriverCheckList(
        new ShiftDriverCheckListModel(element.shiftId, '', '', '', element.driver, element.truck)
      );
    }
    details_store!.setElement(element);
  };

  render() {
    const data = this.transformRoutesList();
    return (
      <React.Fragment>
        <Row>
          {/*<HeaderShiftDetail form={this.form} />*/}
          <Col xs={12} md={4} lg={4}>
            <ShiftDriverCheckList />
          </Col>
          <Col xs={12} md={8}>
            <Row>
              <Col xs={12} style={{ height: '192px' }}>
                {this.props.details_store!.isLoading ? <VSpinner /> : <VTable {...data} />}
              </Col>
              <Col xs={12} style={{ marginTop: '15px' }}>
                <Provider
                  {...{
                    truck_inventory_store: this.truckInventoryStore
                  }}
                >
                  <ShiftTruckInventory />
                </Provider>
              </Col>
            </Row>
          </Col>
        </Row>
        <Row style={{ marginTop: '15px' }}>
          <Col xs>
            <DeliverySummaryDeliveries />
          </Col>
        </Row>
      </React.Fragment>
    );
  }

  transformRoutesList(): IVTableProps {
    const status: IVWidgetTableProps = {
      column: 'trxnStatus',
      widget: {
        type: 'CUSTOMERCOMPONENT',
        cusmtomerCell: {
          renderCustomer: (value: any): ReactNode => {
            let icon = <Icon icon={'tick-circle'} iconSize={15} intent={'success'} />;
            switch (value) {
              case 0:
                icon = <Icon style={iconStyle} icon={'cross'} iconSize={15} intent={'danger'} />;
                break;
              case 1:
                icon = <Icon icon={'warning-sign'} iconSize={15} intent={'warning'} />;
                break;
            }
            return icon;
          }
        }
      }
    };
    const log: IVWidgetTableProps = {
      column: 'shiftResult',
      widget: {
        type: 'CUSTOMERCOMPONENT',
        cusmtomerCell: {
          renderCustomer: (value: any): ReactNode => {
            let icon = <Icon icon={'log-out'} iconSize={15} intent={'none'} />;
            switch (value) {
              case true:
                icon = <Icon icon={'log-in'} iconSize={15} intent={'primary'} />;
                break;
              case false:
                icon = <Icon icon={'log-out'} iconSize={15} intent={'none'} />;
                break;
            }
            return icon;
          }
        }
      }
    };

    const widgetsCell: IVWidgetTableProps[] = [status, log];

    const data = this.props.details_store!.elements.map((item: DetailsShift) => {
      return {
        shiftId: item.shiftId,
        trxnStatus: item.trxnStatus,
        shiftResult: item.shiftResult,
        truck: item.truck,
        driver: item.driver,
        shiftDate: item.shiftDate,
        startHours: item.startHours,
        shiftMiles: item.shiftMiles,
        shiftTotalizer: item.shiftTotalizer,
        shiftTotalizer2: item.shiftTotalizer2,
        totalStops: item.totalStops,
        stopsCompleted: item.stopsCompleted,
        stopsRptd: item.stopsRptd,
        stopsRecd: item.stopsRecd
      };
    });
    return {
      widgetsCell,
      data,
      reordering: true,
      striped: true,
      onOrderColumns: this.onOrdering,
      cellSelectionType: 'ENTIRE_ROW',
      actionsSelection: { onSelectionChange: this.onSelect },
      tableHeight: '192px',
      columns: this.props.details_store!.columns,
      columns_name: {
        shiftId: 'Shift #',
        trxnStatus: 'TRXN Status',
        shiftResult: 'Shift Result',
        truck: 'Truck',
        driver: 'Driver',
        shiftDate: 'Shift Date',
        startHours: 'Start Hours',
        shiftMiles: 'Shift Miles',
        shiftTotalizer: 'Shift Totalizer',
        shiftTotalizer2: 'Shift Totalizer2',
        totalStops: 'Total Stops',
        stopsCompleted: 'Stops Compl',
        stopsRptd: 'Stops Rptd',
        stopsRecd: 'Stops Recd'
      },
      sortable: {
        setupsOrden: this.props.details_store!.sortOrderForTable,
        columns: this.props.details_store!.columns,
        onSort: this.onSort
      },
      typeHeightRow: 'HALF'
    };
  }

  onOrdering = (columns: string[]) => {
    this.props.details_store!.setColumns(columns);
  };

  onSort = (sort: ISortResult) => {
    if (sort.order === 'NONE') {
      this.props.details_store!.removeOrder({
        property: this.props.details_store!.columns[sort.columnIndex],
        direction: sort.order
      });
    } else {
      this.props.details_store!.addOrder({
        property: this.props.details_store!.columns[sort.columnIndex],
        direction: sort.order
      });
    }
    this.props.details_store!.getElements();
  };
}

export default SummaryShiftDetail;
