import styled from 'styled-components';
import backLg from '../../../../../assets/images/back_lg.png';
import { Grid } from 'react-flexbox-grid';

export const LoginFormContainer = styled.div`
  width: 100%;
  height: 100%;
  background-size: auto;
  background: #213e81 url(${backLg}) center no-repeat;
  align-items: center;
  justify-content: center;
`;

interface ITitleHeader {
  textColor: string;
}

export const TitleHeader = styled('h3')`
  color: ${(props: ITitleHeader) => props.textColor && props.textColor};
  margin-top: 5px;
`;

export const GridLogin = styled(Grid)`
  height: 100%;
  justify-content: center;
  display: flex;
  align-items: center;
`;

export const LogoContainer = styled('div')`
  position: absolute;
  top: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  @media (max-width: 375px) {
    flex-direction: column;
  }
`;

interface IImgLogoContainer {
  textAlign: string;
}

export const ImgLogoContainer = styled('div')`
  padding: 20px;
  flex: 1;
  text-align: ${(props: IImgLogoContainer) => props.textAlign && props.textAlign};
`;
