import React from 'react';
import { VCardPanel, VSelectField } from 'gsi-vx-ui';
import { FieldState, FormState } from 'formstate';
import { Drawer, H6 } from '@blueprintjs/core';
import styled from 'styled-components';
import { Col, Row } from 'react-flexbox-grid';
import { AppThemeContext, ITheme } from '../../../../../../App';

const ThemeConsumer = AppThemeContext.Consumer;

const FormCol = styled(Col)`
  display: flex;
  flex-direction: row;
  justify-content: center;
`;
interface IEmailProps {
  isOpen: boolean;
  handleClose(): void;
}
function EmailComponent(props: IEmailProps) {
  const form = new FormState({
    from: new FieldState(''),
    to: new FieldState(''),
    date: new FieldState('')
  });

  return (
    <ThemeConsumer>
      {(themeContext: ITheme) =>
        themeContext && (
          <VCardPanel
            headerText={'Sent Message'}
            bodyPadding={'5px'}
            headerBackgroundColor={themeContext.headers}
            headerColor={'#FFFFFF'}
            width={'700px'}
            collapse
            closeIcon={'minus'}
            openIcon={'plus'}
          >
            <Row>
              <Col lg={4}>
                <VSelectField
                  id={'idemailto'}
                  fieldState={form.$.to}
                  placeholder={'To'}
                  layer={{ inputOrientation: 'end' }}
                  fill
                  options={[{ value: '1', label: 'User 1' }, { value: '2', label: 'User 2' }]}
                />
              </Col>
              <FormCol lg={8}>
                <H6>From:</H6>
                <H6>Date:</H6>
              </FormCol>
            </Row>
          </VCardPanel>
        )
      }
    </ThemeConsumer>
  );
}

export default EmailComponent;
