import React, { Component } from 'react';
import { Button, Grid, GridColumn, Icon, Segment } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import { StationLogsTicketsStore } from '../../../stores/StationLogsTicketsStore';

interface IProp {
  onSave: () => void;
  onCreate: () => void;
  onEdit: () => void;
  onHTML: () => void;
}

const buttonStyle = {
  margin: 0
};

interface IProp {
  station_logs_tickets?: StationLogsTicketsStore;
}

@inject('station_logs_tickets')
@observer
export class StationLogsTicketsFormToolbar extends Component<IProp> {
  constructor(props: IProp) {
    super(props);
  }
  render() {
    const { onSave, onCreate, onEdit, onHTML, station_logs_tickets } = this.props;
    return (
      <Segment size="mini" stacked raised>
        <Grid textAlign="center" centered columns="4">
          <GridColumn textAlign="center">
            <Button onClick={onCreate} size="mini" style={buttonStyle}>
              <Icon name="plus" color="yellow" />
              New
            </Button>
          </GridColumn>
          <GridColumn textAlign="center">
            <Button onClick={onEdit} size="mini" style={buttonStyle}>
              <Icon name="edit outline" color="blue" />
              Edit
            </Button>
          </GridColumn>
          <GridColumn textAlign="center">
            <Button onClick={onSave} disabled={!station_logs_tickets!.validForm} size="mini" style={buttonStyle}>
              <Icon name="save" color="green" />
              Save
            </Button>
          </GridColumn>
          <GridColumn textAlign="center">
            <Button onClick={onHTML} size="mini" style={buttonStyle}>
              <Icon name="file pdf" color="red" />
              Html
            </Button>
          </GridColumn>
        </Grid>
      </Segment>
    );
  }
}
