import { action, observable } from 'mobx';

export class TrackingStore {
  @observable isLoading: boolean;
  @observable driverSelected: string;
  @observable trackings: any[];
  @observable drivers: any[];

  constructor() {
    this.isLoading = false;
    this.trackings = [];
    this.drivers = [];
    this.driverSelected = 'Driver';
    this.loadTrackings();
    this.loadDrivers();
  }

  @action
  setTrackings(trackings: any[]) {
    this.trackings = trackings;
  }

  @action
  setLoading(value: boolean) {
    this.isLoading = value;
  }

  @action
  getTrackings(): any[] {
    return this.trackings;
  }

  @action
  loadTrackings() {
    const testTracking = [
      {
        id: 1,
        time: '5:20 pm',
        status: '',
        duration: '',
        speed: '45',
        location: '16850 County Rd 30, Maple Grove, MN 55311, USA; Maple Grove, Minnesota 55311'
      },
      {
        id: 2,
        time: '5:15 pm',
        status: '',
        duration: '',
        speed: '38',
        location: '16850 County Rd 30, Maple Grove, MN 55311, USA; Maple Grove, Minnesota 55311'
      },
      {
        id: 3,
        time: '5:05 pm',
        status: '',
        duration: '',
        speed: '15',
        location: '16750 County Rd 30, Maple Grove, MN 55311, USA; Maple Grove, Minnesota 55311'
      },
      {
        id: 4,
        time: '4:55 pm',
        status: '',
        duration: '',
        speed: '4',
        location: '16850 County Rd 30, Maple Grove, MN 55311, USA; Maple Grove, Minnesota 55311'
      },
      {
        id: 5,
        time: '5:20 pm',
        status: '',
        duration: '',
        speed: '46',
        location: '16850 County Rd 30, Maple Grove, MN 55311, USA; Maple Grove, Minnesota 55311'
      }
    ];

    this.setTrackings(testTracking);
  }

  @action
  setDrivers(list: any[]) {
    this.drivers = list;
  }
  @action
  loadDrivers() {
    const mockDrivers = [
      {
        driver: 'BOB A',
        id: 1,
        truck: 'TRUCK 1'
      },
      {
        driver: 'JEFF B',
        id: 2,
        truck: 'TRUCK 2'
      },
      {
        driver: 'JOE C',
        id: 3,
        truck: 'TRUCK 2'
      },
      {
        driver: 'RICK D',
        id: 4,
        truck: 'TRUCK 4'
      },
      {
        driver: 'TOM E',
        id: 5,
        truck: 'TRUCK 5'
      }
    ];
    this.setDrivers(mockDrivers);
  }

  @action
  selectDriver(driver: string) {
    this.driverSelected = driver;
  }
}

const trackingStore = new TrackingStore();
export default trackingStore;
