import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { RiftRouter } from 'rift-router';
import { Alignment, Button, Icon, Menu, MenuDivider, MenuItem, Navbar, Popover, Position } from '@blueprintjs/core';
import { InjectedIntlProps, injectIntl } from 'react-intl';

import { AppStore } from '../../stores/AppStore';
import logo from '../../../assets/images/common/logo.png';
import { MenuLogo, ThemeCircle } from './styles';
import { themes } from '../../../App';
import flag_es from '../../../assets/images/flags/es.svg'
import flag_en from '../../../assets/images/flags/en.svg'
import { VSelectField } from 'gsi-vx-ui';
import { ThemeTypes } from '../../types/ThemeTypes';


interface IProps extends InjectedIntlProps {
  app?: AppStore;
  router?: RiftRouter;
}

@inject('app', 'router')
@observer
class MainNavbar extends Component<IProps> {
  state = { activeItem: 'home' };

  render() {
    const flag = {
      marginTop: 3
    };
    const { intl } = this.props;
    const userMenu = (
      <Menu>
        <MenuItem icon="cog" text={`${intl.formatMessage({ id: 'layout.navbar.config' })}`} />
        <MenuItem icon="people" text={`${intl.formatMessage({ id: 'layout.navbar.account' })}`} />
        <MenuItem
          icon="contrast"
          text="Themes"
          popoverProps={{
            hoverCloseDelay: 400,
            captureDismiss: false
          }}
        >
          <MenuItem
            icon={<ThemeCircle themeProps={themes.light} />}
            text="Standard"
            active={this.props.app!.theme === themes['light']}
            onClick={(e: any) => this.changeTheme('light')}
          />
          <MenuItem
            icon={<ThemeCircle themeProps={themes.alternative} />}
            text="Alternative"
            active={this.props.app!.theme === themes['alternative']}
            onClick={(e: any) => this.changeTheme('alternative')}
          />
        </MenuItem>
        <MenuItem icon="translate" text={intl.formatMessage({ id: 'settings.lang' })}>
          <MenuItem
            icon={<img style={flag} width={20} src={flag_en}/>}
            text={intl.formatMessage({ id: 'app.en' })}
            active={this.props.app!.lang === 'en'}
            onClick={(e: any) => this.changeLang('en')}
          />
          <MenuItem
            icon={<img style={flag} width={20} src={flag_es}/>}
            text={intl.formatMessage({ id: 'app.es' })}
            active={this.props.app!.lang === 'es'}
            onClick={(e: any) => this.changeLang('es')}
          />
        </MenuItem>
        <MenuDivider />
        <MenuItem icon="log-out" text={`${intl.formatMessage({ id: 'layout.navbar.logout' })}`} onClick={this.logout} />
      </Menu>
    );

    return (
      <Navbar>
        <Navbar.Group align={Alignment.LEFT}>
          <Navbar.Heading onClick={(e: any) => this.collapseMenu(e)}>
            <Icon icon="menu" />
          </Navbar.Heading>
          <Navbar.Heading>
            <MenuLogo onClick={(e: any) => this.linkTo('/')}>
              <img src={logo} style={{ height: 40 }} />
            </MenuLogo>
          </Navbar.Heading>
          <Navbar.Divider />
        </Navbar.Group>
        <Navbar.Group align={Alignment.RIGHT}>
          <VSelectField
            label={''}
            icon={'form'}
            id={'brand'}
            inline
            defaultText={'Brand'}
            margin={'0'}
            minimal
            value={this.props.app!.brand}
            fill
            onChange={(value: string) => this.props.app!.setBrand(value)}
            options={[{ value: 1, label: 'SHELBYVILLE, MI' }, { value: 2, label: 'Chicago, Illinois' }]}
          />
          <Button
            icon={<Icon icon="notifications-updated" iconSize={20} />}
            minimal
            large
            onClick={(e: any) => this.showProcess(e)}
          />

          <Popover content={userMenu} position={Position.BOTTOM}>
            <Button icon={<Icon icon="user" iconSize={20} />} minimal large />
          </Popover>
        </Navbar.Group>
      </Navbar>
    );
  }

  collapseMenu = (e: any) => {
    this.props.app!.setStatusCollapseMenu();
  };

  showProcess = (e: any) => {
    this.props.app!.setIsOpenProcess(true);
  };

  linkTo = (path: string) => {
    this.props.router!.riftTo(path);
  };

  changeTheme = (theme: ThemeTypes) => {
    if (this.props.app) {
      this.props.app.setTheme(themes[theme]);
    }
  };

  changeLang = (lang: string) => {
    this.props.app!.setLang(lang);
  };

  logout = () => {
    this.props.app!.logout();
    this.linkTo('/login');
  };
}

export default injectIntl(MainNavbar);
