import React, { Component } from 'react';
import { SummaryLayout } from '../../components/SummaryLayout/SummaryLayout';
import { Provider } from 'mobx-react';
import summaryStore, { SummaryStore } from '../../stores/summary/SummaryStore';
import summaryRouteStore, { SummaryRouteStore } from '../../stores/summary/routeStore/SummaryRouteStore';
import summaryDeliveriesStore, {
  SummaryDeliveriesStore
} from '../../stores/summary/DeliveriesStore/SummaryDeliveriesStore';
import shiftDetailsStore, { ShiftDetailsStore } from '../../stores/summary/ShiftDetailsStore/ShiftDetailsStore';

export class SummaryContainer extends Component<any> {
  summaryStore: SummaryStore;
  routeStore: SummaryRouteStore;
  deliveriesStore: SummaryDeliveriesStore;
  detailsStore: ShiftDetailsStore;

  constructor(props: any) {
    super(props);
    this.summaryStore = summaryStore;
    this.routeStore = summaryRouteStore;
    this.deliveriesStore = summaryDeliveriesStore;
    this.detailsStore = shiftDetailsStore;
  }

  render() {
    return (
      <Provider
        {...{
          summary_store: this.summaryStore,
          route_store: this.routeStore,
          deliveries_store: this.deliveriesStore,
          details_store: this.detailsStore
        }}
      >
        <SummaryLayout />
      </Provider>
    );
  }
}
