import { findIndex, remove } from 'lodash';
import { action, observable, reaction } from 'mobx';
import { ParentStore } from './ParentStore';
import { IOrderType } from '../types/OrderType';
import { IFilterType } from '../types/FilterType';

export default class ParentPaginateStore extends ParentStore {
  @observable
  public page: number;
  @observable
  public search: string;

  @observable
  public size: number;

  @observable
  public orders: IOrderType[];

  @observable
  public filters: IFilterType[];

  constructor(service: any) {
    super(service);
    this.page = 1;
    this.size = 10;
    this.search = '';
    this.filters = [];
    this.orders = [];
    reaction(
      () => ({ page: this.page, size: this.size, orders: this.orders, filters: this.filters, search: this.search }),
      () => {
        this.getElements();
      }
    );
  }

  @action
  public setPage(value: number) {
    this.page = value;
  }

  @action
  public setSize(value: number) {
    this.size = value;
  }
  @action
  public setSearch(value: string) {
    this.search = value;
  }

  @action
  public setOrders(value: IOrderType[]) {
    this.orders = value;
  }

  @action
  public setFilters(value: IFilterType[]) {
    this.filters = value;
  }

  @action
  public addFilter(value: IFilterType) {
    const index = findIndex(this.filters, (filter: IFilterType) => {
      return filter.field === value.field;
    });
    if (index !== -1) {
      this.filters[index] = value;
    } else {
      this.filters.push(value);
    }
  }

  @action
  public removeFilter(value: IFilterType) {
    remove(this.filters, (filter: IFilterType) => {
      return filter.field === value.field;
    });
  }

  @action
  public addOrder(value: IOrderType) {
    const index = findIndex(this.orders, (order: IOrderType) => {
      return order.property === value.property;
    });
    if (index !== -1) {
      this.orders[index] = value;
    } else {
      this.orders.push(value);
    }
  }

  @action
  public removeOrder(value: IOrderType) {
    remove(this.orders, (order: IOrderType) => {
      return order.property === value.property;
    });
  }

  public setPagingOption(data: any) {
    this.setElements(data.content);
    this.setCount(data.page.totalElements);
  }

  public getElements(url: string = '', others?: any) {
    this.setLoading(true);
    this.serviceContainer
      .getPaginateAll(this.page, this.size, this.orders, this.filters, this.search, url, others)
      .then((response: any) => {
        const data = response.data;
        this.setPagingOption(data);
        this.setLoading(false);
      })
      .catch((err: any) => {
        this.setLoading(false);
        throw err;
      });
  }
}
