import React from 'react';
import ParentPaginateStore from '../../../../../../../core/stores/ParentPaginateStore';
import deliveryRouteService from '../../../services/RouteService/DeliveryRouteService';
import { action, computed, observable } from 'mobx';
import { SummaryShift } from '../../../models/summaryShift';

export class SummaryRouteStore extends ParentPaginateStore {
  @observable
  public element: SummaryShift;
  @observable
  public elements: SummaryShift[];
  @observable
  public columns: string[];

  constructor() {
    super(deliveryRouteService);
    this.element = new SummaryShift();
    this.elements = [];
    this.columns = [
      'status',
      'truckName',
      'driverFullName',
      'shiftId',
      'startTime',
      'stopsPlanned',
      'stopsAdded',
      'totalStops',
      'stopsCompleted',
      'nda',
      'ndu',
      'totalStopsCompleted',
      'percentCompleted',
      'qtyDelivered',
      'totalMiles',
      'endTime'
    ];
  }

  @action
  setElement(element: SummaryShift) {
    this.element = element;
  }

  @action
  setColumns(columns: string[]) {
    this.columns = columns;
  }

  @action
  setElements(elements: SummaryShift[]) {
    this.elements = elements;
  }

  @computed
  get sortOrderForTable(): { order: 'ASC' | 'DESC' | 'NONE'; columnName: string; columnIndex: number }[] {
    return this.orders.map(item => {
      return { order: item.direction, columnName: item.property, columnIndex: this.columns.indexOf(item.property) };
    });
  }

  public setPagingOption(data: any) {
    const elements: SummaryShift[] = data.content;
    if (data.content.length > 0) {
      this.setElement(elements[0]);
    } else {
      this.setElement(new SummaryShift());
    }
    this.setElements(elements);
    this.setCount(data.totalElements);
  }
}

const summaryRouteStore = new SummaryRouteStore();
export default summaryRouteStore;
