import React from 'react';
import TrackingComponent from '../../components/tracking/TrackingComponent';
import trackingStore, { TrackingStore } from '../../stores/tracking/TrackingStore';
import { observer } from 'mobx-react';

@observer
class TrackingContainer extends React.Component {
  tracking_store: TrackingStore;
  constructor(props: any) {
    super(props);
    this.tracking_store = trackingStore;
  }
  handleOnDriver = (driver: string) => {
    this.tracking_store.selectDriver(driver);
  };

  render() {
    return (
      <React.Fragment>
        <TrackingComponent
          trackings={this.tracking_store.trackings}
          drivers={this.tracking_store.drivers}
          driver={this.tracking_store.driverSelected}
          setDriver={this.handleOnDriver}
        />{' '}
      </React.Fragment>
    );
  }
}

export default TrackingContainer;
