import React, { Component } from 'react';
import { Form, Grid } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';
import InputField from '../../../../../../commons/components/form/InputField';
import MultiInputField from '../../../../../../commons/components/form/MultiInputField';
import { StationLogsTicketsStore } from '../../../stores/StationLogsTicketsStore';

interface IProp {
  station_logs_tickets?: StationLogsTicketsStore;
}

@inject('station_logs_tickets')
@observer
export class StationLogsTicketsForm extends Component<IProp> {
  constructor(props: IProp) {
    super(props);
  }
  render() {
    const { station_logs_tickets } = this.props;
    return (
      <Grid centered textAlign="left" padded>
        <Form style={{ width: '100%' }} size="mini">
          <InputField
            label="Station Type"
            required
            inline
            labelPosition="left"
            type="text"
            fieldState={station_logs_tickets!.form.$.stationType}
          />
          <InputField
            label="Station Logs Date"
            required
            inline
            labelPosition="left"
            type="date"
            fieldState={station_logs_tickets!.form.$.stationLogDate}
          />
          <InputField
            label="PickUp Date/Time"
            required
            inline
            labelPosition="left"
            type="date"
            fieldState={station_logs_tickets!.form.$.stationDatetime}
          />
          <InputField
            label="Pickup Name"
            required
            inline
            labelPosition="left"
            type="text"
            fieldState={station_logs_tickets!.form.$.pickupName}
          />
          <InputField
            label="Ticket #"
            required
            inline
            labelPosition="left"
            type="text"
            fieldState={station_logs_tickets!.form.$.ticket}
          />
          <InputField
            label="Drop Off Name"
            required
            inline
            labelPosition="left"
            type="text"
            fieldState={station_logs_tickets!.form.$.dropOffName}
          />
          <InputField
            label="Drop Off #"
            inline
            required
            labelPosition="left"
            type="text"
            fieldState={station_logs_tickets!.form.$.dropOff}
          />
          <InputField
            label="Driver"
            inline
            required
            labelPosition="left"
            type="text"
            fieldState={station_logs_tickets!.form.$.driver}
          />
          <InputField
            label="Trucking Company Name"
            required
            inline
            labelPosition="left"
            type="text"
            fieldState={station_logs_tickets!.form.$.truckedBy}
          />
          <InputField inline label="ER" labelPosition="left" type="text" fieldState={station_logs_tickets!.form.$.er} />
          <MultiInputField
            inline
            labels={['ft', 'in', '1/4 in']}
            label="Top Gauge"
            labelPosition="left"
            type="text"
            fieldState={station_logs_tickets!.form.$.topGauge}
          />
        </Form>
      </Grid>
    );
  }
}
