import React from 'react';
import deliverySummaryServices from '../../../services/DeliveriesService/DeliverySummaryService';
import ParentPaginateStore from '../../../../../../../core/stores/ParentPaginateStore';
import { action, computed, observable } from 'mobx';
import { SummaryShiftDeliveries } from '../../../models/summaryShiftDeliveries';
import { ShiftModel } from '../../../models/shiftModel';

export class SummaryDeliveriesStore extends ParentPaginateStore {
  @observable
  element: ShiftModel;
  @observable
  elements: SummaryShiftDeliveries[];
  @observable
  public columns: string[];

  constructor() {
    super(deliverySummaryServices);
    this.element = new ShiftModel();
    this.elements = [];
    this.columns = ['rt_sq', 'urgency', 'img', 'ticket', 'accountNumber', 'name', 'eta', 'atd', 'qty'];
  }

  @action
  setElement(element: ShiftModel) {
    this.element = element;
  }

  @action
  setElements(elements: SummaryShiftDeliveries[]) {
    this.elements = elements;
  }

  @action
  setColumns(columns: string[]) {
    this.columns = columns;
  }

  @computed
  get sortOrderForTable(): { order: 'ASC' | 'DESC' | 'NONE'; columnName: string; columnIndex: number }[] {
    return this.orders.map(item => {
      return { order: item.direction, columnName: item.property, columnIndex: this.columns.indexOf(item.property) };
    });
  }

  public setPagingOption(data: any) {
    const elements: SummaryShiftDeliveries[] = data.content;
    if (data.content.length) {
      this.setElements(elements);
      this.setCount(data.page.totalElements);
    } else {
      this.setElements([]);
      this.setCount(0);
    }
  }

  public getElements(url: string = '', others?: any) {
    let urlShift = url;
    if (url === '') {
      urlShift = `?shiftId=${this.element.shiftId}`;
    }
    this.setLoading(true);
    this.serviceContainer
      .getPaginateAll(this.page, this.size, this.orders, this.filters, this.search, urlShift, others)
      .then((response: any) => {
        const data = response.data;
        this.setPagingOption(data);
        this.setLoading(false);
      })
      .catch((err: any) => {
        this.setLoading(false);
        throw err;
      });
  }
}

const summaryDeliveriesStore = new SummaryDeliveriesStore();
export default summaryDeliveriesStore;
