import React from 'react';
import ReactDOM from 'react-dom';
import './index.scss';
import App from './App';
import * as serviceWorker from './serviceWorker';
import store from './core/stores/RootStore';
import { Provider } from 'mobx-react';

declare global {
  interface Window {
    connectFlex: any;
    onMessage: any;
    onLoad: any;
  }
}

const onMessage = (message: any) => {
  if (message && message.data && message.data.type) {
    switch (message.data.type) {
      case 'token':
        localStorage.setItem('userdata', JSON.stringify({ token: message.data.data }));
        store.router.riftTo('/dashboard');
        break;
    }
  }
};

const postMessage = (message: any) => {
  window.parent.postMessage(message.data, '*');
};

if (window.addEventListener) {
  window.addEventListener('message', onMessage, false);
}

/* End Internationalization*/
ReactDOM.render(
  <Provider {...store}>
    <App />
  </Provider>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
