import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { DetailsList } from '../details_list/DetailsList';
import { DispatchSummaryDetails } from '../summary_details/DispatchSummaryDetails';
import { ButtonStyle } from './styles';
import { VTable, VTabsPanel } from 'gsi-vx-ui';
import { IVTableProps } from 'gsi-vx-ui/lib/types/Table/Table';
import { Row } from 'react-flexbox-grid';

@inject('dispatch_store')
@observer
export class DispatchDrivers extends Component {
  private onSelect = (data: any) => {
    console.log(data);
  };
  private onSort = (data: any) => {
    console.log(data);
  };

  render() {
    const listDetails = [
      {
        key: 'startd',
        header: 'Start Depot',
        description: 'Cambridge'
      },
      {
        key: 'endd',
        header: 'End Depot',
        description: 'Cambridge'
      },
      {
        key: 'startt',
        header: 'Start Time',
        description: '8:00 AM'
      },
      {
        key: 'endt',
        header: 'End Time',
        description: '5:00 PM'
      },
      {
        key: 'totaltime',
        header: 'Total Time',
        description: '5 hrs, 34 mins.'
      },
      {
        key: 'totalmiles',
        header: 'Total Miles',
        description: '75'
      },
      {
        key: 'totalstop',
        header: 'Total Stops',
        description: '15'
      }
    ];
    const panes = [
      {
        label: 'Routes',
        content: <VTable {...this.transformDriversList()} />,
        key: '1'
      },
      {
        label: 'Details',
        content: (
          <React.Fragment>
            <DetailsList items={listDetails} />
            <Row center={'xs'} middle={'xs'}>
              <ButtonStyle alignText={'center'} icon={'path-search'} text={'Truck Inventory'} />
            </Row>
            <DispatchSummaryDetails percent={102} />
          </React.Fragment>
        ),
        key: '2'
      },
      {
        label: 'Routes Orders',
        content: '',
        key: '3'
      }
    ];
    return (
      <div style={{ width: '100%' }}>
        <VTabsPanel
          size={'small'}
          padding={'5px 0'}
          tabList={panes}
          activeColor={'#e8e8e8'}
          active={'1'}
          handleChange={this.handelChangeTab}
        />
      </div>
    );
  }

  handelChangeTab = () => {};

  transformDriversList = (): IVTableProps => {
    // todo do some tranform to get de list.
    /*Get the result of the store and then here transform the data to the list*/

    return {
      columns: ['driver', 'truck', 'rte'],
      columns_name: { driver: 'Driver', truck: 'Truck', rte: 'Rte' },
      data: [
        {
          driver: 'Bob A',
          truck: 'Truck 1',
          rte: '1'
        },
        {
          driver: 'Jeff B',
          truck: 'Truck 2',
          rte: '1'
        },
        {
          driver: 'Joe C',
          truck: 'Truck 3',
          rte: 'Rte'
        },
        {
          driver: 'Rick D',
          truck: 'Truck 4',
          rte: 'Rte'
        },
        {
          driver: 'Tom E',
          truck: 'Truck 5',
          rte: 'Rte'
        }
      ]
    };
  };
}
