import React from 'react';
import { action, observable } from 'mobx';
import { DriverSummaryModel } from '../../../models/DriverModel';
import { ShiftModel } from '../../models/shiftModel';

export class SummaryStore {
  @observable isLoading: boolean;
  @observable activeDriver: DriverSummaryModel;
  @observable deliveries: any[];
  @observable routes: any[];
  @observable date: Date;
  @observable shiftNumber: any;
  @observable shift: ShiftModel;

  constructor() {
    this.isLoading = false;
    this.deliveries = [];
    this.routes = [];
    this.date = new Date();
    this.activeDriver = new DriverSummaryModel(1, '', '');
    this.shiftNumber = '';
    this.shift = new ShiftModel();
    this.loadRoutes();
    this.loadDeliveries();
  }

  @action
  setLoading(value: boolean) {
    this.isLoading = value;
  }

  @action
  setShift(shift: ShiftModel) {
    this.shift = shift;
  }

  @action
  setActiveDriver(driver: any) {
    this.activeDriver = new DriverSummaryModel(driver.id, driver.fullName, driver.truck);
  }

  @action
  setShiftNumber(shiftNumber: any) {
    this.shiftNumber = shiftNumber;
  }

  @action
  setRoutes(routes: any) {
    this.routes = routes;
  }
  @action
  setDate(date: any) {
    this.date = date;
  }

  loadRoutes() {
    // todo get data Routes
    // this.setRoutes(this.transformRoutesList('asd'));
    this.setActiveDriver({ id: 1, fullName: 'Jef B', truck: 'Truck 2' });
  }

  @action
  setDeliveries(list: any[]) {
    this.deliveries = list;
  }

  loadDeliveries() {
    const mockDrivers = [{}];
    this.setDeliveries(mockDrivers);
  }
}

const summaryStore = new SummaryStore();
export default summaryStore;
