import React from 'react';
import { Bar } from 'react-chartjs-2';

const data = {
  labels: [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December'
  ],
  datasets: [
    {
      data: [50, 30, 60, 70, 80, 90, 95, 70, 90, 20, 60, 95],
      type: 'line',
      label: 'Deliveries',
      fill: false,
      backgroundColor: '#fff',
      borderColor: '#767676',
      borderCapStyle: 'butt',
      borderDash: [],
      borderDashOffset: 0.0,
      borderJoinStyle: 'miter',
      lineTension: 0.3,
      pointBackgroundColor: '#fff',
      pointBorderColor: '#767676',
      pointBorderWidth: 1,
      pointHoverRadius: 5,
      pointHoverBackgroundColor: '#767676',
      pointHoverBorderColor: '#767676',
      pointHoverBorderWidth: 2,
      pointRadius: 4,
      pointHitRadius: 10
    },
    {
      label: 'Un-Attempted',
      backgroundColor: '#FBBD08',
      yAxisID: 'bar-y-axis',
      data: [5, 2, 2, 4, 10, 5, 12, 2, 5, 2, 5, 4]
    },
    {
      label: 'TankFull',
      backgroundColor: '#2185D0',
      yAxisID: 'bar-y-axis',
      data: [5, 4, 2, 2, 8, 5, 5, 2, 1, 2, 3, 0]
    },
    {
      label: 'Delivery Complete',
      backgroundColor: '#00B5AD',
      yAxisID: 'bar-y-axis',
      data: [20, 21, 24, 25, 26, 17, 28, 19, 20, 11, 22, 33]
    },
    {
      label: 'Attempted',
      backgroundColor: '#DB2828',
      yAxisID: 'bar-y-axis',
      data: [0, 5, 4, 3, 6, 5, 3, 3, 2, 3, 5, 1]
    }
  ]
};

const options = {
  title: {
    display: false
  },
  tooltips: {
    mode: 'label'
  },
  responsive: true,
  scales: {
    xAxes: [
      {
        stacked: true
      }
    ],
    yAxes: [
      {
        stacked: false,
        ticks: {
          beginAtZero: true,
          min: 0,
          max: 100
        }
      },
      {
        id: 'bar-y-axis',
        stacked: true,
        display: false, // optional
        ticks: {
          beginAtZero: true,
          min: 0,
          max: 100
        },
        type: 'linear'
      }
    ]
  }
};

const DeliveryStatusGraph = () => {
  return <Bar data={data} options={options} />;
};

export default DeliveryStatusGraph;
