export class SummaryShift {
  status: string;
  urgency: number;
  truckId: number;
  truckName: string;
  driverFullName: string;
  driverId: number;
  shiftId: number;
  startTime: string;
  stopsPlanned: number;
  stopsAdded: number;
  totalStops: number;
  stopsCompleted: number;
  qtyDelivered: number;
  nda: number;
  ndu: number;
  totalStopsCompleted: number;
  percentCompleted: number;
  totalMiles: number;
  endTime: string;

  constructor(
    status: string = '',
    urgency: number = 0,
    truckId: number = 0,
    truckName: string = '',
    driverFullName: string = '',
    driverId: number = 0,
    shiftId: number = 0,
    startTime: string = '',
    stopsPlanned: number = 0,
    stopsAdded: number = 0,
    totalStops: number = 0,
    stopsCompleted: number = 0,
    qtyDelivered: number = 0,
    nda: number = 0,
    ndu: number = 0,
    totalStopsCompleted: number = 0,
    percentCompleted: number = 0,
    totalMiles: number = 0,
    endTime: string = ''
  ) {
    this.status = status;
    this.urgency = urgency;
    this.truckId = truckId;
    this.truckName = truckName;
    this.driverFullName = driverFullName;
    this.driverId = driverId;
    this.shiftId = shiftId;
    this.startTime = startTime;
    this.stopsPlanned = stopsPlanned;
    this.stopsAdded = stopsAdded;
    this.totalStops = totalStops;
    this.stopsCompleted = stopsCompleted;
    this.qtyDelivered = qtyDelivered;
    this.nda = nda;
    this.ndu = ndu;
    this.totalStopsCompleted = totalStopsCompleted;
    this.percentCompleted = percentCompleted;
    this.totalMiles = totalMiles;
    this.endTime = endTime;
  }
}
