import { AxiosInstance, AxiosPromise } from 'axios';
import { createHttpAxios } from './http';

class AuthServiceAPI {
  public httpAxios: AxiosInstance;

  constructor() {
    this.httpAxios = createHttpAxios('', {
      baseURL: process.env.REACT_APP_OAUTH_API_URL,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8',
        Authorization: 'Basic c21hcnQtZHJvcHMtb2F1dGgyOnNtNHJ0LWRyMHBzLTA0dXRoMi1zZWNyZXQ='
      }
    });
  }

  public login(user: FormData): AxiosPromise<any> {
    return this.httpAxios
      .post('', user)
      .then(response => response)
      .catch(err => {
        throw err;
      });
  }

  public storeUserCredentials(userdata: any) {
    localStorage.setItem('userdata', JSON.stringify(userdata));
  }
  public isLoggedIn() {
    if (localStorage.getItem('userdata')) {
      return true;
    }
    return false;
  }
  public clearToken(): void {
    localStorage.removeItem('userdata');
  }

  public getToken() {
    const userdata = localStorage.getItem('userdata');
    if (userdata) {
      return JSON.parse(userdata).access;
    }
    return '';
  }
  public getRefresh() {
    const userdata = localStorage.getItem('userdata');
    if (userdata) {
      return JSON.parse(userdata).refresh;
    }
    return '';
  }

  public encriptPassword(pass: string) {
    return btoa(`${pass}:${process.env.REACT_APP_API_USER_KEY}:${process.env.REACT_APP_API_USER_PASS}`);
  }
}
const authService = new AuthServiceAPI();

export default authService;
