import React, { Component, ReactNode } from 'react';
import { Col, Grid, Row } from 'react-flexbox-grid';
import { VCardPanel, VSelectField, VSpinner, VTable } from 'gsi-vx-ui';
import { inject, observer } from 'mobx-react';
import { FieldState, FormState } from 'formstate';
import { required } from '../../../../../commons/form/Validators';
import { ISortResult, IVTableProps } from 'gsi-vx-ui/lib/types/Table/Table';
import { IVWidgetTableProps } from 'gsi-vx-ui/lib/types/Table/Widget/Widget';
import { Icon, Tooltip } from '@blueprintjs/core';
import styled from 'styled-components';
import { SummaryDeliveriesStore } from '../../stores/summary/DeliveriesStore/SummaryDeliveriesStore';
import { FilterOperator } from '../../../../../../core/types/FilterType';
import { SummaryShiftDeliveries } from '../../models/summaryShiftDeliveries';
import { truckUrgencyColor } from '../../enums/enums';
import { SummaryStore } from '../../stores/summary/SummaryStore';

import { AppThemeContext, ITheme } from '../../../../../../App';

const ThemeConsumer = AppThemeContext.Consumer;

const DriverStyle = styled('h2')`
  margin-top: 0;
`;

interface IDeliverySummaryDeliveriesProps {
  deliveries_store?: SummaryDeliveriesStore;
  summary_store?: SummaryStore;
}

@inject('summary_store', 'deliveries_store')
@observer
export class DeliverySummaryDeliveries extends Component<IDeliverySummaryDeliveriesProps> {
  form: FormState<any>;

  constructor(props: IDeliverySummaryDeliveriesProps) {
    super(props);
    this.form = new FormState<any>({
      shiftNumber: new FieldState('').validators(required),
      deliveryView: new FieldState(1).validators(required)
    });
  }

  onSelect = (data: any) => {
    console.log(data);
  };

  onSort = (sort: ISortResult) => {
    const { deliveries_store } = this.props;
    if (sort.order === 'NONE') {
      deliveries_store!.removeOrder({
        property: this.props.deliveries_store!.columns[sort.columnIndex],
        direction: sort.order
      });
    } else {
      deliveries_store!.addOrder({
        property: this.props.deliveries_store!.columns[sort.columnIndex],
        direction: sort.order
      });
    }
    deliveries_store!.getElements(`?shiftId=${deliveries_store!.element.shiftId}`);
  };

  getContentTab = (driver: any) => {
    const optionsFilterDeliveriesViewBy = [
      { label: 'View All', value: 1 },
      { label: 'View Delivered', value: 2 },
      { label: 'View Undelivered', value: 3 }
    ];
    const data: IVTableProps = this.transformRoutesList();
    return this.props.deliveries_store!.element.shiftId !== 0 ? (
      <Grid fluid>
        {!this.props.deliveries_store!.isLoading ? (
          <React.Fragment>
            <Row>
              <Col xs={3}>
                <Row start={'xs'}>
                  <Col xs>
                    <DriverStyle>
                      {driver.driverFullName} , {driver.truckName}
                    </DriverStyle>
                  </Col>
                </Row>
              </Col>
              <Col xs={3}>
                {/*  <ShiftNumberSelect form={this.form} onChange={this.onChangeShiftNumber}>

           </ShiftNumberSelect>*/}
              </Col>
              <Col xs={6}>
                <Row end={'xs'}>
                  <VSelectField
                    options={optionsFilterDeliveriesViewBy}
                    filterable={false}
                    inline
                    label={'Filter Delivery View by:'}
                    fieldState={this.form.$.deliveryView}
                    onChange={this.onChangeFilterView}
                    id="view"
                  />
                </Row>
              </Col>
            </Row>
            <Row>
              <Col xs>
                {' '}
                <VTable {...data} />{' '}
              </Col>
            </Row>
          </React.Fragment>
        ) : (
          <VSpinner />
        )}
      </Grid>
    ) : (
      ''
    );
  };

  render() {
    const element = this.props.deliveries_store!.element;
    const loading = this.props.deliveries_store!.isLoading;
    const columns = this.props.deliveries_store!.columns;
    const amount = this.props.deliveries_store!.count || 0;
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <VCardPanel
              closeIcon={'minus'}
              openIcon={'plus'}
              collapse
              defaultActive
              headerText={`Deliveries (${amount})`}
              headerBackgroundColor={themeContext.headers}
              headerColor={'#FFFFFF'}
              headerTextJustify={'center'}
              headerOrientation={'row-reverse'}
              bodyPadding={'10px 0'}
            >
              {this.getContentTab(element)}
            </VCardPanel>
          )
        }
      </ThemeConsumer>
    );
  }

  onChangeFilterView = (data: any) => {
    const { deliveries_store } = this.props;
    const operator: FilterOperator = 'EQUALITY';
    deliveries_store!.addFilter({ operator, field: 'viewStatus', value: data });
    deliveries_store!.getElements(`?shiftId=${deliveries_store!.element.shiftId}`);
  };

  onChangeShiftNumber = (data: any) => {
    this.props.summary_store!.setShiftNumber(data);
    // const { deliveries_store } = this.props;
    // deliveries_store!.getElements(`?shiftId=${data}`);
  };

  transformRoutesList = (): IVTableProps => {
    // todo do some tranform to get de list.
    /*Get the result of the store and then here transform the data to the list*/
    const customWidget: IVWidgetTableProps = {
      column: 'urgency',
      widget: {
        type: 'CUSTOMERCOMPONENT',
        cusmtomerCell: {
          renderCustomer: (value: any): ReactNode => {
            return <Icon icon={'flag'} iconSize={15} color={truckUrgencyColor[value]} />;
          }
        }
      }
    };

    const imgWidget: IVWidgetTableProps = {
      column: 'img',
      widget: {
        type: 'CUSTOMERCOMPONENT',
        cusmtomerCell: {
          renderCustomer: (value: any): ReactNode => {
            return (
              value.img && (
                <Tooltip usePortal content={'View Invoice'}>
                  <Icon style={{ cursor: 'pointer' }} icon={'media'} iconSize={15} color={'#8997a2'} />
                </Tooltip>
              )
            );
          }
        }
      }
    };

    const data = this.props.deliveries_store!.elements.map((item: SummaryShiftDeliveries) => {
      return {
        deliveryId: item.deliveryId,
        rt_sq: item.rt_sq,
        urgency: item.urgency,
        img: { img: item.img, id: item.deliveryId },
        ticket: item.ticket,
        accountNumber: item.accountNumber,
        name: item.name,
        eta: item.eta,
        atd: item.atd,
        qty: item.qty
      };
    });

    const widgetsCell: IVWidgetTableProps[] = [imgWidget, customWidget];
    return {
      widgetsCell,
      data,
      columns: this.props.deliveries_store!.columns,
      striped: true,
      columns_name: {
        rt_sq: 'Rt-Sq',
        urgency: 'Status',
        img: 'Img',
        ticket: 'Order #',
        accountNumber: 'Account',
        name: 'Name',
        eta: 'ETA',
        atd: 'ATD',
        qty: 'QTY'
      },
      typeHeightRow: 'HALF',
      reordering: true,
      onOrderColumns: this.onOrdering,
      sortable: {
        setupsOrden: this.props.deliveries_store!.sortOrderForTable,
        columns: ['rt_sq', 'urgency', 'ticket', 'accountNumber', 'name', 'eta', 'atd', 'qty'],
        onSort: this.onSort
      },
      cellSelectionType: 'CELL',
      actionsSelection: {
        onSelectionChange: this.cellClick
      }
    };
  };

  cellClick = (data: any) => {
    console.log(data);
  };

  onOrdering = (columns: string[]) => {
    this.props.deliveries_store!.setColumns(columns);
  };
}
