import React from 'react';
import { Pie } from 'react-chartjs-2';

const backdata = {
  per_drivers: [
    {
      amount: 37.78,
      name: 'Unassigned Drv'
    },
    {
      amount: 39.73,
      name: 'Dan A - Inactive'
    },
    {
      amount: 22.08,
      name: 'Mike D'
    }
  ],
  per_products: [
    {
      amount: 10.8,
      name: '001-RESIDENTIAL LP-4'
    },
    {
      amount: 83.07,
      name: '002-RES LP USE 001-4'
    },
    {
      amount: 70.12,
      name: '003-COMMERCIAL-6'
    }
  ]
};

type GraphDataType = 'PRODUCTS' | 'DRIVERS';

interface IProps {
  type: GraphDataType;
}
const GallonsGraph = (props: IProps) => {
  const data = buildData(props.type);
  return (
    <Pie
      height={125}
      data={data}
      options={{ title: { display: true }, legend: { position: 'right' }, responsive: true }}
    />
  );
};

const buildData = (type: GraphDataType) => {
  const labels: string[] = [];
  const datasets: number[] = [];
  switch (type) {
    case 'DRIVERS':
      backdata.per_drivers.forEach(value => {
        labels.push(value.name);
        datasets.push(value.amount);
      });
      break;
    case 'PRODUCTS':
      backdata.per_products.forEach(value => {
        labels.push(value.name);
        datasets.push(value.amount);
      });
      break;
  }

  return {
    labels,
    datasets: [
      {
        data: datasets,
        backgroundColor: ['#FF6384', '#36A2EB', '#FFCE56'],
        hoverBackgroundColor: ['#FF6384', '#36A2EB', '#FFCE56']
      }
    ]
  };
};

export default GallonsGraph;
