import React, { Component } from 'react';

import { Container, CSidebarPushable, CSidebarPusher } from './styles';
import { RiftGate, RiftRouter } from 'rift-router';
import MainNavbar from './MainNavbar';
import MainSidebar from './MainSidebar';
import { inject, observer } from 'mobx-react';
import { AppStore } from '../../stores/AppStore';
import DetailsDrawer from './Drawers/DetailsDrawer';
import IRecentAlert from '../../types/RecentAlert';
import { ITheme, AppThemeContext } from '../../../App';

const ThemeConsumer = AppThemeContext.Consumer;

interface IProp {
  app?: AppStore;
  router?: RiftRouter;
}

@inject('app', 'router')
@observer
export default class Main extends Component<IProp> {
  render() {
    return (
      <CSidebarPushable>
        <MainSidebar />
        <CSidebarPusher>
          <MainNavbar />
          <ThemeConsumer>
            {(themeContext: ITheme) =>
              themeContext && (
                <Container background={themeContext.accent_gray}>
                  <RiftGate />
                </Container>
              )
            }
          </ThemeConsumer>
        </CSidebarPusher>
        <DetailsDrawer
          process={this.props.app!.operations}
          alerts={this.props.app!.recentAlerts}
          isOpen={this.props.app!.isOpenProcess}
          onClose={() => this.props.app!.setIsOpenProcess(false)}
          onNavigate={this.navigateAlert}
        />
      </CSidebarPushable>
    );
  }

  navigateAlert = (alert: IRecentAlert) => {
    this.props.router!.riftTo(`/delivery${this.getRouteParams(alert.route)}`);
    this.props.app!.setIsOpenProcess(false);
  };

  private getRouteParams = (object: any) => {
    let params = '';
    console.log(object);
    const keys = Object.keys(object);
    if (keys.length > 0) {
      keys.map((key, index) => {
        const modifier = index === 0 ? '?' : '&&';
        params += `${modifier}${key}=${object[key]}`;
      });
    }
    return params;
  };
}
