import React from 'react';
import ParentPaginateStore from '../../../../../../../core/stores/ParentPaginateStore';
import { action, computed, observable } from 'mobx';
import { DetailsShift } from '../../../models/detailsShift';
import detailsService from '../../../services/DetailsService/DetailsService';
import { ShiftDriverCheckListModel } from '../../../models/ShiftDriverCheckListModel';

export class ShiftDetailsStore extends ParentPaginateStore {
  @observable
  element: DetailsShift;
  @observable
  loadingDriver: boolean;
  @observable
  elementDriverCheckList: ShiftDriverCheckListModel;
  @observable
  elements: DetailsShift[];
  @observable
  public columns: string[];

  constructor() {
    super(detailsService);
    this.element = new DetailsShift();
    this.loadingDriver = false;
    this.elementDriverCheckList = new ShiftDriverCheckListModel();
    this.elements = [];
    this.columns = [
      'shiftId',
      'trxnStatus',
      'shiftResult',
      'truck',
      'driver',
      'shiftDate',
      'startHours',
      'shiftMiles',
      'shiftTotalizer',
      'shiftTotalizer2',
      'totalStops',
      'stopsCompleted',
      'stopsRptd',
      'stopsRecd'
    ];
  }

  @action
  setElement(element: DetailsShift) {
    this.element = element;
  }

  @action
  setLoadingDriver(element: boolean) {
    this.loadingDriver = element;
  }

  @action
  setElementDriverCheckList(element: ShiftDriverCheckListModel) {
    this.elementDriverCheckList = element;
  }

  @action
  setElements(elements: DetailsShift[]) {
    this.elements = elements;
  }

  @action
  setColumns(columns: string[]) {
    this.columns = columns;
  }

  @computed
  get sortOrderForTable(): { order: 'ASC' | 'DESC' | 'NONE'; columnName: string; columnIndex: number }[] {
    return this.orders.map(item => {
      return { order: item.direction, columnName: item.property, columnIndex: this.columns.indexOf(item.property) };
    });
  }

  public setPagingOption(data: any) {
    const elements: DetailsShift[] = data.content;
    if (elements.length > 0) {
      this.setElement(elements[0]);
    } else {
      this.setElement(new DetailsShift());
    }
    this.setElements(elements);
    this.setCount(data.totalElements);
  }

  public getDriverCheckListById(id: number | string) {
    if (id) {
      this.setLoadingDriver(true);
      const filters = this.filters.map(item => {
        if (item.field === 'date') {
          return item;
        }
      });
      this.serviceContainer
        .getByIdDriverCheckList('driverCheckList', id, filters)
        .then((response: any) => {
          this.setLoadingDriver(false);
          this.setElementDriverCheckList(response.data);
        })
        .catch((err: any) => {
          this.setLoadingDriver(false);
          throw err.response;
        });
    }
  }
}

const shiftDetailsStore = new ShiftDetailsStore();
export default shiftDetailsStore;
