import React, { Component } from 'react';
import { VCardPanel, VSpinner } from 'gsi-vx-ui';
import { DetailsList } from '../../../../dispatch/components/details_list/DetailsList';
import { ButtonIcon, ButtonIconAction, IconButton } from './style';
import { inject, observer } from 'mobx-react';
import { ShiftDetailsStore } from '../../../stores/summary/ShiftDetailsStore/ShiftDetailsStore';
import { Icon } from '@blueprintjs/core';
import styled from 'styled-components';
import { AppThemeContext, ITheme } from '../../../../../../../App';

const HeaderDriverCheckList = styled('div')`
  padding: 8px;
  font-size: 20px;
  font-weight: bold;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  height: 35px;
`;
const ContainerCheckList = styled('div')`
  display: flex;
  justify-content: space-between;
  padding-right: 20px;
`;

const iconStyle = {
  backgroundColor: '#c23030',
  color: 'white',
  borderRadius: '50px'
};

const ThemeConsumer = AppThemeContext.Consumer;

interface ShiftDriverCheckListProps {
  details_store?: ShiftDetailsStore;
}

@inject('details_store')
@observer
class ShiftDriverCheckList extends Component<ShiftDriverCheckListProps> {
  constructor(props: ShiftDriverCheckListProps) {
    super(props);
  }

  render() {
    const { elementDriverCheckList } = this.props.details_store!;
    const listDetails = [
      {
        key: 'shift',
        header: 'Shift #',
        description: <input type="text" disabled size={12} placeholder={`${elementDriverCheckList.shiftId || 0}`} />
      },
      {
        key: 'login',
        header: 'Logged In, Shift Started',
        description: (
          <input type="text" disabled size={12} placeholder={`${elementDriverCheckList.loginStarted || ''}`} />
        )
      },
      {
        key: 'logout',
        header: 'Logged Out, Shift Ended',
        description: <input type="text" disabled size={12} placeholder={`${elementDriverCheckList.logoutEnd || ''}`} />
      },
      {
        key: 'endt',
        header: 'Pre-Trip Vehicle Inspection',
        description: (
          <IconButton>
            {elementDriverCheckList.preTripVehicleInspection && <ButtonIcon icon={'tick-circle'} intent={'success'} />}
            <ButtonIconAction disabled={!elementDriverCheckList.preTripVehicleInspection} icon={'duplicate'} />
          </IconButton>
        )
      },
      {
        key: 'totaltime',
        header: 'Post-Trip Vehicle Inspection',
        description: (
          <IconButton>
            {elementDriverCheckList.postTripVehicleInspection && <ButtonIcon icon={'tick-circle'} intent={'success'} />}
            <ButtonIconAction disabled={!elementDriverCheckList.postTripVehicleInspection} icon={'duplicate'} />
          </IconButton>
        )
      },
      {
        key: 'totalmiles',
        header: 'EOS Trip Report',
        description: (
          <IconButton>
            {elementDriverCheckList.lastLogout && <ButtonIcon icon={'tick-circle'} intent={'success'} />}
            <ButtonIconAction disabled={!elementDriverCheckList.lastLogout} icon={'duplicate'} />
          </IconButton>
        )
      },
      {
        key: 'allDriver',
        header: `All Driver Tickets Rcv'd`,
        description: elementDriverCheckList.shiftId && !this.props.details_store!.loadingDriver && (
          <IconButton>
            {elementDriverCheckList.completeShiftsCount ? (
              <ButtonIcon icon={'tick-circle'} intent={'success'} />
            ) : (
              <Icon style={iconStyle} icon={'cross'} intent={'danger'} />
            )}
          </IconButton>
        )
      },
      {
        key: 'totalstop',
        header: `Mobile App Version #`,
        description: (
          <input type="text" disabled size={12} placeholder={`${elementDriverCheckList.mobileAppVersion || ''}`} />
        )
      }
    ];
    const textHeader = this.props.details_store!.elementDriverCheckList.driverName
      ? `${this.props.details_store!.elementDriverCheckList.driverName}, ${
          this.props.details_store!.elementDriverCheckList.truckName
        }`
      : 'No Driver Selected';
    const spinner = this.props.details_store!.loadingDriver ? <VSpinner size={18} intent={'primary'} /> : '';
    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <VCardPanel
              headerTextJustify={'center'}
              bodyPadding={'0'}
              headerText={'Driver Checklist'}
              headerBackgroundColor={themeContext.headers}
              headerColor={'#FFFFFF'}
            >
              <ContainerCheckList>
                <HeaderDriverCheckList>
                  <span>{textHeader}</span>
                  <Icon style={{ marginLeft: '10px' }} icon="drive-time" iconSize={20} />
                </HeaderDriverCheckList>
                {spinner}
              </ContainerCheckList>
              <DetailsList orientationHeader={{ position: 'end', resolution: 'xs', size: 7 }} items={listDetails} />
            </VCardPanel>
          )
        }
      </ThemeConsumer>
    );
  }
}

export default ShiftDriverCheckList;
