import React, { Component } from 'react';
import { Icon, Table } from 'semantic-ui-react';
import IGenericTable, { ISortBy } from './IGenericTable';
import { observer } from 'mobx-react';
import { action, observable } from 'mobx';
import { dataTable, dataTableHeader } from './styles';
import { iconStyle } from '../../../delivery/modules/sumary/components/DeliverySummaryRoute/Components/ProgresLabel/style';

interface IGenericTableProps {
  genericTableData: IGenericTable;
}

@observer
class GenericTable extends Component<IGenericTableProps> {
  @observable stateGenericTable: ISortBy[];

  constructor(props: IGenericTableProps) {
    super(props);
    this.stateGenericTable = this.props.genericTableData.sortBy;
  }

  @action setStateGenericTable(stateColumn: string) {
    let newOrder;
    newOrder = { column: stateColumn, direction: 'ascending' as 'ascending' };
    if (!this.stateGenericTable.find(item => item.column === stateColumn)) {
      this.stateGenericTable.push(newOrder);
    }
    this.stateGenericTable.map(stateSort => {
      if (stateSort.column === stateColumn) {
        switch (stateSort.direction) {
          case 'ascending':
            stateSort.direction = 'descending';
            break;
          case 'descending':
            stateSort.direction = undefined;
            break;
          default:
            stateSort.direction = 'ascending';
            break;
        }
      }
    });
    this.props.genericTableData.onSort(this.stateGenericTable);
  }

  checkDirection(columnKey: string) {
    const result = this.stateGenericTable.find(item => item.column === columnKey);
    return result ? result.direction : undefined;
  }

  render() {
    const { genericTableData } = this.props;

    return (
      <Table sortable celled structured style={dataTable}>
        <Table.Header>
          <Table.Row>
            {genericTableData.headers.map(header => {
              return (
                <Table.HeaderCell
                  textAlign="center"
                  style={dataTableHeader}
                  sorted={this.checkDirection(header.key)}
                  key={header.key}
                  onClick={(e: any) => {
                    e.preventDefault();
                    this.setStateGenericTable(header.key);
                  }}
                >
                  {header.label}
                </Table.HeaderCell>
              );
            })}
          </Table.Row>
        </Table.Header>
        <Table.Body>
          {genericTableData.data.map(row => {
            return (
              <Table.Row
                id={row.key}
                key={row.key}
                onClick={(e: any) => {
                  e.preventDefault();
                  genericTableData.onSelect(row);
                }}
              >
                {row.items.map(item => {
                  return (
                    <Table.Cell colSpan={item.colSpan} textAlign={item.textAlign} key={item.key}>
                      {' '}
                      {item.icon && (
                        <Icon style={iconStyle(item.icon.type)} size={item.icon.size} name={item.icon.icon} />
                      )}{' '}
                      {item.label}
                    </Table.Cell>
                  );
                })}
              </Table.Row>
            );
          })}
        </Table.Body>
        <Table.Footer />
      </Table>
    );
  }
}

export default GenericTable;
