import React, { Component } from 'react';
import { DetailsList } from '../details_list/DetailsList';
import { SegmentPercent } from './styles';
import { Col, Grid, Row } from 'react-flexbox-grid';

interface IDispatchSummaryDetailsProps {
  percent: number;
}

export class DispatchSummaryDetails extends Component<IDispatchSummaryDetailsProps> {
  constructor(props: IDispatchSummaryDetailsProps) {
    super(props);
  }

  render() {
    const listDetails = [
      {
        key: 'capacity',
        header: 'Capacity',
        description: '3499'
      },
      {
        key: 'proj',
        header: 'Proj. Qty',
        description: '3555'
      }
    ];
    const { percent } = this.props;
    return (
      <Grid fluid>
        <Row>
          <Col xs={12} sm={12} md={3}>
            <SegmentPercent>{percent}%</SegmentPercent>
          </Col>
          <Col xs={12} sm={12} md={9}>
            <DetailsList items={listDetails} />
          </Col>
        </Row>
      </Grid>
    );
  }
}
