import React, { Component } from 'react';
import { VCardPanel } from 'gsi-vx-ui';
import { Col, Grid, Row } from 'react-flexbox-grid';
import { DetailsList } from '../../../../dispatch/components/details_list/DetailsList';
import { Icon } from '@blueprintjs/core';
import { AppThemeContext, ITheme } from '../../../../../../../App';

const ThemeConsumer = AppThemeContext.Consumer;

class NotExportedDrivers extends Component {
  render() {
    const listDetails = [
      {
        key: 'capacity',
        header: 'Rick D',
        description: <Icon icon={'delete'} color={'#ff5d55'} iconSize={15} />
      }
    ];
    const listDetailsNotLogin = [
      {
        key: 'capacity',
        header: 'Tom E',
        description: <Icon icon={'delete'} color={'#ff5d55'} iconSize={15} />
      }
    ];

    return (
      <ThemeConsumer>
        {(themeContext: ITheme) =>
          themeContext && (
            <VCardPanel
              bodyPadding={'15px 0'}
              headerBackgroundColor={themeContext.headers}
              headerColor={'#FFFFFF'}
              headerTextJustify={'center'}
              headerText={'These Drivers CANNOT be exported (Missing Data)'}
            >
              <Grid>
                <Row start={'xs'} between={'xs'}>
                  <Col xs={6}>
                    <VCardPanel
                      bodyPadding={'0'}
                      headerTextJustify={'center'}
                      headerText={'Driver(s) with Open'}
                      headerBackgroundColor={themeContext.headers}
                      headerColor={'#FFFFFF'}
                    >
                      <DetailsList items={listDetails} color={'#ff5d55'} />
                    </VCardPanel>
                  </Col>
                  <Col xs={6}>
                    <VCardPanel
                      bodyPadding={'0'}
                      headerTextJustify={'center'}
                      headerText={'Driver(s) with No Login'}
                      headerBackgroundColor={themeContext.headers}
                      headerColor={'#FFFFFF'}
                    >
                      <DetailsList items={listDetailsNotLogin} color={'#ff5d55'} />
                    </VCardPanel>
                  </Col>
                </Row>
              </Grid>
            </VCardPanel>
          )
        }
      </ThemeConsumer>
    );
  }
}

export default NotExportedDrivers;
