export class DetailsShift {
  shiftId: number;
  trxnStatus: number;
  shiftResult: boolean;
  truck: string;
  driver: string;
  driverId: number;
  shiftDate: string;
  startHours: string;
  shiftMiles: number;
  shiftTotalizer: number;
  shiftTotalizer2: number;
  totalStops: number;
  stopsCompleted: number;
  stopsRptd: number;
  stopsRecd: number;

  constructor(
    shiftId = 0,
    trxnStatus = 0,
    shiftResult = false,
    truck = '',
    driver = '',
    driverId = 0,
    shiftDate = '',
    startHours = '',
    shiftMiles = 0,
    shiftTotalizer = 0,
    shiftTotalizer2 = 0,
    totalStops = 0,
    stopsCompleted = 0,
    stopsRptd = 0,
    stopsRecd = 0
  ) {
    this.shiftId = shiftId;
    this.trxnStatus = trxnStatus;
    this.shiftResult = shiftResult;
    this.truck = truck;
    this.driver = driver;
    this.driverId = driverId;
    this.shiftDate = shiftDate;
    this.startHours = startHours;
    this.shiftMiles = shiftMiles;
    this.shiftTotalizer = shiftTotalizer;
    this.shiftTotalizer2 = shiftTotalizer2;
    this.totalStops = totalStops;
    this.stopsCompleted = stopsCompleted;
    this.stopsRptd = stopsRptd;
    this.stopsRecd = stopsRecd;
  }
}
