import styled from 'styled-components';
import { Button, Icon } from '@blueprintjs/core';

export const IconButton = styled('div')`
  width: 75%;
  float: right;
  display: flex;
  align-items: center;
`;
export const ButtonIconAction = styled(Button)`
  width: 80%;
  border-radius: 10px;
  float: right;
`;
export const ButtonIcon = styled(Icon)`
  width: 20%;
  float: left;
`;
