import orderHistoryServices from '../services/OrderHistoryServices';
import ParentPaginateStore from '../../../../../core/stores/ParentPaginateStore';

export class OrderHistoryStore extends ParentPaginateStore {
  constructor() {
    super(orderHistoryServices);
  }
  public setPagingOption(data: any) {
    this.setElements(data.content);
    this.setCount(data.page.totalElements);
  }
}

const orderHistoryStore = new OrderHistoryStore();
export default orderHistoryStore;
