import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from '@blueprintjs/core';
import { VTable } from 'gsi-vx-ui';
import { Col, Row } from 'react-flexbox-grid';
import styled from 'styled-components';
import EmailComponent from './EmailComponent';

interface IMessagingComponent {
  messages: any[];
  drivers: any[];
  setDriver(driver: string): void;
}
interface IMessagingState {
  openEmail: boolean;
}
const EmailCol = styled(({ ...rest }) => <Col {...rest} />)`
  ${(props: any) => (props.display ? `display: flex;` : `display: none;`)}
  justify-content: flex-end;
`;
const FooterCol = styled(Col)`
  display: flex;
  justify-content: flex-end;
`;
const styleButton = { marginTop: 5, marginBottom: 5, width: 200 };

@observer
class MessagingComponent extends Component<IMessagingComponent, IMessagingState> {
  openEmail: boolean;
  constructor(props: IMessagingComponent) {
    super(props);
    this.openEmail = false;
  }
  public state: IMessagingState = {
    openEmail: false
  };
  handelSelectItemMessages = (data: any) => {};
  handelSortDrivers = (columnIndex: number, order: any) => {};
  handelSelectItemDriver = (data: any) => {
    this.props.setDriver(data.items[0].label);
  };
  openEmailForms = () => {
    this.setState({ ...this.state, openEmail: true });
  };
  closeEmailForms = () => {
    this.setState({ ...this.state, openEmail: false });
  };
  render() {
    const { drivers, messages } = this.props;
    return (
      <div>
        <Row>
          <Col xs={12} lg={3}>
            <VTable
              typeHeightRow={'HALF'}
              columns={['driver', 'truck']}
              cellSelectionType={'ENTIRE_ROW'}
              data={drivers}
              sortable={{ columns: ['driver', 'truck'] }}
            />
          </Col>
          <Col xs={12} lg={9}>
            <VTable
              cellSelectionType={'ENTIRE_ROW'}
              typeHeightRow={'HALF'}
              columns={['ack', 'time', 'toname', 'from', 'msg']}
              data={messages}
              columns_name={{
                toname: 'ToName'
              }}
              widgetsCell={[
                {
                  column: 'ack',
                  widget: {
                    type: 'CHECKBOX'
                  }
                }
              ]}
            />
          </Col>
        </Row>
        <Row>
          <EmailCol xs={12} display={this.state.openEmail}>
            <EmailComponent isOpen={this.state.openEmail} handleClose={this.closeEmailForms} />
          </EmailCol>
        </Row>
        <Row>
          <FooterCol xs={12}>
            <Button icon={'annotation'} style={{ ...styleButton, marginRight: 5 }} onClick={this.openEmailForms}>
              Compose
            </Button>
            <Button icon={'refresh'} style={{ ...styleButton, marginRight: 5 }}>
              Refresh
            </Button>
            <Button icon={'export'} style={styleButton}>
              Export to Excel
            </Button>
          </FooterCol>
        </Row>
      </div>
    );
  }
}

export default MessagingComponent;
