import React, { Component, ReactNode } from 'react';
import { observer, Provider } from 'mobx-react';
import OrderManagementLayout from '../components/order_management/OrderManagementLayout';
import orderManagementStore, { OrderManagementStore } from '../stores/OrderManagementStore';
import urgencySummaryStore, { UrgencySummaryStore } from '../stores/UrgencySummaryStore';
import openOrdersStore, { OpenOrdersStore } from '../stores/OpenOrdersStore';
import duplicateOrdersStore, { DuplicateOrdersStore } from '../stores/DuplicateOrdersStore';
import incompleteStore, { IncompleteStore } from '../stores/IncompleteStore';
import orderAccountStore, { OrderAccountStore } from '../stores/OrderAccountStore';
import orderHistoryStore, { OrderHistoryStore } from '../stores/OrderHistoryStore';
import orderProfileStore, { OrderProfileStore } from '../stores/OrderProfileStore';
import ordersStore, { OrdersStore } from '../stores/OrdersStore';
import unGeoStore, { UnGeoStore } from '../stores/UnGeoStore';
import deliveryExceptionStore, { DeliveryExceptionStore } from '../stores/DeliveryExceptionStore';
import { forEach, last } from 'lodash';
import { IExport } from '../enums/enums';
import { VCheckboxField } from 'gsi-vx-ui';

export interface IOrderContext {
  handleOnSaveOrders(): void;
  refreshList(): void;
  clearProfile(): void;
  onExportExcel(): void;
  refresOrder(): void;
  onSearchByDays(data: any): void;
  setSearchList(data: any): void;
  selectProfile(data: any): void;
  renderCustomerType(): ReactNode;
  renderDeliveryType(): ReactNode;
  renderCustom(value: string[]): any[];
  onSaveOrders(data: any, action: any): void;
  onSelectOrders(data: any): void;
  listTypeClick(data: any): void;
  isOrdersLoading: boolean;
  isOrderMLoading: boolean;
  totalOrders: number;
  profileActive: number | string;
  activeList: string;
  searchDays: number | string;
}
export const OrderContext = React.createContext<IOrderContext>({
  isOrdersLoading: false,
  isOrderMLoading: false,
  totalOrders: 0,
  profileActive: -1,
  searchDays: '',
  activeList: '',
  handleOnSaveOrders: () => {},
  refreshList: () => {},
  onExportExcel: () => {},
  refresOrder: () => {},
  renderCustom: () => [],
  clearProfile: () => {},
  setSearchList: () => {},
  onSearchByDays: () => {},
  onSaveOrders: () => {},
  renderCustomerType: () => <></>,
  renderDeliveryType: () => <></>,
  onSelectOrders: ({}) => {},
  selectProfile: ({}) => {},
  listTypeClick: ({}) => {}
});
@observer
class OrderManagementContainer extends Component<any> {
  order_management_store: OrderManagementStore;
  urgency_summary_store: UrgencySummaryStore;
  open_orders_store: OpenOrdersStore;
  duplicate_orders_store: DuplicateOrdersStore;
  incomplete_orders_store: IncompleteStore;
  accounts_orders_store: OrderAccountStore;
  history_orders_store: OrderHistoryStore;
  order_profile_store: OrderProfileStore;
  delivery_exceptions_store: DeliveryExceptionStore;
  orders_store: OrdersStore;
  ungeo_store: UnGeoStore;
  constructor(props: any) {
    super(props);
    this.order_management_store = orderManagementStore;
    this.urgency_summary_store = urgencySummaryStore;
    this.open_orders_store = openOrdersStore;
    this.duplicate_orders_store = duplicateOrdersStore;
    this.incomplete_orders_store = incompleteStore;
    this.accounts_orders_store = orderAccountStore;
    this.orders_store = ordersStore;
    this.history_orders_store = orderHistoryStore;
    this.order_profile_store = orderProfileStore;
    this.delivery_exceptions_store = deliveryExceptionStore;
    this.ungeo_store = unGeoStore;
    this.urgency_summary_store.getElements();
    this.open_orders_store.getElements();
    this.delivery_exceptions_store.getStatus();
    this.order_management_store.loadLists();
  }
  handleOnSaveOrders() {
    this.urgency_summary_store.getElements();
    this.open_orders_store.getElements();
    this.history_orders_store.getElements();
  }
  onSaveOrders = (data: any, action: any) => {
    this.order_management_store.saveOrder(data, () => {
      action();
      this.urgency_summary_store.getElements();
      this.open_orders_store.getElements();
    });
  };
  onExportExcel() {
    const obj: IExport = this.orders_store.getOrderFeature();
    const profile = this.order_management_store.profileActive;
    const search = this.order_management_store.searchOrders;
    const filters = this[obj.store].filters;
    this.order_management_store.exportExcel({ filters, search }, profile, obj.feature, obj.name);
  }
  onSearchByDays = (data: number | string) => {
    this.orders_store.setSearchByDays(data);
  };
  clearProfile() {
    this.order_management_store.setActiveProfile(-1);
    this.open_orders_store.removeFilter({ value: '', field: 'profileId', operator: 'EQUALITY' });
    this.incomplete_orders_store.removeFilter({ value: '', field: 'profileId', operator: 'EQUALITY' });
    this.duplicate_orders_store.removeFilter({ value: '', field: 'profileId', operator: 'EQUALITY' });
    this.ungeo_store.removeFilter({ value: '', field: 'profileId', operator: 'EQUALITY' });
    this.refreshList();
  }
  selectProfile(id: number | string) {
    this.open_orders_store.addFilter({ value: id, field: 'profileId', operator: 'EQUALITY' });
    this.order_management_store.setActiveProfile(id);
    this.refreshList();
  }
  setSearchList(data: any) {
    this.open_orders_store.setSearch(data);
    this.order_management_store.setSearchOrder(data);
    this.duplicate_orders_store.setSearch(data);
    this.incomplete_orders_store.setSearch(data);
    this.ungeo_store.setSearch(data);
  }
  refreshList() {
    this.open_orders_store.getElements();
    this.duplicate_orders_store.getElements();
    this.incomplete_orders_store.getElements();
    this.ungeo_store.getElements();
  }
  renderCustomerType(value: string = 'circle.6'): ReactNode {
    return OrdersStore.getCustomerType(value, this.onOpenDetails);
  }
  renderCustom(columns: string[] = []) {
    const wigets: any[] = [];
    const style = { width: '40px', height: '70px' };
    forEach(columns, c => {
      c === 'geo' &&
        wigets.push({
          column: c,
          widget: {
            type: 'CUSTOMERCOMPONENT',
            cusmtomerCell: {
              renderCustomer: (value: any) => (
                <div onDoubleClick={this.onOpenDetails} style={{ width: '35px' }}>
                  <VCheckboxField id={`{idcolumns}${value}`} value={value} />
                </div>
              )
            }
          }
        });
      c === 'type' &&
        wigets.push({
          column: c,
          widget: {
            type: 'CUSTOMERCOMPONENT',
            cusmtomerCell: {
              renderCustomer: this.renderCustomerType.bind(this)
            }
          }
        });
      c !== 'geo' &&
        c !== 'type' &&
        wigets.push({
          column: c,
          widget: {
            type: 'CUSTOMERCOMPONENT',
            cusmtomerCell: {
              renderCustomer: (value: any) => (
                <div onDoubleClick={this.onOpenDetails.bind(this)}>
                  {value ? <span style={style}>{value}</span> : <p style={style} />}
                </div>
              )
            }
          }
        });
    });
    return wigets;
  }
  onOpenDetails = () => {
    const obj: IExport = this.orders_store.getOrderFeature();
    this[obj.store].setOpenDetails(true);
  };
  refresOrder() {
    const obj: IExport = this.orders_store.getOrderFeature();
    this[obj.store].getElements();
  }
  onSelectOrders(order: any) {
    this.order_management_store.setOrderDetails(order);
  }
  listTypeClick = (data: any) => {
    const list = this.urgency_summary_store.elements;
    const { filterByUrgency } = this.orders_store!;
    const ordertype = list[data.row].orderType;
    const urgency = Number(data.column);
    this.open_orders_store.setUrl('getOrderList');
    data.column === 1 && this.incomplete_orders_store.setFilterType(ordertype);
    data.column === 2 && this.duplicate_orders_store.setFilterType(ordertype);
    data.column === 3 && this.ungeo_store.setFilterType(ordertype);
    if (data.column > 3 && data.column < 9) {
      this.open_orders_store.addFilter({ field: 'orderStatus', value: 'Open', operator: 'EQUALITY' });
      this.open_orders_store.setFilterType(ordertype, urgency - 3);
      !filterByUrgency && this.open_orders_store.getElements();
    } else if (data.column === 9) {
      this.open_orders_store.setUrl('totalOpenOrdersList');
      this.open_orders_store.removeFilter({ operator: 'EQUALITY', field: 'urgencyType', value: '' });
      this.open_orders_store.removeFilter({ operator: 'EQUALITY', field: 'orderType', value: '' });
      ordertype !== 'TOTAL' &&
        filterByUrgency &&
        this.open_orders_store.addFilter({ operator: 'EQUALITY', field: 'orderType', value: ordertype });
      this.open_orders_store.getElements();
    }
    this.orders_store.setActiveList(data.column);
  };
  render() {
    const lastElemnt = last(this.urgency_summary_store.elements);
    return (
      <Provider
        {...{
          urgency_summary_store: this.urgency_summary_store,
          orders_store: this.orders_store,
          open_orders_store: this.open_orders_store,
          duplicate_orders_store: this.duplicate_orders_store,
          incomplete_orders_store: this.incomplete_orders_store,
          order_management_store: this.order_management_store,
          accounts_orders_store: this.accounts_orders_store,
          order_profile_store: this.order_profile_store,
          ungeo_store: this.ungeo_store,
          delivery_exceptions_store: this.delivery_exceptions_store,
          history_orders_store: this.history_orders_store
        }}
      >
        <OrderContext.Provider
          value={{
            isOrdersLoading:
              this.open_orders_store.isLoading ||
              this.ungeo_store.isLoading ||
              this.duplicate_orders_store.isLoading ||
              this.incomplete_orders_store.isLoading,
            filterByUrgency: this.orders_store.filterByUrgency,
            searchDays: this.orders_store.searchDays,
            activeList: this.orders_store.activeList,
            profileActive: this.order_management_store.profileActive,
            handleOnSaveOrders: this.handleOnSaveOrders.bind(this),
            refresOrder: this.refresOrder.bind(this),
            onSaveOrders: this.onSaveOrders.bind(this),
            setSearchList: this.setSearchList.bind(this),
            onSearchByDays: this.onSearchByDays.bind(this),
            renderCustom: this.renderCustom.bind(this),
            onExportExcel: this.onExportExcel.bind(this),
            isOrderMLoading: this.order_management_store.isLoading,
            clearProfile: this.clearProfile.bind(this),
            refreshList: this.refreshList.bind(this),
            selectProfile: this.selectProfile.bind(this),
            totalOrders: (lastElemnt && lastElemnt.total) || 0,
            renderCustomerType: this.renderCustomerType.bind(this),
            renderDeliveryType: OrdersStore.renderDeliveryType,
            onSelectOrders: this.onSelectOrders.bind(this),
            listTypeClick: this.listTypeClick.bind(this)
          }}
        >
          <OrderManagementLayout
            changeColor={this.props.changeColor}
            totalExcept={this.delivery_exceptions_store!.count}
          />
        </OrderContext.Provider>
      </Provider>
    );
  }
}

export default OrderManagementContainer;
