import { action, observable } from 'mobx';
import IModulesTypes from '../commons/types/modules';

export class ReviewStore {
  @observable
  activeModule: IModulesTypes;

  @observable
  modules: IModulesTypes[];

  @observable
  isLoading: boolean;

  @observable
  tickets: any[];

  constructor() {
    this.isLoading = false;
    this.tickets = [];
    this.modules = [
      { name: 'review', icon: 'clipboard', label: 'Review', children: [] },
      { name: 'fuelPurchase', icon: 'tint', label: 'Fuel Purchase', children: [] },
      { name: 'vehicleMaintenance', icon: 'settings', label: 'Vehicle Maintenance', children: [] },
      { name: 'dvir', icon: 'settings', label: 'DVIR', children: [] },
      { name: 'driverAddedTanks', icon: 'settings', label: 'Driver Added Tanks', children: [] },
      {
        name: 'stationsLogs',
        icon: 'tint',
        label: 'Stations Logs',
        children: [
          { name: 'tickets', icon: 'home', label: 'Tickets', children: [] },
          { name: 'openClose', icon: 'home', label: 'Open/Close', children: [] }
        ]
      }
    ];
    this.activeModule = this.modules[5];
  }

  @action
  createModuleList(modules: IModulesTypes[]) {
    this.modules = modules;
  }

  @action
  setActiveModule(module: IModulesTypes) {
    this.activeModule = module;
  }

  @action
  setTickets(tickets: any[]) {
    this.tickets = tickets;
  }

  @action
  setLoading(value: boolean) {
    this.isLoading = value;
  }
}

const reviewStore = new ReviewStore();
export default reviewStore;
