import React, { Component } from 'react';
import { DeliveryStore } from '../DeliveryStore';
import { inject, observer } from 'mobx-react';
import TrackingContainer from '../modules/tracking/containers/tracking/TrackingContainer';
import OrderManagementContainer from '../modules/order_management/containers/OrderManagementContainer';
import MessagingContainer from '../modules/messaging/containers/messaging/MessagingContainer';
import { SummaryContainer } from '../modules/sumary';
import { DeliveryDispatch } from '../modules/dispatch';
import { VTabsPanel } from 'gsi-vx-ui';
import { IconName } from '@blueprintjs/core';
import { ITabsPanelTypes } from 'gsi-vx-ui/lib/types/TabsPanel';
import { RiftRouter } from 'rift-router';

interface IDeliveryLayout {
  delivery_store?: DeliveryStore;
  router?: RiftRouter;
}

@inject('delivery_store', 'router')
@observer
class DeliveryLayout extends Component<IDeliveryLayout> {
  constructor(props: IDeliveryLayout) {
    super(props);
    if (this.props.router!.search.hasOwnProperty('submodule')) {
      this.props.delivery_store!.setActiveModule(this.props.router!.search.submodule);
    } else {
      this.props.delivery_store!.setActiveModule('module_1');
    }
  }
  handleChange = (data: ITabsPanelTypes) => {
    this.props.delivery_store!.setActiveModule(data.key);
  };
  changeColor = (module: number, color: IconName) => {
    this.props.delivery_store!.setColor(module, color);
  };
  render() {
    const { activeModule } = this.props.delivery_store!;
    const modules: ITabsPanelTypes[] = [
      {
        icon: { intent: 'primary', icon: 'folder-close', iconSize: 16 },
        label: 'Delivery Summary',
        key: 'module_1',
        content: <SummaryContainer />
      },
      {
        icon: { intent: 'primary', icon: 'folder-close', iconSize: 16 },
        label: 'Order Management',
        key: 'module_2',
        content: <OrderManagementContainer changeColor={this.changeColor} />
      },
      {
        icon: { intent: 'primary', icon: 'folder-close', iconSize: 16 },
        label: 'Dispatch',
        key: 'module_3',
        content: <DeliveryDispatch />
      },
      {
        icon: { intent: 'primary', icon: 'folder-close', iconSize: 16 },
        label: 'Tracking',
        key: 'module_4',
        content: <TrackingContainer />
      },
      {
        icon: { intent: 'primary', icon: 'folder-close', iconSize: 16 },
        label: 'Messaging',
        key: 'module_5',
        content: <MessagingContainer />
      }
    ];
    return (
      <VTabsPanel
        active={this.props.delivery_store!.activeModule}
        padding={'10px'}
        tabList={modules}
        handleChange={this.handleChange}
      />
    );
  }
}

export default DeliveryLayout;
