import React, { Component } from 'react';
import { Button } from '@blueprintjs/core';
import { GridLogin, ImgLogoContainer, LoginFormContainer, LogoContainer, TitleHeader } from './styles';
import { FormState } from 'formstate';
import { FormattedMessage, InjectedIntlProps, injectIntl } from 'react-intl';
import { Col, Grid, Row } from 'react-flexbox-grid';
import { VCardPanel, VInputField, VSelectField, VSpinner } from 'gsi-vx-ui';
import { AppThemeContext, ITheme } from '../../../../../App';
import sdwoLogo from '../../../../../assets/images/logos/SmartDrops White+Orange Tagline@2x.png';
import vwoLogo from '../../../../../assets/images/logos/Vertrax White + Orange@2x.png';

const ThemeConsumer = AppThemeContext.Consumer;
interface ILoginProps extends InjectedIntlProps {
  onLogin: () => void;
  error: boolean;
  form: FormState<any>;
  isLoading: boolean;
}

class Login extends Component<ILoginProps> {
  constructor(props: ILoginProps) {
    super(props);
  }

  render() {
    const { error, form, intl } = this.props;
    const options = [
      {
        label: 'SHELBYVILLE, MI',
        value: 'C1'
      }
    ];
    return (
      <LoginFormContainer>
        <LogoContainer>
          <ImgLogoContainer textAlign={'left'}>
            <img width={245} src={sdwoLogo} />
          </ImgLogoContainer>
          <ImgLogoContainer textAlign={'right'}>
            <img width={230} src={vwoLogo} />
          </ImgLogoContainer>
        </LogoContainer>
        <GridLogin>
          {this.props.isLoading ? (
            <VSpinner />
          ) : (
            <Col xs={12}>
              <Row center={'xs'}>
                <Col xs={10} md={8} lg={4}>
                  <ThemeConsumer>
                    {(themeContext: ITheme) =>
                      themeContext && (
                        <VCardPanel bodyPadding={'5px 15px 15px 15px'} noHeader>
                          <TitleHeader textColor={themeContext.accent_red}>
                            <FormattedMessage id="home.login.msg" defaultMessage="Login to your account" />
                          </TitleHeader>
                          <VInputField
                            fill
                            layer={{
                              labelWidth: 3,
                              labelOrientation: 'start',
                              inputOrientation: 'start'
                            }}
                            fieldState={form.$.username}
                            id="username"
                            label={intl.formatMessage({ id: 'home.login.username' })}
                            inline={true}
                            type="username"
                            leftIcon="user"
                          />
                          <VInputField
                            fill
                            layer={{
                              labelWidth: 3,
                              labelOrientation: 'start',
                              inputOrientation: 'start'
                            }}
                            fieldState={form.$.password}
                            id="username"
                            label={intl.formatMessage({ id: 'home.login.pass' })}
                            inline={true}
                            type="password"
                            leftIcon="lock"
                          />
                          <Button
                            alignText={'center'}
                            fill
                            active
                            large
                            intent={'primary'}
                            text={intl.formatMessage({ id: 'home.login.loginBtn' })}
                            onClick={this.login}
                          />
                        </VCardPanel>
                      )
                    }
                  </ThemeConsumer>
                </Col>
              </Row>
            </Col>
          )}
        </GridLogin>
      </LoginFormContainer>
    );
  }

  login = () => {
    this.props.onLogin();
  };
}

export default injectIntl(Login);
