import React, { Component, ReactNode } from 'react';
import { VCardPanel, VSpinner, VTable } from 'gsi-vx-ui';
import { inject, observer } from 'mobx-react';
import { ISortResult, IVTableProps } from 'gsi-vx-ui/lib/types/Table/Table';
import { Col, Grid, Row } from 'react-flexbox-grid';
import { SummaryRouteStore } from '../../stores/summary/routeStore/SummaryRouteStore';
import { SummaryShift } from '../../models/summaryShift';
import moment from 'moment';
import { Icon } from '@blueprintjs/core';
import { IVWidgetTableProps } from 'gsi-vx-ui/lib/types/Table/Widget/Widget';
import { statusOptions, truckUrgencyColor } from '../../enums/enums';
import { reaction } from 'mobx';
import { SummaryStore } from '../../stores/summary/SummaryStore';
import { AppThemeContext, ITheme } from '../../../../../../App';
import { FilterOperator } from '../../../../../../core/types/FilterType';

const ThemeConsumer = AppThemeContext.Consumer;

interface IDeliverySummaryRouteProps {
  route_store?: SummaryRouteStore;
  summary_store?: SummaryStore;
  onSelectShift: (element: any) => void;
}

const iconStyle = {
  backgroundColor: '#c23030',
  color: 'white',
  borderRadius: '50px'
};

@inject('route_store', 'summary_store')
@observer
export class DeliverySummaryRoute extends Component<IDeliverySummaryRouteProps> {
  constructor(props: IDeliverySummaryRouteProps) {
    super(props);
    const operator: FilterOperator = 'EQUALITY';
    this.props.route_store!.addFilter({
      operator,
      field: 'date',
      value: moment(this.props.summary_store!.date).format('YYYY-MM-DD')
    });
    if (this.props.route_store!.elements.length === 0) {
      this.props.route_store!.getElements();
    } else {
      this.props.onSelectShift({ element: this.props.route_store!.elements[0], tab: 'summary' });
    }
    reaction(
      () => this.props.route_store!.element,
      () => this.props.onSelectShift({ element: this.props.route_store!.element, tab: 'summary' })
    );
  }

  onSelect = (data: any) => {
    const { route_store } = this.props;
    const element = route_store!.elements.find(element => element.shiftId === data.shiftId) || data;
    route_store!.setElement(element);
  };

  onSort = (sort: ISortResult) => {
    if (sort.order === 'NONE') {
      this.props.route_store!.removeOrder({
        property: this.props.route_store!.columns[sort.columnIndex],
        direction: sort.order
      });
    } else {
      this.props.route_store!.addOrder({
        property: this.props.route_store!.columns[sort.columnIndex],
        direction: sort.order
      });
    }
    this.props.route_store!.getElements();
  };

  render() {
    const data = this.transformRoutesList();
    const loading = this.props.route_store!.isLoading;
    return (
      <Grid fluid>
        <Row>
          <Col xs>
            <ThemeConsumer>
              {(themeContext: ITheme) =>
                themeContext && (
                  <VCardPanel
                    headerTextJustify={'center'}
                    headerText={`Delivery Shift (${this.props.route_store!.count})`}
                    bodyPadding={'0'}
                    headerBackgroundColor={themeContext.headers}
                    headerColor={'#FFFFFF'}
                  >
                    {loading ? <VSpinner /> : <VTable {...data} />}
                  </VCardPanel>
                )
              }
            </ThemeConsumer>
          </Col>
        </Row>
      </Grid>
    );
  }

  transformRoutesList(): IVTableProps {
    const { route_store } = this.props;
    const customWidget: IVWidgetTableProps = {
      column: 'status',
      widget: {
        type: 'CUSTOMERCOMPONENT',
        cusmtomerCell: {
          renderCustomer: (value: any): ReactNode => {
            let icon = <Icon icon={'tick-circle'} iconSize={15} intent={'warning'} />;
            switch (value.status) {
              case statusOptions.SUCCESS:
                icon = <Icon icon={'tick-circle'} iconSize={15} intent={'success'} />;
                break;
              case statusOptions.MISSING:
                icon = <Icon icon={'warning-sign'} iconSize={15} intent={'warning'} />;
                break;
              case statusOptions.INCOMPLETE:
                icon = <Icon style={iconStyle} icon={'cross'} iconSize={15} intent={'danger'} />;
                break;
              case statusOptions.TRUCK:
                icon = <Icon icon={'drive-time'} iconSize={15} color={truckUrgencyColor[value.truckColor]} />;
                break;
            }
            return <div>{icon}</div>;
          }
        }
      }
    };

    const widgetsCell: IVWidgetTableProps[] = [customWidget];
    const data = route_store!.elements.map((item: SummaryShift) => {
      return {
        status: { status: item.status, truckColor: item.urgency },
        urgency: item.urgency,
        truckId: item.truckId,
        truckName: item.truckName,
        driverFullName: item.driverFullName,
        driverId: item.driverId,
        shiftId: item.shiftId,
        startTime: item.startTime,
        stopsPlanned: item.stopsPlanned,
        stopsAdded: item.stopsAdded,
        totalStops: item.totalStops,
        stopsCompleted: item.stopsCompleted,
        nda: item.nda,
        ndu: item.ndu,
        totalStopsCompleted: item.totalStopsCompleted,
        percentCompleted: `${item.percentCompleted}%`,
        qtyDelivered: item.qtyDelivered,
        totalMiles: item.totalMiles,
        endTime: item.endTime
      };
    });
    return {
      data,
      widgetsCell,
      striped: true,
      cellSelectionType: 'ENTIRE_ROW',
      actionsSelection: { onSelectionChange: this.onSelect },
      columns: this.props.route_store!.columns,
      columns_name: {
        status: 'S',
        truckName: 'Truck',
        driverFullName: 'Driver',
        shiftId: 'Shift ID',
        startTime: 'Start Shift',
        stopsPlanned: 'Stops Planned',
        stopsAdded: 'Stops Added',
        totalStops: 'Total Stops',
        stopsCompleted: 'Stops Compl',
        nda: 'NDA',
        ndu: 'NDU',
        totalStopsCompleted: 'Total Stops Compl',
        percentCompleted: '% Compl',
        qtyDelivered: 'Qty Del',
        totalMiles: 'Total Miles',
        endTime: 'End Shift'
      },
      reordering: true,
      onOrderColumns: this.onOrdering,
      typeHeightRow: 'HALF',
      sortable: {
        setupsOrden: this.props.route_store!.sortOrderForTable,
        columns: [
          'status',
          'truckName',
          'driverFullName',
          'shiftId',
          'startTime',
          'stopsPlanned',
          'stopsAdded',
          'totalStops',
          'stopsCompleted',
          'nda',
          'ndu',
          'totalStopsCompleted',
          'percentCompleted',
          'qtyDelivered',
          'totalMiles',
          'endTime'
        ],
        onSort: this.onSort
      }
    };
  }

  onOrdering = (columns: string[]) => {
    this.props.route_store!.setColumns(columns);
  };
}
