import { action, observable } from 'mobx';
import React from 'react';
import { ParentStore } from '../../../../../core/stores/ParentStore';
import OrderManagementServices from '../services/OrderManagementServices';
import { showToastNotification } from 'gsi-vx-ui';
import FileSaver from 'file-saver';
import { cloneDeep, find, forEach, isEqual } from 'lodash';
import moment from 'moment';

export class OrderManagementStore extends ParentStore {
  @observable
  public listProducts: any[];
  @observable public listTypes: any[];
  @observable public listFees: any[];
  @observable public listSelectFees: any[];
  @observable public listDetailsFees: any[];
  @observable public listFillTypes: any[];
  @observable public listUrgency: any[];
  @observable public listZone: any[];
  @observable public advancedProfiles: any[];
  @observable public listProfiles: any[];
  @observable public listStates: any[];
  @observable public profileActive: number | string;
  @observable public startDate: any;
  @observable public orderDetails: any;
  @observable public endDate: any;
  @observable public searchOrders: string;
  @observable public filterDays: number | string;
  constructor() {
    super(OrderManagementServices);
    this.listProducts = [];
    this.listTypes = [];
    this.listSelectFees = [];
    this.listFees = [];
    this.listDetailsFees = [];
    this.listFillTypes = [];
    this.listUrgency = [];
    this.advancedProfiles = [];
    this.listProfiles = [];
    this.listZone = [];
    this.listStates = [];
    this.orderDetails = {};
    this.startDate = null;
    this.endDate = null;
    this.filterDays = '';
    this.searchOrders = '';
    this.profileActive = -1;
  }

  @action
  public getZones() {
    const zones: any[] = [];
    this.serviceContainer.getZones().then((response: any) => {
      response.data.content.map((z: any) => {
        zones.push({ value: z.zoneId, label: z.description });
      });
      this.setZones(zones);
    });
  }
  @action
  public getProducts() {
    const products: any[] = [];
    this.serviceContainer.getProducts().then((response: any) => {
      response.data.content.map((p: any) => {
        products.push({ value: p.productId, label: p.productName });
      });
      this.setProducts(products);
    });
  }
  @action
  public loadLists() {
    this.getAdvanceProfile();
    this.getZones();
    this.getProducts();
    this.getStates();
    this.getTypes();
    this.getUrgency();
    this.getFillTypes();
  }
  @action
  public getStates() {
    const states: any[] = [];
    this.serviceContainer.getStates().then((response: any) => {
      response.data.content.map((s: any) => {
        states.push({ value: s.state, label: s.stateCode });
      });
      this.setStates(states);
    });
  }
  @action
  public getTypes() {
    const types: any[] = [];
    this.serviceContainer.getTypes().then((response: any) => {
      response.data.content.map((o: any) => {
        types.push({ value: o.orderTypeid, label: o.ordTypeName });
      });
      this.setTypes(types);
    });
  }
  @action
  public getFillTypes() {
    this.serviceContainer.getFillTypes().then((response: any) => {
      const types: any[] = response.data.content.map((o: any) => {
        return { value: o.fillTypeId.toString(), label: o.type };
      });
      this.setFillTypes(types);
    });
  }
  @action
  public getUrgency() {
    this.serviceContainer.getUrgency().then((response: any) => {
      const urgencies: any[] = response.data.map((u: any) => {
        return { value: u.key, label: u.value };
      });
      this.setUrgency(urgencies);
    });
  }
  @action
  public cancelOrder(id: number, action?: any) {
    this.serviceContainer.cancelOrder(id).then(() => {
      showToastNotification({ type: 'success', message: 'Order canceled!' });
      action!();
    });
  }
  @action
  public saveProfile(data: any, action?: any) {
    this.setLoading(true);
    this.serviceContainer
      .saveProfile(data)
      .then(() => {
        this.setLoading(false);
        showToastNotification({ type: 'success', message: 'Profile saved!' });
        this.getAdvanceProfile();
        action!();
      })
      .catch(() => {
        this.setLoading(false);
      });
  }
  @action
  public updateProfile(data: any, action?: any) {
    this.setLoading(true);
    this.serviceContainer
      .updateProfile(data)
      .then(() => {
        this.setLoading(false);
        showToastNotification({ type: 'success', message: 'Profile saved!' });
        this.getAdvanceProfile();
        action!();
      })
      .catch(() => {
        this.setLoading(false);
      });
  }

  @action
  public setEndDate(value: any) {
    this.endDate = value;
  }
  @action
  public setStartDate(value: any) {
    this.startDate = value;
  }
  @action
  public setFilterDays(value: number | string) {
    this.filterDays = value;
  }
  @action
  public setOrderDetails(order: any) {
    this.orderDetails = order;
    if (order && order.orderId) {
      this.setLoading(true);
      this.serviceContainer
        .getFeesByOrder({ orderId: order.orderId })
        .then((response: any) => {
          this.setDetailsFees(response.data.content);
          this.setLoading(false);
        })
        .catch(() => {
          this.setLoading(false);
        });
    } else {
      this.setDetailsFees([]);
    }
  }
  @action
  public exportExcel(filters: any, profile: number | string, feature: string, name: string = 'Orders') {
    this.setLoading(true);
    this.serviceContainer
      .exportExcel('exportOrdersToExcel', filters, profile, feature, 10)
      .then((response: any) => {
        const uint8View = new Uint8Array(response.data);
        const blob = new Blob([uint8View], {
          type: 'application/vnd.ms-excel;charset=utf-8'
        });
        FileSaver.saveAs(blob, `${name}.xlsx`);
        this.setLoading(false);
      })
      .catch(() => {
        this.setLoading(false);
      });
  }

  @action
  public getAdvanceProfile() {
    this.setLoading(true);
    this.serviceContainer
      .getAdvanceProfile()
      .then((response: any) => {
        this.setAdvancedProfiles(response.data.content);
        const profiles: any[] = response.data.content.map((p: any) => {
          return { value: p.profileId, label: p.name };
        });
        this.setProfiles(profiles);
        this.setLoading(false);
      })
      .catch(() => {
        this.setLoading(false);
      });
  }

  @action
  public saveOrder(data: any, action?: any) {
    this.setLoading(true);
    !!this.listDetailsFees.length && (data.orderAdditionalFees = this.listDetailsFees);
    const operation = data.id ? 'updateOrder' : 'saveOrder';
    data.ordDate = moment(data.ordDate).format('YYYY-MM-DDTHH:mm:ss');
    data.fillTypeId && (data.fillTypeId = Number(data.fillTypeId));
    this.serviceContainer[operation](data)
      .then(() => {
        this.setLoading(false);
        showToastNotification({ type: 'success', message: 'Order saved!' });
        action!();
      })
      .catch(() => {
        this.setLoading(false);
      });
  }
  @action
  public getFees(data: any = -1) {
    const fees: any[] = [];
    const select_fees: any[] = [];
    this.serviceContainer
      .getFees({ productId: data })
      .then((response: any) => {
        forEach(response.data.content, (f: any) => {
          fees.push(f);
          select_fees.push({ value: f.additionalFeeId, label: f.description });
        });
        this.setListFees(fees);
        this.setSelectListFees(select_fees);
      })
      .catch(() => {
        this.setLoading(false);
      });
  }
  @action
  public addFee(value: any) {
    const fee = find(this.listFees, v => v.additionalFeeId === value);
    fee && this.setDetailsFees(this.listDetailsFees.concat(cloneDeep(fee)));
  }
  @action
  public delFee(value: any) {
    if (value) {
      const new_list = this.listDetailsFees.filter(f => !isEqual(f, value));
      this.setDetailsFees(new_list);
    }
  }
  @action
  public editFee(values: any[] = []) {
    const newFees: any[] = this.listDetailsFees.map(e => {
      const element = values.find(v => v.additionalFeeId === e.additionalFeeId);
      return element || e;
    });
    this.setDetailsFees(newFees);
  }
  @action
  public setActiveProfile(value: number | string) {
    this.profileActive = value;
  }
  @action
  public setListFees(value: any[]) {
    this.listFees = value;
  }
  @action
  public setSelectListFees(value: any[]) {
    this.listSelectFees = value;
  }
  public setZones(elements: any[]) {
    this.listZone = elements;
  }
  public setAdvancedProfiles(elements: any[]) {
    this.advancedProfiles = elements;
  }
  public setProfiles(elements: any[]) {
    this.listProfiles = elements;
  }
  public setTypes(elements: any[]) {
    this.listTypes = elements;
  }
  public setFillTypes(elements: any[]) {
    this.listFillTypes = elements;
  }
  public setUrgency(elements: any[]) {
    this.listUrgency = elements;
  }
  public setProducts(elements: any[]) {
    this.listProducts = elements;
  }
  public setStates(elements: any[]) {
    this.listStates = elements;
  }
  @action
  public setDetailsFees(elements: any[]) {
    this.listDetailsFees = elements;
  }
  @action
  public setSearchOrder(data: string) {
    this.searchOrders = data;
  }
}

const orderManagementStore = new OrderManagementStore();
export default orderManagementStore;
